import request from '@/utils/request'

export default {
  data () {
    return {
      task: null,
      loading: false,
      show: false
    }
  },
  methods: {
    changeTaskStatus (action) {
      if (this.loading) return
      this.loading = true
      return request.post(`/crss-robot/task/robotapi/v1/taskLog/${action}`, {
        taskUuid: this.task.taskInstanceId
      }).finally(() => {
        // 一！定！要！在finally把loading置为false
        // http://jira.cloudminds.com/browse/GINLT-3374?filter=19616
        this.loading = false
      })
    }
  }
}
