<template>
  <div class="page delivery-pending">
    <Map v-show="showMap" @on-select="handleSelect" :on-back="handleMapBackBtnClick" :type="pointType"></Map>
    <Interact v-show="!showMap">
      <!-- 1柜关闭 -->
      <template v-if="doorClosed">
        <template slot="tips">
          {{ $t('ready') }}
        </template>
        <template slot="title">
          {{ getTarget }}
        </template>
        <template slot="footer">
          <IconLoading v-if="loading"></IconLoading>
          <template v-else>
            <iButton key="continue" type="outline" @click="handleContinue" v-show="continuePutIn">
              {{ $t('continuePutIn') }}
            </iButton>
            <iButton :loading="loading"
                      class="start-btn" key="start"
                      @click="handleStart">
              {{ $t('start') }}
            </iButton>
            <iButton type="ghost" key="cancel" @click="handleCancel">
              {{ $t('cancel') }}
            </iButton>
          </template>
        </template>
      </template>
      <!-- 初始状态 -->
      <template v-else>
        <template slot="tips">
          {{ $t('putTips') }}
        </template>
        <template slot="title">
          {{ $t('doorOpened') }}
        </template>
        <iButton type="outline" slot="footer" :loading="loading" @click="handleCloseDoor">
          {{ $t('closeDoor') }}({{ countdown }}S)
        </iButton>
      </template>
    </Interact>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Map from '@/components/Map'
import Interact from '@/components/Interact'
import iButton from '@/components/Button'
import IconLoading from '@/components/IconLoading'
import countdown from '@/mixins/countdown'
import doorsControl from '@/mixins/doorsControl'
import startTask from '@/mixins/startTask'
import pong from '@/ue4/pong'
import pongTypes from '@/ue4/pong/types'
import POINT_TYPE from '@/const'
import backHandle from '@/mixins/backHandle'

export default {
  name: 'DeliveryPending',
  mixins: [countdown, doorsControl, startTask, backHandle],
  components: {
    Map,
    Interact,
    iButton,
    IconLoading
  },
  data () {
    return {
      taskType: 'delivery',
      showMap: true,
      // 柜门关上
      doorClosed: false,
      // 现在是开/关门，1表示开，0表示关
      doorSwitchAction: 0,
      COUNTDOWN: 20,
      pointType: POINT_TYPE.INTEREST
    }
  },
  computed: {
    ...mapGetters('app', ['hasCab']),
    // 能否继续放入
    continuePutIn () {
      return this.tasks.length < 2
    }
  },
  methods: {
    handleMapBackBtnClick () {
      if (this.tasks.length) {
        this.showMap = false
      } else {
        this.back()
      }
    },
    // 用户点击UI上的关门，或者超时自动关门
    handleCloseDoor () {
      this.countdownTimer && clearTimeout(this.countdownTimer)
      this.doorSwitchAction = 0
      this.changeDoorsStatus(this.tasks.length, this.doorSwitchAction)
      this.loading = true
    },
    handleSelect (location) {
      console.log('select location: ', location)
      this.tasks.push({
        type: 2,
        point: {
          x: location.x,
          y: location.y,
          yaw: location.yaw
        },
        poi: location.name,
        mapId: location.mapId,
        floor: location.floor,
        drawer: this.tasks.length + 1
      })
      // 有箱体的，操作开门
      if (this.hasCab) {
        this.doorSwitchAction = 1
        // 开门
        this.changeDoorsStatus(this.tasks.length, this.doorSwitchAction)
        this.$toast.show(this.$t('doorOpening'), { icon: 'loading' })
        return
      }
      // 无箱体
      this.showMap = false
      this.doorClosed = true
    },
    // 继续放入
    handleContinue () {
      this.showMap = true
    },
    handleDoorSwitchResp ({ code }) {
      this.$toast.hide()
      this.loading = false
      // 开关失败
      if (code !== 0) {
        this.$message(this.$t('doorSwitchTimeout'))
      }
      // 开门成功
      if (this.doorSwitchAction) {
        this.showMap = false
        this.doorClosed = false
        // 超时自动关门
        return this.startCountdown(this.handleCloseDoor)
      }
      // 关门成功
      this.doorClosed = true
    }
  },
  mounted () {
    pong.$on(pongTypes.DOOR_SWITCH_RESP, this.handleDoorSwitchResp)
  },
  beforeDestroy () {
    pong.$off(pongTypes.DOOR_SWITCH_RESP, this.handleDoorSwitchResp)
  }
}
</script>

<style lang="scss" scoped>
.delivery-pending {
  .start-btn {
    margin: 0 6.7vw;
  }
}
</style>

<i18n>
{
  "zh-CN": {
    "putTips": "请放入物品，记得关门哦~",
    "continuePutIn": "继续放入"
  },
  "en": {
    "putTips": "Please put in items and close the door",
    "continuePutIn": "Continue to put items"
  },
  "th": {
    "putTips": "กรุณาใส่สิ่งของและปิดประตู",
    "continuePutIn": "ใส่ของชิ้นต่อไป"
  }
}
</i18n>
