import mock from '../pong/mock'
import types from './types'

// 在电脑端运行时把它关掉，否则不能mock
let runOnRobot = false
if (process.env.NODE_ENV === 'production') {
  runOnRobot = true
}
// ue4唯一入口
const ENTRY = 'callue4fun'

// action和params分割标识符
const SPLIT = '#$*&@'

const delayQueue = []
let ueQueryInterval = null

function extracted () {
  ueQueryInterval && clearTimeout(ueQueryInterval)
  if (window.ue) {
    if (delayQueue.length) {
      console.log('延迟执行队列：', delayQueue)
      delayQueue.forEach((item) => {
        ping(item.action, item.params, item.ignoreOpId)
      })
    }
    return
  }
  ueQueryInterval = setTimeout(extracted, 500)
}

export function h5InVokeUE4 (action, body) {
  window.ue.cloudminds.hue(ENTRY, `${action}${SPLIT}${JSON.stringify(body)}`)
}

export function ping (action, params, ignoreOpId = false) {
  const body = params || {}
  if (!ignoreOpId && body.opId === undefined) {
    body.opId = `ue4-h5-${Date.now()}`
  }
  if (window.ue) {
    if (action !== types.ONE_LINE_LOG) {
      const head = `[Ping] ${action}: `
      console.log(head, body)
      logger(head, body)
    }
    h5InVokeUE4(action, body)
    return body.opId
  }

  if (runOnRobot) {
    // 在开始调用时window.ue可能还没注入，需要延后执行
    // 这会导致在电脑端调试时无法mock数据，后续想下其他方案
    delayQueue.push({ action, params: body, ignoreOpId })
    ueQueryInterval && clearTimeout(ueQueryInterval)
    ueQueryInterval = setTimeout(extracted, 500)
    return
  }
  const mocker = mock[action]
  if (typeof mocker === 'function') {
    mock[action](body)
    return body.opId
  }
  console.error('[Mock Error] no such mock name: ', action)
}

export function logger () {
  if (process.env.NODE_ENV === 'production') {
    const log = Array.prototype.join.call(Array.prototype.map.call(arguments, e => {
      if (typeof e === 'object') {
        return JSON.stringify(e)
      }
      return e
    }), ' ')
    ping(types.ONE_LINE_LOG, `[${new Date().toLocaleString()}]  ${log}`, true)
  }
}

export default ping
