<template>
  <div id="standby"
       :style="{ backgroundImage: `url(${sceneBackground})` }">
    <div class="fs4">
      {{ $t('standbyTips') }}
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import faceSmile from '@/assets/images/face-smile.png'

export default {
  name: 'Standby',
  computed: {
    ...mapState('app', ['appConfig']),
    sceneBackground () {
      return this.appConfig && this.appConfig.sceneBackgrounds ? (this.appConfig.sceneBackgrounds.key1 || faceSmile) : faceSmile
    }
  }
}
</script>

<style lang="scss" scoped>
#standby {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: 100% 100%;

  .fs4 {
    text-align: center;
    padding: 3vh 0;
  }
}
</style>

<i18n>
{
  "en": {
    "standbyTips": "Click me for more information"
  },
  "zh-CN": {
    "standbyTips": "点我还有更多精彩内容，等你来探索哦~"
  },
  "th": {
    "standbyTips": "คลิกที่นี้เพื่อดูข้อมูลเพิ่มเติม"
  }
}
</i18n>
