import { logger } from '@/ue4/ping'
import TASK_STATUS from '@/const'
import request from '@/utils/request'

export default {
  namespaced: true,
  state: {
    queryTaskLoading: false,
    currentTask: null
  },
  mutations: {
    setCurrentTask (state, data) {
      if (data.errorCode) {
        const errs = data.errorCode.toString().split('&')
        data.errorCode = errs.length ? errs.pop() : ''
      }
      state.currentTask = data
    },
    clearCurrentTask (state) {
      state.currentTask = null
    },
    setQueryTaskLoading (state, loading) {
      state.queryTaskLoading = loading
    }
  },
  actions: {
    queryTask ({ dispatch, commit, state: { queryTaskLoading }, rootState: { app: { robotInfo } } }) {
      if (queryTaskLoading) return
      commit('setQueryTaskLoading', true)
      request
        .post(
          '/crss/task/v1/taskLog/getCurrentTaskLog',
          { robotCode: robotInfo.robotId },
          { proxy: '/231', timeout: 10000 })
        .then((data) => {
          if (!data || data.code !== 0) {
            Promise.reject(data)
          } else {
            const task = data.data
            if (task && (task.status == TASK_STATUS.RUNNING || task.status == TASK_STATUS.PAUSED)) {
              task.taskInstanceId = task.taskUuid
              console.log('查询到任务：', task)
              // 找到当前任务节点
              if (task.workData) {
                const data = JSON.parse(task.workData)
                let lastRunningNode, lastPendingNode
                data.taskNodes && data.taskNodes.forEach(node => {
                  if (node.status == 1) {
                    lastRunningNode = node
                  } else if (node.status == -1) {
                    lastPendingNode = node
                  }
                })
                if (lastPendingNode) {
                  task.detail = lastPendingNode.pointName
                }
                if (lastRunningNode) {
                  task.detail = lastRunningNode.pointName
                }
              }
              // 表示从cross主动查询到的task
              task.cross = true
              commit('setCurrentTask', task)
              logger('查询到任务: ', task)
            } else {
              commit('clearCurrentTask')
              /* eslint-disable */
              // commit('setCurrentTask', {
              //   "taskType": 12,
              //   "taskMode": 0,
              //   "robotName": "GLNX0004",
              //   "robotType": "ginger-lite",
              //   "errorCode": "",
              //   "errorMessage": "",
              //   "taskName": "Gaochao_Office",
              //   "startTime": "2021-04-23T08:35:41",
              //   "taskInstanceId": "ue4-h5-1619166941077",
              //   "robotCode": "GINLITXR-1LXXXXXXXXXGNL03S2111000004",
              //   "taskId": "ue4-h5-1619166941077",
              //   "taskUuid": "ue4-h5-1619166941077",
              //   "status": 1
              // })
              /* eslint-enable */
              logger('未查询到正在运行的任务: ', task)
            }
          }
        })
        .catch((err) => {
          logger('查询任务错误：', err)
          setTimeout(() => {
            dispatch('queryTask')
          }, 3000)
        })
        .finally(() => {
          commit('setQueryTaskLoading', false)
        })
    }
  }
}
