import VConsole from 'vconsole'
import Vue from 'vue'
import message from '@/components/Message'
import notice from '@/components/Notice'
import toast from '@/components/Toast'
// 位置在下的一些提醒
import notice2 from '@/components/Notice2'
import block from '@/components/Block'
import App from './App.vue'
import router from './router'
import i18n from './i18n'
import store from './store'
import pong from './ue4/pong'
import pongTypes from './ue4/pong/types'
import { logger } from './ue4/ping'
import SDK from 'cross_sdk'
// import { h5InVokeUE4 } from './ue4/ping'
// import pingTypes from './ue4/ping/types'

Vue.config.productionTip = false
Vue.prototype.$message = message
Vue.prototype.$notice = notice
Vue.prototype.$toast = toast
Vue.prototype.$block = block
// 位置在下的一些提醒
Vue.prototype.$notice2 = notice2

const open = XMLHttpRequest.prototype.open
XMLHttpRequest.prototype.open = function () {
  if (store.state.app.robotInfo.ENV_URL && !arguments[1].match(/^https?:\/\//)) {
    arguments[1] = store.state.app.robotInfo.ENV_URL + arguments[1]
  }
  return open.apply(this, arguments)
}

const types = {}
Object.keys(pongTypes).forEach((k) => {
  types[pongTypes[k]] = k
})

Vue.use(new SDK())

// 全局暴露方法名供ue4调用
window.UE4Called = function (action, data) {
  if (['reportPose', 'reportPoseResp'].includes(action)) {
    return
  }
  let payload = data
  if (typeof data === 'string') {
    try {
      payload = JSON.parse(data)
    } catch (e) {
      console.error('parse data error: ', e)
      console.log('UE4Called error on action: ', action, '=>', data)
    }
  }
  if (action !== 'naviGoal') {
    console.log(`[Pong] ${action}: `, payload)
    if (!types[action]) {
      console.warn(`收到非约定的消息类型[${action}]`)
    }
  }
  logger('[pong] ', action, ' ', payload)
  pong.$emit(action, payload)
}

// backdoor
window.back = function () {
  router.replace({ name: 'Home' })
}
window.refresh = function () {
  location.reload()
}

