<template>
  <img class="icon-loading" :style="{ 'animation-timing-function': timingFunction }" src="@/assets/images/icon-loading.png">
</template>

<script>
export default {
  props: {
    timingFunction: {
      type: String,
      default: 'ease-in-out'
    }
  },
  name: 'IconLoading'
}
</script>

<style lang="scss" scoped>
@keyframes animation-spin {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

.icon-loading {
  width: #{64 / $r}rem;
  animation: animation-spin .8s infinite;
}
</style>
