<template>
  <div class="page page-delivery">
    <template v-if="status === PENDING">
      <GridPending v-if="isPatrol"></GridPending>
      <Pending v-else></Pending>
    </template>
    <Running v-else-if="status === RUNNING" :task="task" ref="running"></Running>
    <Success v-else-if="status === SUCCESS" :task="task"></Success>
    <RunningFailed></RunningFailed>
    <RunningException :type="type"></RunningException>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from 'vuex'
import Pending from './Pending'
import GridPending from './GridPending'
import Running from './Running'
import Success from './Success'
import RunningFailed from '@/components/RunningFailed'
import RunningException from '@/components/RunningException'
import pong from '@/ue4/pong'
import pongTypes from '@/ue4/pong/types'
import TASK_STATUS from '@/const'

const { PENDING, RUNNING, SUCCESS } = TASK_STATUS

export default {
  name: 'Delivery',
  components: {
    Pending,
    GridPending,
    Running,
    Success,
    RunningFailed,
    RunningException
  },
  data () {
    return {
      // 当前状态
      status: PENDING,
      PENDING,
      RUNNING,
      SUCCESS,
      task: null,
      type: 0
    }
  },
  computed: {
    ...mapState('task', ['currentTask']),
    ...mapGetters('app', ['isPatrol'])
  },
  methods: {
    ...mapMutations('task', ['clearCurrentTask']),
    handleTaskNotify (data) {
      this.task = data
      if (data.status === 'running') {
        // 返航也会上报task running事件
        // 正在执行的任务
        this.status = RUNNING
      }
    },
    handleTaskSummary (data) {
      this.task = data
      if (data.status === '4' || data.status === TASK_STATUS.SUCCESS) {
        this.status = SUCCESS
        // 清除一下缓存任务防止意外
        this.clearCurrentTask()
      }
      if (data.status === '4' || data.status === '5' || data.status === '3' || data.status === TASK_STATUS.SUCCESS || data.status === TASK_STATUS.CANCELED || data.status === TASK_STATUS.FAILED) {
        localStorage.setItem('taskSummary', JSON.stringify(data))
      }
    }
  },
  mounted () {
    // 任务状态变更
    pong.$on(pongTypes.TASK_NOTIFY, this.handleTaskNotify)
    // 任务成功/失败监控
    pong.$on(pongTypes.TASK_SUMMARY, this.handleTaskSummary)
    // 通过queryTask查询到的正在运行/暂停的任务，比如重启/断网
    if (this.currentTask && this.currentTask.taskType === this.type) {
      this.task = this.currentTask
      this.status = RUNNING
    }
    const arrivedDelevery = localStorage.getItem('arrivedDelevery')
    if (arrivedDelevery) {
      this.status = RUNNING
      localStorage.removeItem('arrivedDelevery')
      this.$nextTick(() => {
        this.$refs.running.handleArrived(JSON.parse(arrivedDelevery))
      })
    }
  },
  beforeDestroy () {
    pong.$off(pongTypes.TASK_NOTIFY, this.handleTaskNotify)
    pong.$off(pongTypes.TASK_SUMMARY, this.handleTaskSummary)
  }
}
</script>
