<template>
  <div class="home-desktop">
    <div class="menu-item"
        v-for="(item, index) in menu"
        :key="index"
        :style="{ 'background-image': `url(${item.icon})` }"
        @click="handleClick(index)">
      {{ $t(item.code) }}
      <div class="red-dot" v-if="item.route == 'Delivery' && failedDeliveryNodes.length">{{ failedDeliveryNodes.length }}</div>
    </div>
    <Modal :show="showFailedDeliveryTips">
      <template v-if="lastTaskSummary">
        <div class="exception-tips flex-default-center">
          {{ $t('tips') }}: {{ $t(`deliveryExceptionTips${hasCab ? '' : 'NoCab'}`) }}
        </div>
        <div class="exception-row flex-between-center"
            v-for="(item, index) in failedDeliveryNodes"
            :key="index">
          <div class="point-name">{{ item.from }} <img src="@/assets/images/icon-arrow-right.png"> {{ item.to }}</div>
          <div class="error-detail">{{ $t(`deliveryStatus.${item.errorCode}`) }}</div>
        </div>
      </template>
      <iButton slot="footer"
              @click="handleConfirmTips">
        {{ $t('gotIt') }}
      </iButton>
    </Modal>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
import Modal from '@/components/Modal'
import iButton from '@/components/Button'
import TASK_STATUS from '@/const'
import request from '@/utils/request'
import pong from '@/ue4/pong'
import pongTypes from '@/ue4/pong/types'

export default {
  name: 'HomeDesktop',
  components: {
    Modal,
    iButton
  },
  data () {
    return {
      lastTaskSummary: null,
      showFailedDeliveryTips: false,
      startCharge: false,
      chargeTimer: null
    }
  },
  computed: {
    ...mapState('task', ['queryTaskLoading']),
    ...mapState('app', ['appConfig']),
    ...mapGetters('app', ['hasCab']),
    ...mapState('map', ['queryMapLoading', 'currentFloor', 'currentLocation', 'currentMapId']),
    failedDeliveryNodes () {
      const rows = []
      if (!this.lastTaskSummary) return rows
      this.lastTaskSummary.taskNodes.slice(0, -1)
        .reduce((prev, current) => {
          const hasError = current.errorCode != 0 && current.errorCode != 20000
          if (hasError || current.status == TASK_STATUS.UNEXECUTED || current.status == TASK_STATUS.CANCELED) {
            rows.push({
              from: prev ? prev.targetPointName : this.$t('currentPoint'),
              to: current.targetPointName,
              errorCode: hasError ? current.errorCode : current.status
            })
          }
          return current
        }, null)
      return rows
    },
    menu () {
      if (this.appConfig) {
        return this.appConfig.apps.map(e => {
          let route = e.code
          if (e.code === 'Express') {
            route = 'ExpressEntry'
          } else if (e.code === 'DeliveryMeal') {
            route = 'Delivery'
          }
          return {
            route,
            ...e
          }
        })
      }
      return []
    }
  },
  methods: {
    ...mapActions('map', ['queryMapInfo']),
    handleClick (index) {
      const menu = this.menu[index]
      if (menu.route == 'Delivery' && this.failedDeliveryNodes.length) {
        this.showFailedDeliveryTips = true
        return
      }
      // 任务同步
      if (menu.route !== 'Other' && this.queryTaskLoading) {
        return this.$message(this.$t('queryingTask'))
      }
      if (menu.code === 'Charge') {
        this.charge()
        return
      }
      if (menu.route === 'Welcome') {
        return this.$message(this.$t('comingSoon'))
      }
      if (!this.$router.options.routes.find(e => e.name === menu.route || e.name === menu.code)) {
        return this.$message(this.$t('notImplemented'))
      }
      // 现在redirect时vue-router会报错提醒
      this.$router.push({
        name: menu.route
      }).catch(() => {})
    },
    handleConfirmTips () {
      this.showFailedDeliveryTips = false
      localStorage.removeItem('taskSummary')
      this.getLastTask()
    },
    getLastTask () {
      this.lastTaskSummary = JSON.parse(localStorage.getItem('taskSummary'))
    },
    charge () {
      this.startCharge = true
      // 十秒没应答就取消
      this.chargeTimer = setTimeout(() => {
        this.chargeTimer && clearTimeout(this.chargeTimer)
        this.startCharge = false
        this.$message(this.$t('missingCurrentLocation'))
      }, 10000)
      this.$toast.show(this.$t('sendCmd'), { icon: 'loading' })
      this.queryMapInfo()
    },
    handleMapInfoResp () {
      if (this.startCharge) {
        this.startCharge = false
        if (this.currentLocation) {
          request
            .post('/crss-robot/task/robotapi/v1/receptionTask/startTask', {
              param: JSON.stringify({
                param: {
                  taskType: 'charge',
                  opId: `startTask-${Date.now()}`,
                  tasks: [{
                    type: 1,
                    point: {
                      x: this.currentLocation.x,
                      y: this.currentLocation.y,
                      yaw: 0
                    },
                    poi: this.$t('currentPoint'),
                    mapId: this.currentMapId,
                    floor: this.currentFloor
                  }]
                }
              })
            })
            .then((res) => {
              console.log('发起成功: ', res)
            })
            .catch((error) => {
              console.log('发起失败: ', error)
              if (error.status == 200) {
                const err = error.data
                let code = err.code
                let message = err.message
                let data
                if (err.code == 122304) {
                  try {
                    data = JSON.parse(err.data)
                  } catch (runtimeErr) {
                    console.error('parse err.data error: ', runtimeErr)
                    data = err.data
                  }
                  const hasErrCode = data.errCode !== undefined && data.errCode !== ''
                  code = hasErrCode ? data.errCode : err.code
                  message = hasErrCode ? this.$t(`errors.${code}`) : data.errDetail
                }
                this.$notice(`${this.$t('startTaskFailed')}<br>code: ${code || ''}<br> message: ${message}`, {
                  actions: [{
                    onClick: 'close',
                    emitSeconds: 10,
                    label: this.$t('gotIt')
                  }]
                })
              } else {
                this.$message('network error')
              }
            })
            .finally(this.$toast.hide)
          return
        }
        return this.$message(this.$t('missingCurrentLocation'))
      }
    }
  },
  created () {
    pong.$on(pongTypes.MAP_INFO_RESP, this.handleMapInfoResp)
    this.getLastTask()
  },
  beforeDestroy () {
    this.$toast.hide()
    this.chargeTimer && clearTimeout(this.chargeTimer)
  }
}
</script>

<i18n>
{
  "zh-CN": {
    "queryingTask": "正在同步任务，请稍侯",
    "comingSoon": "敬请期待",
    "notImplemented": "功能未实现"
  },
  "en": {
    "queryingTask": "Syncing tasks, please wait",
    "comingSoon": "Comming soon",
    "notImplemented": "Not implemented"
  },
  "th": {
    "queryingTask": "กำลังเชื่อมต่อ, โปรดรอ",
    "comingSoon": "เร็ว ๆ นี้",
    "notImplemented": "ไม่ได้ใช้งาน"
  }
}
</i18n>

<style lang="scss" scoped>
.home-desktop {
  padding: 14.4rem 0;
  flex-wrap: wrap;
  flex: 1;
  display: flex;
  align-content: flex-start;
  .menu-item {
    box-sizing: border-box;
    height: 20vh;
    width: 25vw;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    margin-right: 5vw;
    margin-bottom: 2.5vh;
    padding: 2.1vh 0;
    text-align: center;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    position: relative;
    font-size: 4rem;
    &:nth-child(3n) {
      margin-right: 0;
    }
    .red-dot {
      $vector: #{76 / $r}rem;
      display: block;
      position: absolute;
      height: $vector;
      width: $vector;
      border-radius: 50%;
      background-color: #FF0000;
      text-align: center;
      line-height: $vector;
      color: #fff;
      right: 0;
      top: 0;
      transform: translate3d(30%, -30%, 0);
    }
  }
  .exception-row {
    margin-top: 3.6rem;
    .error-detail {
      color: #FF0000;
    }
    img {
      width: 5.1vw;
      height: 2.1vh;
      filter: invert(100%)
    }
  }
  .exception-tips {
    font-size: 3rem;
    color: #ccc;
    .alert-icon {
      flex: 0 0 3.5rem;
      height: 3.5rem;
      width: 3.5rem;
      background-color: #ffb600;
      border-radius: 50%;
      color: #fff;
      text-align: center;
      line-height: 3.5rem;
      vertical-align: middle;
      margin-right: 2rem;
    }
  }
}
</style>
