<template>
  <div class="task-item" :class="{ open }">
    <div class="header">
      <div class="title fs4 flex-between-center">
        <div class="flex-default-center">
          <img src="@/assets/images/icon-loop.png" alt=""> {{ data.name }}
        </div>
        <img class="toggle-arrow" src="@/assets/images/icon-arrow-left.png" @click.stop.prevent="() => $emit('toggle')" alt="">
      </div>
      <div class="time" v-if="data.startTime">
        {{ data.startTime }} - {{ data.endTime }}, {{ $t('weekly') }} {{ data.days.split(',').map(e => $t(e)).join(' ') }}
      </div>
    </div>
    <div v-if="data.points && open" class="disinfect-route">
      <div class="location-item flex-between-center" v-for="(point, pi) in data.points" :key="pi">
        <div class="flex-default-center">
          <span class="point"></span>
          <span class="name">{{ point.name }}</span>
        </div>
        <span class="duration">{{ data.duration }}{{ $t('minute') }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    data: {
      required: true
    },
    open: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<i18n>
{
  "zh-CN": {
    "weekly": "每周",
    "1": "周一",
    "2": "周二",
    "3": "周三",
    "4": "周四",
    "5": "周五",
    "6": "周六",
    "7": "周日"
  }
}
</i18n>

<style lang="scss" scoped>
.task-item {
  transition: $base-transition;
  .header {
    padding: 3vh 4vw;
    .title {
      font-weight: 500;
      img {
        width: 5.25vw;
        height: 5.25vw;
        margin-right: 2.5vw;
      }
      .toggle-arrow {
        filter: invert(50%);
        width: 1.56vw;
        height: auto;
        transform: rotate(-90deg);
        transition: $base-transition;
      }
    }
    .time {
      font-size: 3rem;
      margin-top: 1vh;
      color: #999;
    }
  }
  .disinfect-route {
    font-size: 3rem;
    margin: 0 4vw;
    position: relative;
    .location-item {
      margin-bottom: 3vh;
      .point {
        display: inline-block;
        height: 1.25vw;
        width: 1.25vw;
        background-color: $primary-color;
        border-radius: 50%;
        margin-right: 2.75vw;
      }
    }
    &::before {
      content: '';
      position: absolute;
      width: 1px;
      top: 1vh;
      bottom: 1vh;
      left: 0.625vw;
      transform: scaleX(.5);
      background-color: $primary-color;
    }
  }
  &.open {
    .toggle-arrow {
      transform: rotate(90deg) !important;
    }
  }
}
</style>
