<template>
  <Bar class="search-bar" v-bind="$attrs">
    <div class="flex-default-center">
      <div class="search-input flex-between-center">
        <img class="icon-search" src="@/assets/images/icon-search.png">
        <input :placeholder="placeholder" type="text" v-model="query" @input="handleChange">
        <img class="icon-close" v-show="showClearBtn" src="@/assets/images/icon-close.png" @click="handleClear">
      </div>
      <a class="search-btn">
        {{ $t('search') }}
      </a>
    </div>
  </Bar>
</template>

<script>
import Bar from './Bar.vue'

export default {
  name: 'SearchBar',
  props: {
    placeholder: {
      type: String,
      default () {
        return this.$t('searchLocation')
      }
    }
  },
  components: {
    Bar
  },
  data () {
    return {
      query: ''
    }
  },
  computed: {
    showClearBtn () {
      return this.query.length
    }
  },
  methods: {
    handleClear () {
      this.query = ''
      this.handleChange()
    },
    handleChange () {
      this.$emit('on-change', this.query)
    }
  }
}
</script>

<style lang="scss" scoped>
.search-bar {
  .search-input {
    flex: 1;
    margin-right: 2.8vw;
    background-color: #fff;
    height: 5.2vh;
    border-radius: 2.6vh;
    padding: .8vw 4.4vw .9vw 2.1vw;
    overflow: hidden;
    .icon-search {
      width: 4.1vw;
      flex: 0 0 4.1vw;
    }
    input {
      margin: 0 2.1vw;
      flex: 1;
      background-color: transparent;
      border: none;
      outline: none;
      font-size: 4rem;
      color: #000;
      &::placeholder {
        color: #ABABAB;
        opacity: 1;
      }
    }
    .icon-close {
      width: 4.2vw;
      flex: 0 0 4.2vw;
    }
  }
}
</style>

<i18n>
{
  "zh-CN": {
    "search": "搜索",
    "searchLocation": "搜索位置"
  },
  "en": {
    "search": "Search",
    "searchLocation": "Search location"
  },
  "th": {
    "search": "ค้นหา",
    "searchLocation": "ค้นหาสถานที่"
  }
}
</i18n>
