<template>
  <label class="switch">
    <input type="checkbox"
          :checked="checked"
          :disabled="loading || disabled"
          @change="handleChange">
    <div class="switch-display">
      <span class="switch-display-handle">
        <IconLoading v-if="loading" class="switch-loading"></IconLoading>
      </span>
    </div>
  </label>
</template>

<script>
import IconLoading from './IconLoading'

export default {
  name: 'iSwitch',
  props: {
    value: {},
    disabled: {
      type: Boolean,
      default: false
    },
    trueValue: {
      default: true
    },
    falseValue: {
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    }
  },
  components: {
    IconLoading
  },
  computed: {
    checked () {
      return this.value === this.trueValue
    }
  },
  methods: {
    handleChange ({ target }) {
      const value = target.checked ? this.trueValue : this.falseValue
      this.$emit('input', value)
      this.$emit('on-change', value)
    }
  }
}
</script>

<style lang="scss" scoped>
.switch {
  display: inline-block;
  input[type="checkbox"] {
    display: none;
    &:checked + .switch-display {
      background-color: $primary-color;
      border-color: $primary-color;
      .switch-display-handle {
        transform: translate3d(100%, 0, 0);
        &:after {
          background-color: #fff;
        }
      }
    }
    &:disabled + .switch-display {
      cursor: not-allowed;
      background-color: #999;
      border-color: #999;
      .switch-display-handle:after {
        background-color: darken(#fff, 20);
      }
    }
  }
  .switch-display {
    $sw: 122 / $r;
    $sh: 66 / $r;
    width: #{$sw}rem;
    height: #{$sh}rem;
    background-color: #000;
    border-radius: #{$sh / 2}rem;
    border: #{2 / $r}rem solid #fff;
    padding: #{5 / $r}rem;
    box-sizing: border-box;
    transition: $base-transition;
    overflow: hidden;

    .switch-display-handle {
      display: inline-block;
      vertical-align: top;
      height: 100%;
      width: #{$sw - $sh}rem;
      position: relative;
      transition: $base-transition;
      &:after {
        content: '';
        position: absolute;
        z-index: 1;
        height: 100%;
        width: #{$sh - 10 / $r - 4 / $r}rem;
        border-radius: 50%;
        background-color: rgba(255, 255, 255, .5);
      }
      .switch-loading {
        position: absolute;
        width: #{$sh - 10 / $r - 4 / $r}rem;
        display: block;
        margin: 0 auto;
        z-index: 2;
      }
    }
  }
}
</style>
