<template>
  <Interact class="page delivery-success">
    <img :src="appConfig.sceneBackgrounds ? (appConfig.sceneBackgrounds.key4 || faceLove) : faceLove">
    <template slot="tips">
      {{ $t('taskSuccess') }}
    </template>
    <template slot="title">
      <!-- <div class="title-row">优衣库 <img src="@/assets/images/icon-arrow-right.png"> 中国银行</div>
      <div class="title-row">优衣库 <img src="@/assets/images/icon-arrow-right.png"> 横滨酒店</div> -->
      <div class="flex-default-center">
        <div class="title-row"
            v-for="(row, index) in taskSummary"
            :key="index">
          {{ row[0].targetPointName }} <img src="@/assets/images/icon-arrow-right.png"> {{ row[1].targetPointName }}
        </div>
      </div>
    </template>
    <iButton type="outline" slot="footer" @click="backHome">
      {{ $t('gotIt') }}({{ countdown }}S)
    </iButton>
  </Interact>
</template>

<script>
import { mapState } from 'vuex'
import Interact from '@/components/Interact.vue'
import iButton from '@/components/Button.vue'
import countdown from '@/mixins/countdown'
import faceLove from '../../assets/images/face-love.png'
import backHandle from '@/mixins/backHandle'

export default {
  name: 'DeliverySuccess',
  mixins: [countdown, backHandle],
  props: {
    task: {
      type: Object,
      required: true
    }
  },
  components: {
    Interact,
    iButton
  },
  data () {
    return {
      COUNTDOWN: 20,
      faceLove
    }
  },
  computed: {
    ...mapState('app', ['appConfig']),
    taskSummary () {
      const { length } = this.task.taskNodes
      if (length < 2) {
        return []
      }
      const rows = [
        [{ targetPointName: this.$t('currentPoint') }, this.task.taskNodes[0]]
      ]
      length === 3 && rows.push([this.task.taskNodes[0], this.task.taskNodes[1]])
      return rows
    }
  },
  methods: {
    backHome () {
      this.countdownTimer && clearTimeout(this.countdownTimer)
      this.back()
    }
  },
  mounted () {
    this.startCountdown(this.backHome)
  }
}
</script>

<style lang="scss" scoped>
.delivery-success {
  .title-row {
    &:first-child {
      margin-right: 4rem;
    }
    img {
      width: 5.1vw;
      height: 2.1vh;
    }
  }
}
</style>
