<template>
  <div class="page page-charge">
<!--    <Pending v-if="status === PENDING"></Pending>-->
    <Interact v-if="status === RUNNING" key="running">
      <img :src="appConfig.sceneBackgrounds ? (appConfig.sceneBackgrounds.key2 || faceWorking) : faceWorking">
      <template slot="tips">
        {{ $t('destination') }}
      </template>
      <template slot="title">
        {{ taskTarget }}
      </template>
    </Interact>
    <RunningFailed></RunningFailed>
    <RunningException :type="type"></RunningException>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
// import Pending from './Pending.vue'
import Interact from '@/components/Interact.vue'
import RunningFailed from '@/components/RunningFailed'
import RunningException from '@/components/RunningException'
import pong from '@/ue4/pong'
import pongTypes from '@/ue4/pong/types'
import TASK_STATUS from '@/const'
import faceWorking from '@/assets/images/face-working.png'

const { PENDING, RUNNING, SUCCESS } = TASK_STATUS

export default {
  name: 'Charge',
  components: {
    // Pending,
    Interact,
    RunningFailed,
    RunningException
  },
  data () {
    return {
      status: PENDING,
      PENDING,
      RUNNING,
      SUCCESS,
      task: null,
      type: 24,
      faceWorking
    }
  },
  computed: {
    ...mapState('task', ['currentTask']),
    ...mapState('app', ['appConfig']),
    taskTarget () {
      if (this.task) {
        return this.task.detail || this.$t('charging')
      }
      return '-'
    }
  },
  methods: {
    ...mapMutations('task', ['clearCurrentTask']),
    handleTaskNotify (data) {
      this.task = data
      if (data.status === 'running') {
        this.status = RUNNING
      }
    },
    handleTaskSummary (data) {
      this.task = data
      if (data.status === '4' || data.status === TASK_STATUS.SUCCESS) {
        this.status = SUCCESS
        // 清除一下缓存任务防止意外
        this.clearCurrentTask()
        this.$router.replace({ name: 'Home' })
      }
    }
  },
  mounted () {
    pong.$on(pongTypes.TASK_NOTIFY, this.handleTaskNotify)
    // 任务成功/失败监控
    pong.$on(pongTypes.TASK_SUMMARY, this.handleTaskSummary)
    if (this.currentTask && this.currentTask.taskType === this.type) {
      this.task = this.currentTask
      this.status = RUNNING
    }
  },
  beforeDestroy () {
    pong.$off(pongTypes.TASK_NOTIFY, this.handleTaskNotify)
    pong.$off(pongTypes.TASK_SUMMARY, this.handleTaskSummary)
  }
}
</script>

<style lang="scss" scoped>
.page-charge {}
</style>
