<template>
  <div class="page page-home" @touchstart="handleTouchScreen">
    <iHeader></iHeader>
    <Desktop></Desktop>
    <iFooter></iFooter>
    <Standby v-show="standby" @touchstart.native.stop.prevent="handleTouchScreen"></Standby>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { logger } from '@/ue4/ping'
import Standby from './Standby.vue'
import iHeader from './Header.vue'
import Desktop from './Desktop.vue'
import iFooter from './Footer.vue'

export default {
  name: 'Home',
  components: {
    Standby,
    iHeader,
    Desktop,
    iFooter
  },
  data () {
    return {
      queryTimer: null,
      standbyTimer: null,
      standby: false
    }
  },
  computed: {
    ...mapState('app', ['robotInfo'])
  },
  methods: {
    ...mapActions('task', ['queryTask']),
    startStandbyTimer () {
      this.standbyTimer && clearTimeout(this.standbyTimer)
      this.standbyTimer = setTimeout(() => {
        this.standby = true
        console.log('待机')
        logger('待机')
      }, 60000)
    },
    handleTouchScreen () {
      this.standby = false
      console.log('重置待机计时')
      logger('重置待机计时')
      this.startStandbyTimer()
    },
    queryTimerHandle () {
      this.queryTimer && clearTimeout(this.queryTimer)
      if (this.robotInfo) {
        this.queryTask()
        return
      }
      this.queryTimer = setTimeout(this.queryTimerHandle, 500)
    }
  },
  mounted () {
    if (this.robotInfo) {
      this.queryTask()
    } else {
      this.queryTimer && clearTimeout(this.queryTimer)
      this.queryTimer = setTimeout(this.queryTimerHandle, 500)
    }
    this.startStandbyTimer()
  },
  beforeDestroy () {
    this.queryTimer && clearTimeout(this.queryTimer)
    this.standbyTimer && clearTimeout(this.standbyTimer)
  }
}
</script>

<style lang="scss" scoped>
.page-home {
  display: flex;
  flex-direction: column;
  padding: 3% 7.5%;
  .task-detail {
    margin-top: 3rem;
  }
}
</style>
