import ping from '@/ue4/ping'
import pingTypes from '@/ue4/ping/types'

export default {
  data () {
    return {
      doorsStatus: [0, 0]
    }
  },
  methods: {
    changeDoorsStatus (doorIndex, status) {
      this.doorsStatus[parseInt(doorIndex) - 1] = status
      ping(pingTypes.DOOR_SWITCH, {
        doors: this.doorsStatus.map((_, index) => `door_${index + 1}`),
        status: this.doorsStatus,
        mock: status
      })
    }
  }
}
