<template>
  <div id="app"
      :class="{ 'app-disconnected': !connectStatus }"
      :style="{ backgroundImage: `url(${appBackground})` }"
      @touchstart="handleClick">
    <transition name="disconnected">
      <NetworkError v-show="!connectStatus"></NetworkError>
    </transition>
    <router-view />
    <QA></QA>
    <!-- <div style="position:fixed; top: 20px; left: 20px;z-index:2;">
      <div v-for="(i, index) in 10" :key="index">
        <button class="fs4" v-for="(btn, j) in 2" :key="j" @click="handleTest(`${i}-${btn}`)">
          {{ `${i}-${btn}` }}
        </button>
      </div>
    </div> -->
  </div>
</template>

<script>
import { mapActions, mapState, mapMutations } from 'vuex'
import ping, { logger } from '@/ue4/ping'
import pingTypes from '@/ue4/ping/types'
import pong from '@/ue4/pong'
import pongTypes from '@/ue4/pong/types'
import NetworkError from '@/components/NetworkError'
import QA from '@/components/QA'
import ClickDetect from '@/utils/clickDetect'
import TASK_STATUS from '@/const'

const defaultBg = require('@/assets/images/index-bg.jpg')

export default {
  name: 'GingerLite',
  components: {
    NetworkError,
    QA
  },
  data () {
    return {
      vconsole: new ClickDetect([0, 60], [0, 60], () => {
        const vconsoleDom = document.querySelector('#__vconsole')
        if (vconsoleDom) {
          const { display } = vconsoleDom.style
          vconsoleDom.style.display = display === 'block' ? 'none' : 'block'
        }
      }),
      // launcher: new ClickDetect([1080, 1200], [0, 120], () => {
      //   ping(pingTypes.TOGGLE_LAUNCHER)
      // }, 2),
      appConfigOpId: null,
      getRobotInfoInterval: null
    }
  },
  computed: {
    ...mapState('app', ['robotInfo', 'appConfig', 'connectStatus']),
    ...mapState('task', ['currentTask', 'queryTaskLoading']),
    hasRunningTask () {
      return !!this.currentTask
    },
    appBackground () {
      if (this.appConfig && this.appConfig.background) {
        return this.appConfig.background
      }
      if (process.env.NODE_ENV !== 'production') {
        return defaultBg
      }
      return ''
    }
  },
  watch: {
    // 查询任务有正在运行的任务，就跳到相应页面
    hasRunningTask (nv) {
      if (nv) {
        const { taskType, status } = this.currentTask
        logger('查询到正在运行的任务：', this.currentTask)
        if (this.$route.name === 'Other') {
          logger('正在[其他]页面')
          return
        }
        if (taskType === 0) {
          if (this.$route.name !== 'Delivery') {
            logger('跳转到送货界面')
            this.$router.push({ name: 'Delivery', query: { authed: true } })
          }
          console.log(status)
          // else if (
          //   // 任务正在运行而且刚好停在输入验证码的界面，不显示弹窗让用户重试
          //   status == TASK_STATUS.RUNNING &&
          //   this.$route.matched[0].instances.default.status == TASK_STATUS.RUNNING &&
          //   this.$route.matched[0].instances.default.$refs.running &&
          //   this.$route.matched[0].instances.default.$refs.running.status == 2
          // ) {
          //   console.log('here')
          //   // this.clearCurrentTask()
          // }
        } else if (taskType === 1) {
          if (this.$route.name !== 'Guide') {
            logger('跳转到引领界面')
            this.$router.push({ name: 'Guide' })
          }
        } else if (taskType === 12) {
          if (this.$route.name !== 'Working') {
            logger('跳转到任务中界面')
            this.$router.push({ name: 'Working' })
          }
        } else if (taskType === 24) {
          if (this.$route.name !== 'Charge') {
            logger('回航充电')
            this.$router.push({ name: 'Charge' })
          }
        } else {
          logger('未知任务类型')
          console.log('未知任务类型')
        }
      } else {
        // 未查询到任务且还在任务中界面，就退回主页
        if (['Delivery', 'Guide', 'Charge'].includes(this.$route.name)) {
          if (this.$route.matched[0].instances.default.status === TASK_STATUS.RUNNING) {
            this.$router.replace({ name: 'Home' })
          }
        }
      }
    }
  },
  methods: {
    handleTest (index) {
      console.log('@@@', index)
      ping(pingTypes.DOOR_SWITCH, {
        doors: [index],
        status: [1]
      })
    },
    ...mapActions('map', ['queryMapInfoResp', 'queryReturnPointResp']),
    ...mapActions('task', ['queryTask']),
    ...mapActions('app', ['getAppConfig']),
    ...mapMutations('app', ['setRobotInfo', 'setConnectStatus', 'setPageData']),
    ...mapMutations('task', ['setCurrentTask', 'clearCurrentTask']),
    pingHandle () {
      this.getRobotInfoInterval && clearTimeout(this.getRobotInfoInterval)
      if (this.robotInfo) {
        return
      }
      ping(pingTypes.GET_ROBOT_INFO)
      this.getRobotInfoInterval = setTimeout(this.pingHandle, 500)
    },
    getRobotInfo () {
      this.$toast.show(this.$t('getRobotInfoTips'), { icon: 'loading' })
      this.getRobotInfoInterval && clearTimeout(this.getRobotInfoInterval)
      this.getRobotInfoInterval = setTimeout(this.pingHandle, 500)
      pong.$emit(pongTypes.GET_ROBOT_INFO_RESP, {
        code: 0,
        // robotId: 'GINLITXR-1LXXXXXXXXXGNL03S2038000019',
        robotId: '74EE2ADB99D9',
        robotType: 'patrol',
        ENV_URL: 'https://cross-fit86.harix.iamidata.com',
        bodyType: {
          SKUNumber: 3
        }
      }, 1000)
    },
    handleDisconnected () {
      this.setConnectStatus(false)
    },
    handleReconnected () {
      // 如果是断网状态下收到reconnected才是真正重连
      // 否则是闪断
      if (!this.connectStatus) {
        this.setConnectStatus(true)
        this.queryTask()
      }
    },
    handleClick (event) {
      this.vconsole.detect(event)
      // this.launcher.detect(event)
    },
    getRobotInfoResp (data) {
      // 查询到设备id再请求appconfig
      this.getAppConfig()
      // 设置sdk选项
      this.cross_sdk.setOption({
        robotCode: data.robotId,
        type: 0,
        appId: '61095f6966146393710420b62808637b',
        appSecret: '2ea5aa97d33a46369bcc45a761efea0c'
      })
      this.setRobotInfo(data)
      this.getRobotInfoInterval && clearTimeout(this.getRobotInfoInterval)
      this.$toast.hide()
    },
    handleCallForExpress () {
      this.$router.push({ name: 'ExpressEntry' })
    },
    handleDeliveryArrived (data) {
      if (this.$route.name !== 'Delivery') {
        logger('app收到notifyOpenDoor: ', data)
        localStorage.setItem('arrivedDelevery', JSON.stringify(data))
        this.$router.push({ name: 'Delivery', query: { authed: true } })
      }
    },
    handleUpdatePage (data) {
      if (this.currentTask) this.clearCurrentTask()
      if (data.pageType === 'ProgressPage') {
        this.setPageData(data)
        this.$router.push({ name: 'Working' })
      } else if (data.pageType === 'VideoCallPage') {
        this.$router.push({ name: 'VideoChat', query: { caller: data.caller } })
      } else if (data.pageType === 'TrainingPage') {
        this.$router.push({ name: 'Training', params: { data: data.trainingResults } })
      }
    },
    handleGotoDelivery () {
      if (this.$route.name !== 'DispatchExpress') {
        this.$router.push({ name: 'DispatchExpress' })
      }
    },
    handleTaskNotify (data) {
      if (data.taskType === 'delivery' && this.$route.name !== 'Delivery') {
        // 脑瘫设计，不要问为什么
        data.taskType = 0
        this.setCurrentTask(data)
        this.$router.push({ name: 'Delivery', query: { authed: true } })
      } else if (data.taskType === 'guide' && this.$route.name !== 'Guide') {
        // 同上
        data.taskType = 1
        this.setCurrentTask(data)
        this.$router.push({ name: 'Guide' })
      } else if (data.taskType === 'charge' && this.$route.name !== 'Charge') {
        data.taskType = 24
        this.setCurrentTask(data)
        this.$router.push({ name: 'Charge' })
      }
    }
  },
  // 把事件响应放在created里做
  // 因为app的mounted可能会在其他组件的mounted之后
  // 如果在其他页面组件调用了ping，那这里可能会收不到
  // 特别是直接在某个页面直接刷新时
  created () {
    pong.$on(pongTypes.DISCONNECTED, this.handleDisconnected)
    pong.$on(pongTypes.RECONNECTED, this.handleReconnected)
    // 全局集中处理的数据
    pong.$on(pongTypes.MAP_INFO_RESP, this.queryMapInfoResp)
    pong.$on(pongTypes.READ_LOCAL_RESP, this.queryReturnPointResp)
    pong.$on(pongTypes.GET_ROBOT_INFO_RESP, this.getRobotInfoResp)
    pong.$on(pongTypes.CALL_FOR_EXPRESS, this.handleCallForExpress)
    pong.$on(pongTypes.UPDATE_PAGE, this.handleUpdatePage)
    pong.$on(pongTypes.PREPARE_DISPATCH, this.handleGotoDelivery)
    pong.$on(pongTypes.TASK_NOTIFY, this.handleTaskNotify)
    const language = window.navigator.userLanguage || window.navigator.language
    this.$i18n.locale = language
    this.getRobotInfo()
    /* eslint-disable */
    // setTimeout(() => {
    //   this.$router.push({ name: 'Training', params: { data: [{
    //     "userName": "userName",
    //     "pointName": "位置点",
    //     "question": "aaa",
    //     "answer": "aaa",
    //     "result": 1
    //   },
    //   {
    //     "userName": "userName2",
    //     "pointName": "位置点2",
    //     "question": "aaa",
    //     "answer": "aaa",
    //     "result": 1
    //   }] } })
    // }, 3000)
    /* eslint-enable */
  },
  mounted () {
    document.body.addEventListener('touchstart', this.handleClick)
    const fs = 12 / 1200 * window.innerWidth
    document.querySelector('html').style.fontSize = `${fs}px`
    // 兼容代码
    pong.$on(pongTypes.DELIVERY_ARRIVED, this.handleDeliveryArrived)
    // setTimeout(() => {
    //   pong.$emit(pongTypes.TASK_NOTIFY, {
    //     opId: 'mock',
    //     startPoi: '你家',
    //     endPoi: '我家',
    //     currentPoi: '中间',
    //     detail: '你好',
    //     status: 1,
    //     taskType: 'delivery',
    //     duration: 0
    //   })
    // }, 2000)
    // setTimeout(() => {
    //   pong.$emit('updatePage', {
    //     pageType: 'ProgressPage',
    //     status: 1,
    //     taskType: 'disinfect',
    //     taskName: 'mock',
    //     nextPoint: '厕所',
    //     currentPoint: '前台',
    //     info: {
    //       name: 'mock',
    //       duration: 5,
    //       days: '1,2,3,4',
    //       startTime: '14:00',
    //       endTime: '14:20',
    //       points: [
    //         {
    //           name: '第一个点'
    //         },
    //         {
    //           name: '第二个点'
    //         },
    //         {
    //           name: '第三个点'
    //         }
    //       ]
    //     }
    //   })
    // }, 1000)
  },
  beforeDestroy () {
    this.getRobotInfoInterval && clearTimeout(this.getRobotInfoInterval)
    pong.$off(pongTypes.DISCONNECTED, this.handleDisconnected)
    pong.$off(pongTypes.RECONNECTED, this.handleReconnected)
    pong.$off(pongTypes.MAP_INFO_RESP, this.queryMapInfoResp)
    pong.$off(pongTypes.READ_LOCAL_RESP, this.queryReturnPointResp)
    pong.$off(pongTypes.CALL_FOR_EXPRESS, this.handleCallForExpress)
    pong.$off(pongTypes.DELIVERY_ARRIVED, this.handleDeliveryArrived)
    pong.$off(pongTypes.UPDATE_PAGE, this.handleUpdatePage)
    pong.$off(pongTypes.PREPARE_DISPATCH, this.handleGotoDelivery)
  }
}
</script>

<style lang="scss">
html {
  font-size: #{$r}px;
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  user-select: none;
}

#app {
  font-family: PingFangSC-Regular, Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fff;
  height: 100%;
  width: 100%;
  overflow: hidden;
  background-color: #000;
  background-size: 100% 100%;
  transition: .2s ease-in-out;
  &.app-disconnected {
    padding-top: 5vh;
  }
}

.disconnected-enter-active,
.disconnected-leave-active {
  transform: scale3d(1, 1, 1);
  transition: .2s ease-in-out;
}

.disconnected-enter,
.disconnected-leave-to {
  transform: scale3d(1, 0, 1);
}

/* ELEMENTS */
div {
  box-sizing: border-box;
}

a,
a:visited {
  color: #fff;
  text-decoration: none;
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: transparent;
}

/* LAYOUTS */
.page {
  height: 100%;
  width: 100%;
}

.black-bg {
  background-color: #000;
}

.flex-default-center {
  display: flex;
  align-items: center;
}

.flex-between-center {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.mt-10 {
  margin-top: #{10/$r}rem;
}

/* COLORS */
.secondary-text-color {
  color: #ddd;
}

.primary-color-text {
  color: $primary-color;
}

/* FONTS */
.fs4 {
  font-size: 4rem;
}

/* VCONSOLE */
#__vconsole {
  display: none;
}
</style>
