<template>
  <div class="page page-working">
    <Interact class="page-working" @touchstart.native="handleClick">
      <template slot="tips">
        {{ getTips }}
      </template>
      <template slot="title">
        {{ getTaskName }}
      </template>
      <template slot="footer" v-if="showFooter">
        <template v-if="currentTask">
          <IconLoading v-if="taskLoading"></IconLoading>
          <template v-else>
            <iButton type="outline" @click="handleChangeTaskStatus">{{ $t('cancelTask') }}</iButton>
            <iButton class="cancel-task-btn" :emit-seconds="10" @click="handleChangeTaskStatus(false)">{{ $t('continueTask') }}</iButton>
          </template>
        </template>
        <iButton v-else :emit-seconds="10" type="outline" @click="handleBack">{{ $t('gotIt') }}</iButton>
      </template>
    </Interact>
    <Modal :show="showManager" :loading="taskLoading">
      <TaskItem :data="taskInfo" :open="open" @toggle="handleToggleTaskDetail"></TaskItem>
      <template slot="footer">
        <iButton type="outline-light" :emit-seconds="10" @click="handleCancel">{{ $t('cancel') }}</iButton>
        <iButton class="terminate-task-btn" @click="handleChangeTaskStatus">{{ $t('terminate') }}</iButton>
      </template>
    </Modal>
  </div>
</template>

<script>
import Interact from '@/components/Interact'
import iButton from '@/components/Button'
import Modal from '@/components/Modal'
import TaskItem from './Disinfect/TaskItem'
import IconLoading from '@/components/IconLoading'
import { mapState, mapMutations } from 'vuex'
import STATUS from '@/const'
import ClickDetect from '@/utils/clickDetect'
import request from '@/utils/request'
import backHandle from '@/mixins/backHandle'

export default {
  components: {
    Interact,
    iButton,
    Modal,
    TaskItem,
    IconLoading
  },
  mixins: [backHandle],
  data () {
    return {
      taskManager: new ClickDetect([0, 1200], [0, 1920], () => {
        this.$router.push({ name: 'PasswordAuth', query: { to: 'Working' } })
      }, 2),
      statusMapping: {
        [STATUS.PENDING]: 'taskPending',
        [STATUS.SUCCESS]: 'taskSuccess',
        [STATUS.PAUSED]: 'taskPaused',
        [STATUS.FAILED]: 'taskFailed',
        [STATUS.CANCELED]: 'taskCanceled',
        [STATUS.RUNNING]: 'taskRunning'
      },
      showManager: false,
      taskLoading: false,
      open: false
    }
  },
  computed: {
    ...mapState('app', ['pageData']),
    ...mapState('task', ['currentTask']),
    showFooter () {
      if (this.currentTask) {
        return true
      }
      return this.showBackBtn
    },
    showBackBtn () {
      if (!this.pageData) return true
      if (this.pageData.status === STATUS.SUCCESS || this.pageData.status === STATUS.FAILED ||
        this.pageData.status === STATUS.CANCELED) {
        return true
      }
      return false
    },
    getTips () {
      return `${this.$t(this.getTaskStatus)}${this.currentTask ? '' : `${this.getNextTips} ${this.getNext}`}`
    },
    getTaskStatus () {
      if (this.currentTask) {
        return this.statusMapping[this.currentTask.status]
      }
      if (!this.pageData) return
      return this.statusMapping[this.pageData.status]
    },
    getNextTips () {
      if (!this.pageData) return
      if (this.pageData.nextPoint) {
        return this.$t('next')
      }
      return ''
    },
    getNext () {
      if (!this.pageData) return
      return this.pageData.nextPoint
    },
    getTaskName () {
      if (this.currentTask) {
        return this.currentTask.taskName
      }
      if (!this.pageData) return
      return this.pageData.taskName
    },
    taskInfo () {
      if (!this.pageData) return
      if (this.pageData.info) return this.pageData.info
      return {
        name: this.pageData.taskName
      }
    }
  },
  methods: {
    ...mapMutations('task', ['clearCurrentTask']),
    handleBack () {
      this.back()
    },
    handleClick (e) {
      this.taskManager.detect(e)
    },
    handleCancel () {
      this.showManager = false
    },
    handleChangeTaskStatus (cancel) {
      this.taskLoading = true
      return request.post(
        `/crss/task/v1/taskLog/${cancel ? 'cancel' : 'retry'}`,
        { taskUuid: this.currentTask ? this.currentTask.taskInstanceId : this.pageData.taskInstanceId }
      ).then(() => {
        cancel && this.$message(this.$t('cancelSuccess'))
        if (this.showManager) {
          this.showManager = false
        }
        this.clearCurrentTask()
      }).catch((err) => {
        this.$message(err && err.message ? err.message : this.$t('error.-1'))
      }).finally(() => {
        this.taskLoading = false
      })
    },
    handleToggleTaskDetail () {
      this.open = !this.open
    }
  },
  mounted () {
    if (this.$route.query.authed) {
      this.showManager = true
    }
  }
}
</script>

<i18n>
{
  "zh-CN": {
    "working": "任务中",
    "next": "，下一站"
  }
}
</i18n>

<style lang="scss" scoped>
.page-working {
  .task-name {
    font-size: 5rem;
    text-align: left;
  }
  .terminate-task-btn,
  .cancel-task-btn {
    margin-left: 4rem;
  }
}
</style>
