<template>
  <div class="page page-dispatch-express-pending">
    <Bar class="bar">
      {{ $t('dispatchExpress') }}
    </Bar>
    <div class="tips fs4">
      {{ $t('enterUserPhoneNumber') }}
    </div>
    <div class="input-number flex-default-center">
      <div class="input-number-item"
          v-for="i in length"
          :key="i">
        {{ phoneNumber[i-1] }}
      </div>
    </div>
    <NumberKeyboard class="keyboard" @on-click="handleClick" :disabled="loading"></NumberKeyboard>
    <iButton class="comfirm-dispatch-btn"
            v-show="tasks.length"
            type="outline"
            :loading="loading"
            @click="handleStartDispatch">
      {{ $t('startDispatch') }}
    </iButton>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import request from '@/utils/request'
import Bar from '@/components/Bar.vue'
import NumberKeyboard from '@/components/NumberKeyboard.vue'
import iButton from '@/components/Button'
import ping from '@/ue4/ping'
import pingTypes from '@/ue4/ping/types'
import pong from '@/ue4/pong'
import pongTypes from '@/ue4/pong/types'

export default {
  components: {
    Bar,
    NumberKeyboard,
    iButton
  },
  data () {
    return {
      phoneNumber: [],
      tasks: [],
      length: 4,
      loading: false
    }
  },
  computed: {
    ...mapState('app', ['robotInfo']),
    inputLength () {
      return this.phoneNumber.length
    },
    lengthFullfill () {
      return this.inputLength === this.length
    }
  },
  methods: {
    handleClick (key) {
      if (key === 'delete') {
        this.phoneNumber.pop()
      } else if (!this.lengthFullfill) {
        this.phoneNumber.push(key)
        if (this.lengthFullfill) {
          this.validate()
        }
      }
    },
    validate () {
      if (this.loading) return
      this.loading = true
      request
        .get('/crss-mobile/uces/v1/person/getByFourPhone', {
          params: {
            phone: this.phoneNumber.join(''),
            robotCode: this.robotInfo.robotId
          }
        })
        .then((res) => {
          if (res.data && res.data.length) {
            this.tasks.push(...res.data.map(e => {
              return {
                mapId: e.mapCode,
                poi: e.pointName,
                userId: e.id,
                userName: e.name
              }
            }))
            this.$message(this.$t('validateSuccess'))
          } else {
            this.$message(this.$t('invalidPhoneNumber'))
          }
        })
        .finally(() => {
          this.loading = false
          this.phoneNumber = []
        })
    },
    handleStartDispatch () {
      this.loading = true
      ping(pingTypes.REQUEST_DELIVERY, { tasks: this.tasks, taskType: 'requestDelivery' })
    },
    handleAddReceiver (data) {
      this.phoneNumber = data.phoneNum.toString().split('')
      this.validate()
    }
  },
  created () {
    pong.$on(pongTypes.ADD_RECEIVER, this.handleAddReceiver)
    pong.$on(pongTypes.START_DISPATCH, this.handleStartDispatch)
  },
  beforeDestroy () {
    pong.$off(pongTypes.ADD_RECEIVER, this.handleAddReceiver)
    pong.$off(pongTypes.START_DISPATCH, this.handleStartDispatch)
  }
}
</script>

<style lang="scss" scoped>
.page-dispatch-express-pending {
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
  .bar {
    align-self: flex-start;
  }
  .keyboard {
    width: 78%;
    height: 52%;
  }
  .tips {
    margin-top: 4vh;
  }
  .input-number {
    margin: 3.1vh 0;
    .input-number-item {
      height: #{120 / $r}rem;
      width: #{120 / $r}rem;
      border-radius: #{10 / $r}rem;
      background-color: #fff;
      line-height: #{120 / $r}rem;
      text-align: center;
      font-size: #{82 / $r}rem;
      margin-right: 3.3vw;
      color: #000;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  .comfirm-dispatch-btn {
    margin-top: 3vh;
  }
}
</style>

<i18n>
{
  "zh-CN": {
    "enterUserPhoneNumber": "请输入手机号后4位",
    "invalidPhoneNumber": "验证失败，请核对信息",
    "startDispatch": "开始派送",
    "validateSuccess": "验证成功，请把包裹放进筐内。如需继续，请重复输入放置即可"
  },
  "en": {
    "enterUserPhoneNumber": "Please enter the last four digits of reciver phone number",
    "invalidPhoneNumber": "Validation failed, please check the information",
    "startDispatch": "Start dispatching",
    "validateSuccess": "Validation succeed, put package into basket. If more receivers, please enter the last four digits of receiver phone number"
  },
  "th": {
    "enterUserPhoneNumber": "โปรดใส่รหัส 4 ตัวเลขจากข้อความในมือถือ",
    "invalidPhoneNumber": "การตรวจสอบล้มเหลว, โปรดตรวจสอบข้อมูลอีกครัง",
    "startDispatch": "เริ่มการทำงาน",
    "validateSuccess": "การตรวจสอบเสร็จสมบูรณ์, ใส่สินค้าในที่เก็บของ. ถ้ามีผู้รับอื่นเพิ่มเติม, ปรดใส่รหัส 4 ตัวเลขจากข้อความในมือถือของผู้รับคนใหม่"
  }
}
</i18n>
