import { logger } from '@/ue4/ping'
import TYPE from '@/const'
import request from '@/utils/request'

export default {
  namespaced: true,
  state: {
    appConfig: null,
    robotInfo: null,
    connectStatus: true,
    pageData: null
  },
  getters: {
    hasCab ({ robotInfo }) {
      if (!robotInfo || !robotInfo.bodyType) {
        const hasCab = JSON.parse(localStorage.getItem('hasCab'))
        logger('使用缓存箱体信息：', hasCab)
        return hasCab
      }
      return robotInfo.bodyType.SKUNumber == TYPE.BOX
    },
    isPatrol ({ robotInfo }) {
      return robotInfo && robotInfo.robotType === 'patrol'
    }
  },
  mutations: {
    setAppConfig (state, data) {
      const { ENV_URL } = state.robotInfo
      if (ENV_URL) {
        if (data.background && !data.background.match(/^https?:\/\//)) {
          data.background = ENV_URL + data.background
        }
        if (data.sceneBackgrounds) {
          Object.keys(data.sceneBackgrounds).forEach((key) => {
            if (!data.sceneBackgrounds[key].match(/^https?:\/\//)) {
              data.sceneBackgrounds[key] = ENV_URL + data.sceneBackgrounds[key]
            }
          })
        }
        if (data.logo && !data.logo.match(/^https?:\/\//)) {
          data.logo = ENV_URL + data.logo
        }
        if (data.apps) {
          data.apps = data.apps.map(e => {
            if (!e.icon.match(/^https?:\/\//)) {
              e.icon = ENV_URL + e.icon
            }
            return e
          })
        }
      }
      state.appConfig = data
    },
    setRobotInfo (state, data) {
      state.robotInfo = data
    },
    setConnectStatus (state, status) {
      state.connectStatus = status
    },
    setPageData (state, data) {
      state.pageData = data
    }
  },
  actions: {
    getAppConfig ({ dispatch, commit, getters }) {
      request
        .get('/crss-robot/reception/robotapi/v1/configtemplate/getAppConfig')
        .then((res) => {
          if (res && res.data) {
            const sceneBackgrounds = res.data.sceneBackgrounds
            if (sceneBackgrounds) {
              try {
                res.data.sceneBackgrounds = JSON.parse(sceneBackgrounds)
              } catch {
                res.data.sceneBackgrounds = null
              }
            }
            // console.log('appConfig', res.data)
            commit('setAppConfig', res.data)
            localStorage.setItem('hasCab', getters.hasCab)
            logger('缓存箱体信息：', getters.hasCab)
          }
        }).catch((err) => {
          logger('getAppConfig失败', err)
          // appConfig是非常重要的信息，防止服务器宕机
          setTimeout(() => {
            dispatch('getAppConfig')
          }, 5000)
        })
    }
  }
}
