import { mapState, mapActions } from 'vuex'

export default {
  data () {
    return {
      currentFloorIndex: 0,
      loading: false,
      queryMapTimeout: null,
      showRetryBtn: false
    }
  },
  computed: {
    ...mapState('map', ['mapInfo', 'queryMapLoading']),
    floors () {
      if (this.mapInfo) {
        return this.mapInfo.map(e => {
          return {
            floor: e.floor,
            mapId: e.mapId
          }
        })
      }
      return []
    },
    currentFloorInfo () {
      if (this.mapInfo && this.mapInfo.length) {
        return this.mapInfo[this.currentFloorIndex]
      }
      return {}
    },
    queryMapNoResp () {
      return !this.mapInfo && this.queryMapLoading
    }
  },
  watch: {
    queryMapNoResp (nv) {
      // 正在查询，超过10秒没查到就显示个按钮可以让用户点击重试加载
      if (!nv) {
        this.showRetryBtn = false
        this.queryMapTimeout && clearTimeout(this.queryMapTimeout)
      }
    }
  },
  methods: {
    ...mapActions('map', ['queryMapInfo']),
    handleSwitchFloor (index) {
      this.currentFloorIndex = index
    },
    startQueryMapTimeout () {
      this.queryMapTimeout && clearTimeout(this.queryMapTimeout)
      this.queryMapTimeout = setTimeout(() => {
        this.showRetryBtn = true
      }, 10000)
    },
    handleRetry () {
      this.showRetryBtn = false
      this.startQueryMapTimeout()
      // 再发一次请求
      this.queryMapInfo()
    }
  },
  mounted () {
    this.queryMapInfo()
    if (this.queryMapNoResp) {
      this.startQueryMapTimeout()
    }
  },
  beforeDestroy () {
    this.queryMapTimeout && clearTimeout(this.queryMapTimeout)
  }
}
