<template>
  <Modal class="running-failed"
        :show="show"
        key="running-failed">
    <template>
      <div>{{ getTitle }}</div>
      <div class="failed-tips" v-if="task">
        <template v-if="isCanceled">
          {{ task.taskName }}
        </template>
        <Error v-else-if="hasErrorCode" :code="task.errorCode"></Error>
      </div>
      <!-- 如果任务失败或被取消都不能重试 -->
      <iButton slot="footer"
              class="continue-task-btn"
              @click="handleBack"
              :emit-seconds="10">
        {{ $t('gotIt') }}
      </iButton>
    </template>
  </Modal>
</template>

<script>
// 任务过程中直接收到失败或取消后弹框
// 显示我知道了
import Modal from '@/components/Modal'
import iButton from '@/components/Button'
import Error from '@/components/Error'
import pong from '@/ue4/pong'
import pongTypes from '@/ue4/pong/types'
import TASK_STATUS from '@/const'

export default {
  name: 'RunningFailed',
  components: {
    Modal,
    iButton,
    Error
  },
  data () {
    return {
      task: null,
      show: false,
      showErrorCode: false
    }
  },
  computed: {
    isCanceled () {
      return this.task && this.task.status == TASK_STATUS.CANCELED
    },
    getTitle () {
      if (this.isCanceled) {
        return this.$t('taskCanceled')
      }
      return this.$t('taskFailed')
    },
    hasErrorCode () {
      return this.task && this.task.errorCode !== undefined
    }
  },
  methods: {
    handleBack () {
      this.$router.replace({ name: 'Home' })
    },
    handleTaskSummary (data) {
      if (data.status === '3' || data.status === '5' || data.status === TASK_STATUS.FAILED || data.status === TASK_STATUS.CANCELED) {
        this.show = true
        this.task = data
      } else {
        this.show = false
      }
    }
  },
  mounted () {
    pong.$on(pongTypes.TASK_SUMMARY, this.handleTaskSummary)
  },
  beforeDestroy () {
    pong.$off(pongTypes.TASK_SUMMARY, this.handleTaskSummary)
  }
}
</script>

<style lang="scss" scoped>
.running-failed {
  .failed-tips {
    margin-top: 3rem;
  }
}
</style>
