<template>
  <div class="page publicize-pending">
    <SelectList v-if="show === ROUTE"
                @on-confirm="handleConfirmRoute"
                :on-back="handleBack"
                :placeholder="$t('searchRoute')"
                :empty-tips="$t('noRouteTips')"
                :search-empty-tips="$t('noMatchRoute')"
                :confirm-text="$t('confirmRoute')"
                :select-tips="$t('routeIsRequired')"
                :list="routeList">
    </SelectList>
    <SelectList v-else-if="show === THEME"
                @on-confirm="handleConfirmTheme"
                :on-back="handleBack"
                :placeholder="$t('searchTheme')"
                :empty-tips="$t('noThemeTips')"
                :search-empty-tips="$t('noMatchTheme')"
                :confirm-text="$t('confirmTheme')"
                :list="themeList">
    </SelectList>
    <template v-else>
      <Bar>
        {{ $t('Publicize') }}
      </Bar>
      <ListPanel class="setting" >
        <SettingRow @click.native="toggleShow(ROUTE)">
          <template slot="title">{{ $t('route') }}</template>
          <template v-if="route">
            {{ route.name }}
          </template>
          <div v-else class="placeholder">
            <strong>+</strong>{{ $t('selectRoute') }}
          </div>
        </SettingRow>
        <SettingRow @click.native="toggleShow(THEME)">
          <template slot="title">{{ $t('theme') }}</template>
          <template v-if="theme">
            {{ theme.name }}
          </template>
          <div v-else class="placeholder">
            <strong>+</strong>{{ $t('selectTheme') }}
          </div>
        </SettingRow>
        <SettingRow @click.native="toggleShow(DATETIME)">
          <template slot="title">{{ $t('time') }}</template>
          <div class="flex-default-center">
            <div class="default-start-time">
              {{ $t('today') }} {{ $d(now, 'time') }}
            </div>
            <span class="time-seperator">-</span>
            <div v-if="endtime">
              {{ endtime.label }}
            </div>
            <div v-else class="placeholder">
              {{ $t('selectTime') }}
            </div>
          </div>
        </SettingRow>
        <iButton slot="footer" @click="handleStart">{{ $t('startPublicize') }}</iButton>
      </ListPanel>
    </template>
    <VuePicker :data="pickData"
              :showToolbar="true"
              :visible.sync="pickerVisible"
              @confirm="handleConfirmEndtime" />
  </div>
</template>

<script>
import Bar from '@/components/Bar.vue'
import iButton from '@/components/Button'
import ListPanel from './ListPanel'
import SelectList from './SelectList'
import SettingRow from './SettingRow'
import VuePicker from 'vue-pickers'
import ping from '@/ue4/ping'
import pingTypes from '@/ue4/ping/types'
import pong from '@/ue4/pong'
import pongTypes from '@/ue4/pong/types'

const ROUTE = 1
const THEME = 2
const DATETIME = 3
const MAIN = 4

export default {
  name: 'Publicize',
  components: {
    Bar,
    SettingRow,
    iButton,
    SelectList,
    ListPanel,
    VuePicker
  },
  data () {
    const now = Date.now()
    return {
      show: MAIN,
      ROUTE,
      THEME,
      DATETIME,
      MAIN,
      now,
      routeList: [],
      themeList: [],
      route: null,
      theme: null,
      endtime: null,
      pickData: [
        Array
          .from(Array(7).keys())
          .map((e, index) => {
            const time = now + index * 24 * 60 * 60 * 1000
            return {
              label: index === 0 ? this.$t('today') : this.$d(time, 'date'),
              value: time
            }
          }),
        [
          { label: this.$t('morning'), value: 0 },
          { label: this.$t('afternoon'), value: 1 }
        ],
        Array
          .from(Array(12).keys())
          .map(e => {
            return {
              label: e.toString().padStart(2, 0),
              value: e
            }
          }),
        Array
          .from(Array(60).keys())
          .map(e => {
            return {
              label: e.toString().padStart(2, 0),
              value: e
            }
          })
      ]
    }
  },
  computed: {
    pickerVisible: {
      get () {
        return this.show === DATETIME
      },
      set (visible) {
        if (visible) {
          this.show = DATETIME
        } else {
          this.show = MAIN
        }
      }
    }
  },
  methods: {
    toggleShow (page) {
      this.show = page
    },
    handleBack () {
      this.show = MAIN
    },
    handleConfirmRoute (route) {
      this.route = route
      this.handleBack()
    },
    handleConfirmTheme (theme) {
      this.theme = theme
      this.handleBack()
    },
    handleConfirmEndtime (data) {
      const dDate = new Date(data[0].value)
      const year = dDate.getFullYear()
      const month = dDate.getMonth() + 1
      const date = dDate.getDate()
      const hour = data[1].value ? data[2].value + 12 : data[2].value
      const minute = data[3].value
      this.validateEndtime(year, month, date, hour, minute).then(() => {
        this.endtime = {
          label: `${data[0].label} ${hour.toString().padStart(2, 0)}:${minute.toString().padStart(2, 0)}`,
          value: [year, month, date, hour, minute]
        }
      })
    },
    validateEndtime (year, month, date, hour, minute) {
      const now = Date.now()
      // 不能小于当前时间
      return new Promise(resolve => {
        if (new Date(`${year}/${month}/${date} ${hour}:${minute}:00`).getTime() <= now) {
          this.$message(this.$t('invalidEndtime'))
        } else {
          resolve()
        }
      })
    },
    handleGetPublicizeDataResp (resp) {
      if (resp.code === 0) {
        const { data } = resp
        if (data) {
          if (data.routeIDs && data.routeNAMEs) {
            this.routeList = data.routeIDs.map((e, index) => {
              return {
                id: e,
                name: data.routeNAMEs[index]
              }
            })
          }
          if (data.themeIDs && data.themeNAMEs) {
            this.themeList = data.themeIDs.map((e, index) => {
              return {
                id: e,
                name: data.themeNAMEs[index]
              }
            })
          }
        }
      }
    },
    handleStart () {
      if (!this.route) {
        return this.$message(this.$t('routeIsRequired'))
      }
      if (!this.theme) {
        return this.$message(this.$t('themeIsRequired'))
      }
      if (!this.endtime) {
        return this.$message(this.$t('endtimeIsRequired'))
      }
      this.validateEndtime(...this.endtime.value).then(() => {
        ping(pingTypes.START_PUBLICIZE, {
          routeID: this.route.id,
          themeID: this.theme.id,
          end: this.endtime.value.join('-')
        })
      })
    }
  },
  mounted () {
    pong.$on(pongTypes.GET_PUBLICIZE_DATA_RESP, this.handleGetPublicizeDataResp)
    ping(pingTypes.GET_PUBLICIZE_DATA)
  },
  beforeDestroy () {
    pong.$off(pongTypes.GET_PUBLICIZE_DATA_RESP, this.handleGetPublicizeDataResp)
  }
}
</script>

