    "noRouteTips": "您当前暂未可选择的路线请前往后台创建哦~",
    "noMatchRoute": "没有搜索到相关的路线",
    "confirmRoute": "确认路线",
    "routeIsRequired": "请选择路线",
    "themeIsRequired": "请选择主题",
    "endtimeIsRequired": "请选择结束时间",
    "invalidEndtime": "结束时间不能小于当前时间",
    "publicizeFailed": "开始宣讲失败"
  },
  "en": {
    "today": "Today",
    "startPublicize": "Start patrolling",
    "searchTheme": "Search theme",
    "confirmTheme": "Confirm theme",
    "selectTime": "Please select a deadline",
    "noThemeTips": "No theme matched, please create it on Administration Station",
    "noMatchTheme": "No match theme was found",
    "searchRoute": "Search route",
    "noRouteTips": "No route matched, please create it on Administration Station",
    "noMatchRoute": "No matched route was found",
    "confirmRoute": "Confirm the route",
    "routeIsRequired": "Please select the route",
    "themeIsRequired": "Please select the theme",
    "endtimeIsRequired": "Please select the endding time",
    "invalidEndtime": "The endding time cannot be less than the current time",
    "publicizeFailed": "Publicizing failed"
  },
  "th": {
    "today": "วันนี้",
    "startPublicize": "เริ่มการเดินทาง",
    "searchTheme": "ค้นหาธีม",
    "confirmTheme": "ยืนยันธีม",
    "selectTime": "โปรดระบุเวลาสิ้นสุด",
    "noThemeTips": "ไม่มีธีมที่ตรงกันโปรดสร้างใน Administration Station",
    "noMatchTheme": "ไม่พบธีมที่ตรงกัน",
    "searchRoute": "ค้นหาเส้นทาง",
    "noRouteTips": "ไม่มีเส้นทางที่ตรงกันโปรดสร้างบน Administration Station",
    "noMatchRoute": "ไม่พบเส้นทางที่ตรงกัน",
    "confirmRoute": "ยืนยันเส้นทาง",
    "routeIsRequired": "กรุณาเลือกเส้นทาง",
    "themeIsRequired": "โปรดเลือกธีม",
    "endtimeIsRequired": "โปรดเลือกเวลาสิ้นสุด",
    "invalidEndtime": "เวลาสิ้นสุดต้องไม่น้อยกว่าเวลาปัจจุบัน",
    "publicizeFailed": "การเผยแพร่ล้มเหลว"
  }
}
</i18n>

<style lang="scss" scoped>
.publicize-pending {
  display: flex;
  flex-direction: column;
  .setting {
    flex: 1;
    margin-top: 5vh;
    .time-seperator {
      margin: 0 #{40 / $r}rem;
    }
    .placeholder {
      text-align: center;
      color: #9D9D9D;
      strong {
        margin-right: 1.7vw;
      }
    }
  }
}
</style>
