import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home/Home'
import Delivery from '../views/Delivery/Delivery'
import Guide from '../views/Guide/Guide'
import PasswordAuth from '../views/PasswordAuth/PasswordAuth'
import Publicize from '../views/Publicize/Publicize'
import Express from '../views/Express/Express'
import ExpressEntry from '../views/Express/Entry'
import SendExpress from '../views/Express/SendExpress/SendExpress'
import DispatchExpress from '../views/Express/DispatchExpress/DispatchExpress'
import Disinfect from '../views/Disinfect/Disinfect'
import DisinfectTaskList from '../views/Disinfect/TaskList'
import NewDisinfectTask from '../views/Disinfect/NewTask'
import Working from '../views/Working'
import VideoChat from '../views/VideoChat'
import Other from '../views/Other/Other'
import Disconnected from '../views/Disconnected/Disconnected'
import InitPose from '../views/Other/InitPose'
import Training from '../views/Training'
import { logger } from '@/ue4/ping'
import store from '@/store'
import Charge from '../views/Charge/Charge'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/PasswordAuth',
    name: 'PasswordAuth',
    component: PasswordAuth
  },
  {
    path: '/Delivery',
    name: 'Delivery',
    component: Delivery,
    meta: {
      // 要验证管理员密码
      passwordAuth () {
        return store.state.app.appConfig ? store.state.app.appConfig.deliveryEnterPwdFlag == '1' : false
      }
    }
  },
  {
    path: '/Guide',
    name: 'Guide',
    component: Guide,
    meta: {
      // 要验证管理员密码
      passwordAuth () {
        return store.state.app.appConfig ? store.state.app.appConfig.guidePwdFlag == '1' : false
      }
    }
  },
  {
    path: '/Publicize',
    name: 'Publicize',
    component: Publicize,
    meta: {
      // 要验证管理员密码
      passwordAuth: true
    }
  },
  {
    path: '/Express',
    name: 'Express',
    component: Express,
    children: [
      {
        path: '/',
        name: 'ExpressEntry',
        component: ExpressEntry
      },
      {
        path: '/SendExpress',
        name: 'SendExpress',
        component: SendExpress
      },
      {
        path: '/DispatchExpress',
        name: 'DispatchExpress',
        component: DispatchExpress
      }
    ]
  },
  {
    path: '/Disinfect',
    name: 'Disinfect',
    component: Disinfect,
    children: [
      {
        path: '/',
        name: 'Disinfect',
        component: DisinfectTaskList
      },
      {
        path: '/NewDisinfectTask',
        name: 'NewDisinfectTask',
        component: NewDisinfectTask
      }
    ]
  },
  {
    path: '/Working',
    name: 'Working',
    component: Working
  },
  {
    path: '/VideoChat',
    name: 'VideoChat',
    component: VideoChat
  },
  {
    path: '/Training',
    name: 'Training',
    component: Training
  },
  {
    path: '/Other',
    name: 'Other',
    component: Other,
    meta: {
      // 要验证管理员密码
      passwordAuth: true
    }
  },
  {
    path: '/InitPose',
    name: 'InitPose',
    component: InitPose
  },
  {
    path: '/Disconnected',
    name: 'Disconnected',
    component: Disconnected
  },
  {
    path: '/Charge',
    name: 'Charge',
    component: Charge
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  const { passwordAuth } = to.meta
  const authRequired = typeof passwordAuth === 'function' ? passwordAuth() : passwordAuth
  if (authRequired && from.name !== 'PasswordAuth' && !to.query.authed) {
    const log = `重定向：${to.name} => PasswordAuth`
    console.log(log)
    logger(log)
    next({ name: 'PasswordAuth', query: { to: to.name, from: from.name } })
  } else {
    const log = `路由跳转：${to.name}`
    console.log(log)
    logger(log)
    next()
  }
})

export default router
