<template>
  <div class="home-footer">
    <img src="@/assets/images/face-smily.png">
    <div class="footer-info">
      <!-- <div class="weather">
        28.5℃ 晴转多云
      </div> -->
      <div class="greeting">
        {{ greeting }}
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'HomeFooter',
  computed: {
    ...mapState('app', ['appConfig']),
    greeting () {
      if (this.appConfig) return this.appConfig.mainTip
      if (process.env.NODE_ENV !== 'production') {
        return this.$t('homeGreeting')
      }
      return ''
    }
  }
}
</script>

<style lang="scss" scoped>
.home-footer {
  justify-self: flex-end;
  display: flex;
  align-items: center;
  img {
    width: 8.2vw;
    height: 5.6vh;
  }
  .footer-info {
    margin-left: 4.2vw;
    color: #fff;
    .weather {
      font-size: 3rem;
      margin-bottom: .5vh;
    }
    .greeting {
      font-size: 4rem;
    }
  }
}
</style>
