<template>
  <div class="page black-bg delivery-running">
    <Interact v-show="status === DELIVERYING">
      <img :src="appConfig.sceneBackgrounds ? (appConfig.sceneBackgrounds.key2 || faceWorking) : faceWorking">
      <template slot="tips">
        {{ $t('destination') }}
      </template>
      <template slot="title">
        {{ taskTarget }}
      </template>
    </Interact>
    <Password v-if="status === VERIFY"
              @touchstart.native="handleScreenTouch"
              ref="password"
              :max-retry="3"
              :tips="verifyTips"
              :disabled="verifyLoading || !!doorSwitchAction"
              :blocked-tips="$t('maxRetryTips')"
              @on-max-retry="handleMaxRetry"
              @on-complete-input="handleVerify">
      <span class="fs4 forgot-pwd" @click="handleSendPwd">
        <IconLoading v-if="pwdLoading"></IconLoading>
        <template v-else>
          {{ $t('forgotPwd') }}
        </template>
      </span>
    </Password>
    <Interact key="opendoor" v-if="status === OPEN">
      <img :src="appConfig.sceneBackgrounds ? (appConfig.sceneBackgrounds.key2 || faceWorking) : faceWorking">
      <template slot="tips">
        {{ $t('openTips') }}
      </template>
      <template slot="title">
        {{ $t('arrived') }}
      </template>
      <iButton type="outline" slot="footer" @click="handleOpenDoor" :loading="openingLoading">
        {{ $t('openDoor') }}
      </iButton>
    </Interact>
    <Interact v-show="status === PICKING">
      <img :src="appConfig.sceneBackgrounds ? (appConfig.sceneBackgrounds.key3 || faceWink) : faceWink">
      <template slot="tips">
        {{ $t(getPickingTips) }}
      </template>
      <template slot="title">
        {{ $t(getPickingTitle) }}
      </template>
      <iButton type="outline" slot="footer" @click="handleCloseDoor" :loading="pickingLoading">
        {{ $t(getPickingConfirm) }}({{ countdown }}S)
      </iButton>
    </Interact>
  </div>
</template>

<script>
import { mapGetters, mapState, mapMutations } from 'vuex'
import Interact from '@/components/Interact.vue'
import Password from '@/components/Password.vue'
import iButton from '@/components/Button.vue'
import IconLoading from '@/components/IconLoading.vue'
import countdown from '@/mixins/countdown'
import doorsControl from '@/mixins/doorsControl'
import pong from '@/ue4/pong'
import pongTypes from '@/ue4/pong/types'
import ping, { logger } from '@/ue4/ping'
import pingTypes from '@/ue4/ping/types'
import request from '@/utils/request'
import faceWorking from '@/assets/images/face-working.png'
import faceWink from '@/assets/images/face-wink.png'

const DELIVERYING = 1 // 正在送货
const VERIFY = 2 // 验证取货码
const PICKING = 3 // 取货
const OPEN = 4 // 显示开门界面

// 取货关门事件上报错误码
const CLOSE_DOOR_STATUS = {
  SUCCESS: {
    status: 20000,
    message: 'success'
  },
  TIMEOUT: {
    status: 20002,
    message: 'timeout'
  },
  MAX_RETRY: {
    status: 20003,
    message: 'max_retry_limit'
  }
}

export default {
  name: 'DeliveryRunning',
  props: {
    task: {
      type: Object,
      require: true
    }
  },
  mixins: [countdown, doorsControl],
  components: {
    Interact,
    Password,
    iButton,
    IconLoading
  },
  data () {
    return {
      status: null,
      DELIVERYING,
      VERIFY,
      PICKING,
      OPEN,
      arrivedTask: null,
      // 到货180s无人操作屏幕，或者触摸屏幕后180s没完成取货流程就关门走人
      waitingTimer: null,
      verifyLoading: false,
      touchTimes: 0,
      doorCloseEventInterval: null,
      pickingLoading: false,
      closeDoorStatus: CLOSE_DOOR_STATUS.SUCCESS,
      doorSwitchAction: 0,
      COUNTDOWN: 20,
      pwdLoading: false,
      openingLoading: false,
      faceWorking,
      faceWink
    }
  },
  computed: {
    ...mapState('app', ['robotInfo', 'appConfig']),
    ...mapGetters('app', ['hasCab', 'isPatrol']),
    getPickingTips () {
      if (this.hasCab) return 'pickingTips'
      return 'noCabPickingTips'
    },
    getPickingTitle () {
      if (this.hasCab) return 'doorOpened'
      return 'arrived'
    },
    getPickingConfirm () {
      if (this.hasCab) return 'closeDoor'
      return 'pickedUp'
    },
    taskTarget () {
      if (this.task) {
        return this.task.detail
      }
      return '-'
    },
    verifyTips () {
      if (this.verifyLoading) {
        return this.$t('verifying')
      }
      if (this.doorSwitchAction) {
        return this.$t('doorOpening')
      }
      return this.$t('enterVerifyCode')
    }
  },
  methods: {
    ...mapMutations('task', ['setCurrentTask']),
    reset () {
      this.waitingTimer && clearTimeout(this.waitingTimer)
      this.doorCloseEventInterval && clearTimeout(this.doorCloseEventInterval)
      this.touchTimes = 0
      this.status = DELIVERYING
      this.verifyLoading = false
      this.pickingLoading = false
      this.pwdLoading = false
      this.openingLoading = false
      // 模拟到达取货
      process.env.NODE_ENV === 'development' && setTimeout(() => {
        pong.$emit(pongTypes.DELIVERY_ARRIVED, {
          delay: '0',
          opId: '40bdc635-4e98-440f-9a50-0d05760b111f',
          drawer: '1',
          timeout: 60
        })
        setTimeout(() => {
          /* eslint-disable */
          this.setCurrentTask({
            "id":"1407965917317877762",
            "robotType":"ginger-lite",
            "robotCode":"GINLITXR-1LXXXXXXXXXGNL10S2111000003",
            "robotName":"0003",
            "taskType":0,
            "taskName":"出发点->仓库->充电拐角",
            "status":1,
            "taskUuid":"1e3bfb8393ba4c23a812a235b5bf8b65",
            "errorCode":"",
            "errorMessage":null,
            "errorDetail":null,
            "bizType":0,
            "taskInstanceId":"1e3bfb8393ba4c23a812a235b5bf8b65"
          })
          /* eslint-enable */
        }, 3000)
      }, 5000)
    },
    // 无需取货码直接开门
    handleOpenDoor () {
      this.openingLoading = true
      this.openDoor()
    },
    // 用户输入完取货码，开始验证
    handleVerify (pwd) {
      this.verifyLoading = true
      request.post('/crss-robot/devices/robotapi/v1/device/drawer/verifyPassword', {
        drawer: this.arrivedTask.drawer,
        password: pwd
      }).then(this.openDoor)
        .catch(({ data }) => {
          if (data && data.code == 103000) {
            return this.$refs.password.fail()
          }
          this.$message((data && data.message) ? data.message : 'network error')
          this.$refs.password.clear()
        }).finally(() => {
          this.verifyLoading = false
        })
    },
    openDoor () {
      // 通过
      this.waitingTimer && clearTimeout(this.waitingTimer)
      console.log('清除超时计时：', this.waitingTimer)
      logger('清除超时计时：', this.waitingTimer)
      // 安保调用出货
      if (this.isPatrol) {
        const doors = this.arrivedTask.drawer.split(',')
        ping(pingTypes.DOOR_SWITCH, {
          doors,
          status: Array.from(Array(doors.length).fill(1))
        })
      } else {
        // gingerlite箱体调用开门
        this.doorSwitchAction = 1
        this.changeDoorsStatus(this.arrivedTask.drawer, this.doorSwitchAction)
      }
      // related: http://jira.cloudminds.com/browse/GINLT-2347?filter=19616
      // 开门标记，在没有清除此标记的情况下再来openDoor事件则视为重复，显示取货状态
      // 需要在关门时、任务开始时将标记清除
      localStorage.setItem('openDoorEvent', this.arrivedTask.drawer)
    },
    handleArrived (data) {
      this.arrivedTask = data
      // 如果到达的notifyOpenDoor消息就是上次缓存的closeDoorEvent
      // 默认用户已经取货，直接上报关门消息
      // 如果是上次缓存的openDoorEvent，则忽略该消息
      const openDoorEvent = localStorage.getItem('openDoorEvent')
      logger('开门事件标记', openDoorEvent)
      if (openDoorEvent && openDoorEvent == this.arrivedTask.drawer) {
        logger('已开过门', this.arrivedTask)
        this.status = PICKING
        return
      }
      const closeDoorEvent = JSON.parse(localStorage.getItem('closeDoorEvent'))
      logger('关门事件标记 ', closeDoorEvent)
      if (closeDoorEvent && closeDoorEvent.drawer == this.arrivedTask.drawer) {
        logger('关门事件重发 ', closeDoorEvent)
        this.status = PICKING
        this.closeDoorStatus = closeDoorEvent.closeDoorStatus
        this.pickingLoading = true
        this.startDoorCloseEventInterval()
        return
      }
      // 有箱体
      if (this.hasCab) {
        this.touchTimes = 0
        console.log('到达开启超时计时')
        logger('到达开启超时计时')
        this.startWaitingTimer()
        logger('是否需要验证密码：', this.appConfig.getDeliveryPwdFlag)
        this.status = this.appConfig.getDeliveryPwdFlag == 0 ? OPEN : VERIFY
      } else {
        logger('无箱体版取货逻辑')
        // 无箱体的直接提示取货，如果不是主动点击确认都认为是取货超时
        this.handleDoorOpened()
      }
    },
    startWaitingTimer () {
      this.waitingTimer && clearTimeout(this.waitingTimer)
      const log = `取货将在${new Date(Date.now() + 180 * 1000).toLocaleTimeString()}超时`
      console.log(log)
      logger(log)
      this.waitingTimer = setTimeout(() => {
        // 等待超时，走人
        this.closeDoorStatus = CLOSE_DOOR_STATUS.TIMEOUT
        this.startDoorCloseEventInterval()
      }, 180 * 1000)
    },
    handleScreenTouch () {
      // 在打电话后用户触摸屏幕开始计时180s，未完成输取货码就走
      if (this.touchTimes === 0) {
        console.log('用户触摸屏幕，重启超时计时')
        this.startWaitingTimer()
      }
      this.touchTimes++
    },
    handleDoorOpened () {
      this.openingLoading = false
      this.status = PICKING
      // 非箱体版沿用云端设置的超时值
      if (!this.hasCab) {
        this.COUNTDOWN = parseInt(this.arrivedTask.timeout) || 70
      }
      this.startCountdown(this.handleCloseDoor)
    },
    handleCloseDoor (event) {
      this.countdownTimer && clearTimeout(this.countdownTimer)
      this.pickingLoading = true
      // 有箱体的，需要关闭箱体并且上报
      if (this.hasCab) {
        this.doorSwitchAction = 0
        this.changeDoorsStatus(this.arrivedTask.drawer, this.doorSwitchAction)
      } else {
        // 有event表示是用户点击的，否则是倒计时自动确认的
        // 无箱体的或是patrol，点击确认即表示成功取货
        this.closeDoorStatus = CLOSE_DOOR_STATUS[event ? 'SUCCESS' : 'TIMEOUT']
        this.startDoorCloseEventInterval()
      }
      localStorage.removeItem('openDoorEvent')
      // window.ue && pong.$emit(pongTypes.DOOR_CLOSED)
    },
    // 取出货物，门关好后上报个关门事件，以标识任务结束
    handleAfterDoorClose () {
      if (this.status === PICKING) {
        this.closeDoorStatus = CLOSE_DOOR_STATUS.SUCCESS
        this.startDoorCloseEventInterval()
      }
    },
    pingReport () {
      this.doorCloseEventInterval && clearTimeout(this.doorCloseEventInterval)
      if (!localStorage.getItem('closeDoorEvent')) {
        return
      }
      ping(pingTypes.DOOR_CLOSED_EVENT, {
        opId: this.arrivedTask.opId,
        drawer: this.arrivedTask.drawer,
        ...this.closeDoorStatus
      })
      this.doorCloseEventInterval = setTimeout(this.pingReport, 3000)
    },
    startDoorCloseEventInterval () {
      // 存起来防止ue4重启，在重启后检察到该字段如果有notifyOpenDoor消息
      // 并且是同一个drawer则默认成功，继续发送事件
      const event = JSON.stringify({
        drawer: this.arrivedTask.drawer,
        closeDoorStatus: this.closeDoorStatus
      })
      localStorage.setItem('closeDoorEvent', event)
      logger('添加关门事件标记 ', event)
      // 每三秒发一次关门事件上报，直到成功
      this.doorCloseEventInterval && clearTimeout(this.doorCloseEventInterval)
      this.doorCloseEventInterval = setTimeout(this.pingReport, 3000)
    },
    // 确认关门信息上报成功后，云端会继续下发notifyTask => running的指令
    handleDoorCloseEvent (data) {
      // 清除事件
      localStorage.removeItem('closeDoorEvent')
      logger('清除关门事件标记')
      this.reset()
    },
    // 超过重试次数，直接上报关门事件，走人
    handleMaxRetry () {
      this.closeDoorStatus = CLOSE_DOOR_STATUS.MAX_RETRY
      this.startDoorCloseEventInterval()
      logger('超过重试密码次数，上报关门继续下一点位')
    },
    handleDoorSwitchResp ({ code }) {
      // this.loading = false
      // 开关失败，现在失败只有超时一种，即使超时也不影响流程走完
      if (code !== 0) {
        this.$message(this.$t('doorSwitchTimeout'))
      }
      // 1、如果之前调用过开门/出货，返回的消息表示开门完成
      // 2、如果是patrol，返回这条消息一定是出货成功。patrol不需要用户点击
      //    也可出货，因此这里多判断一下
      if (this.isPatrol || this.doorSwitchAction) {
        return this.handleDoorOpened()
      }
      // 取货时关门完成
      if (this.status === PICKING) {
        this.closeDoorStatus = CLOSE_DOOR_STATUS.SUCCESS
        this.startDoorCloseEventInterval()
      }
    },
    handleSendPwd () {
      if (this.pwdLoading) return
      this.pwdLoading = true
      request
        .post('/crss/task/v1/receptionTask/forgotPwd', {
          robotCode: this.robotInfo.robotId,
          drawer: this.arrivedTask.drawer
        })
        .then(() => {
          this.$message(this.$t('pwdResendTips'))
        })
        .catch(({ data }) => {
          this.$notice(`${data ? data.message : 'unknow'}`, {
            actions: [{
              onClick: 'close',
              emitSeconds: 5,
              label: this.$t('gotIt')
            }]
          })
        })
        .finally(() => {
          this.pwdLoading = false
        })
    }
  },
  mounted () {
    pong.$on(pongTypes.DELIVERY_ARRIVED, this.handleArrived)
    pong.$on(pongTypes.DOOR_SWITCH_RESP, this.handleDoorSwitchResp)
    pong.$on(pongTypes.DOOR_CLOSED_EVENT_RESP, this.handleDoorCloseEvent)
    // 不需要重置状态，因为外层使用的是v-if判断，页面切换时状态就会重置
    // 只有在mock时有用，下次更新将这些代码优化
    this.reset()
  },
  beforeDestroy () {
    this.waitingTimer && clearTimeout(this.waitingTimer)
    this.doorCloseEventInterval && clearTimeout(this.doorCloseEventInterval)
    pong.$off(pongTypes.DELIVERY_ARRIVED, this.handleArrived)
    pong.$off(pongTypes.DOOR_SWITCH_RESP, this.handleDoorSwitchResp)
    pong.$off(pongTypes.DOOR_CLOSED_EVENT_RESP, this.handleDoorCloseEvent)
  }
}
</script>

<style lang="scss" scoped>
.forgot-pwd {
  margin-top: 4rem;
}
</style>

<i18n>
{
  "zh-CN": {
    "enterVerifyCode": "输入取货码",
    "pickingTips": "请取出物品，记得关门哟~",
    "noCabPickingTips": "请取走物品，记得点击确认哟~",
    "arrived": "送货到达",
    "pickedUp": "已取货",
    "verifying": "正在校验",
    "maxRetryTips": "重试次数过多，请与工作人员联系",
    "forgotPwd": "忘记密码？",
    "pwdResendTips": "密码已下发，请注意查收",
    "openTips": "请点击下方开门按钮~"
  },
  "en": {
    "enterVerifyCode": "Enter pickup code",
    "pickingTips": "Please take out your item, and close the door",
    "noCabPickingTips": "Please take out your item and click Confirm",
    "arrived": "Arrived",
    "pickedUp": "Item picked up",
    "verifying": "Verifying",
    "maxRetryTips": "Too many retries. Please contact staff",
    "forgotPwd": "Forgot Password?",
    "pwdResendTips": "Success! Please note to check",
    "openTips": "Click to open the door"
  },
  "th": {
    "enterVerifyCode": "ป้อนรหัสรับสินค้า",
    "pickingTips": "กรุณานำสิ่งของของคุณออกและปิดประตู",
    "noCabPickingTips": "โปรดนำรายการของคุณออกแล้วคลิกยืนยัน",
    "arrived": "มาถึงแล้ว",
    "pickedUp": "รับรายการแล้ว",
    "verifying": "กำลังตรวจสอบ",
    "maxRetryTips": "ลองซ้ำมากเกินไป กรุณาติดต่อเจ้าหน้าที่",
    "forgotPwd": "ลืมรหัสผ่าน?",
    "pwdResendTips": "สำเร็จ! โปรดเตรียมตัวรับสาย",
    "openTips": "กรุณาคลิกที่ปุ่มด้านล่างเพื่อเปิดประตู"
  }
}
</i18n>
