import Vue from 'vue'
import Toast from './Toast'

const ToastConstructor = Vue.extend(Toast)

const toast = new ToastConstructor({
  el: document.createElement('div'),
  data () {
    return {
      content: 'toast',
      icon: null,
      showToast: false,
      closeTimeout: null
    }
  },
  methods: {
    show (content, opt = {}) {
      this.content = content
      if (opt.duration) {
        this.closeTimeout && clearTimeout(this.closeTimeout)
        this.closeTimeout = setTimeout(this.hide, opt.duration)
      }
      this.theme = opt.theme
      this.icon = opt.icon
      this.showToast = true
    },
    hide () {
      this.closeTimeout && clearTimeout(this.closeTimeout)
      this.showToast = false
    }
  },
  mounted () {
    document.body.append(this.$el)
  }
})

export default {
  show: toast.show.bind(toast),
  hide: toast.hide
}
