<template>
  <transition name="notice">
    <div class="notice" v-if="show" :style="{ top: `calc(${top}px + ${index}vh + ${offsetTop}vh)` }">
      <div class="content" v-html="content"></div>
      <div v-if="actions" class="footer flex-default-center">
        <iButton v-for="(action, index) in actions"
                :key="index"
                @click="handleActionClick(action.onClick)"
                :type="action.type || 'default'"
                :emit-seconds="action.emitSeconds">
          {{ action.label }}
        </iButton>
      </div>
    </div>
  </transition>
</template>

<script>
import iButton from '@/components/Button'

export default {
  name: 'Notice',
  components: {
    iButton
  },
  methods: {
    handleActionClick (action) {
      if (action === 'close') {
        return this.hide()
      }
      action()
    }
  }
}
</script>

<style lang="scss" scoped>
.notice-enter-active,
.notice-leave-active {
  opacity: 1;
  transition: $base-transition;
}

.notice-enter,
.notice-leave-to {
  opacity: 0;
}
.notice {
  position: fixed;
  left: 12.5vw;
  background-color: #fff;
  width: 75vw;
  border-radius: 2.5rem;
  padding: 2.6vh;
  overflow: hidden;
  // box-shadow: 0 0 4rem rgba(0, 0, 0, .5);
  z-index: $notice-z-index;
  .content {
    font-size: 4rem;
    color: #000;
    text-align: center;
  }
  .footer {
    margin-top: 2.6vh;
    justify-content: center;
    button {
      margin-right: 6.66rem;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}
</style>
