<template>
  <div class="search-result">
    <div class="search-result-row" v-if="data.length">
      <div class="row-item flex-between-center"
          v-for="(row, i) in data"
          :key="i"
          @click="handleClick(row.item)">
        <div class="location-name" v-html="row.html"></div>
        <div class="location-code">{{ row.code }}</div>
      </div>
    </div>
    <div v-else class="not-found">
      <img src="@/assets/images/icon-not-found.png">
      <div>{{ $t('notFoundTips') }}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SearchResult',
  props: {
    data: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    handleClick (item) {
      this.$parent.handleSelect(item)
    }
  }
}
</script>

<style lang="scss" scoped>
.search-result {
  .search-result-row {
    background-color: #fff;
    border-radius: #{10/$r}rem;
    padding: 2.6vh 4.2vw;
    .row-item {
      padding: 1.1vh 0;
      border-bottom: 1px solid #979797;
      font-size: 4rem;
      color: #000;
      .location-code {
        color: #8a8a8a;
        font-size: #{40/$r}rem;
      }
    }
  }
  .not-found {
    text-align: center;
    font-size: 4rem;
    img {
      margin: 14.7vh auto 3.5vh auto;
      width: 21.9vw;
      height: 10.4vh;
    }
  }
}
</style>

<i18n>
{
  "zh-CN": {
    "notFoundTips": "抱歉，没有找到你搜索的内容哦~"
  },
  "en": {
    "notFoundTips": "Sorry, there is no content you are searching for"
  },
  "th": {
    "notFoundTips": "ขออภัยไม่มีเนื้อหาที่คุณกำลังค้นหา"
  }
}
</i18n>
