<template>
  <div class="page page-other">
    <Map v-show="showMap"
        @on-select="handleSelect"
        :onBack="toggleMap"
        :btn-text="$t('select')"
        :sorter="mapSorter"
        :type="pointType">
    </Map>
    <div v-show="!showMap" class="page">
      <Bar :on-back="handleBack">
        {{ $t('Other') }}
      </Bar>
      <div class="settings">
        <SettingRow>
          <template slot="title">{{ $t('manual') }}</template>
          <template slot="tips">{{ $t('manualTips') }}</template>
          <iSwitch @on-change="handleChangeEmergencyStopStatus" :true-value="1" :false-value="0"></iSwitch>
        </SettingRow>
        <SettingRow v-if="hasCab">
          <template slot="title">{{ $t('door') }}</template>
          <template slot="tips">{{ $t('doorTips') }}</template>
          <iSwitch @on-change="handleChangeDoorStatus" :true-value="1" :loading="doorLoading" :false-value="0"></iSwitch>
        </SettingRow>
        <!-- <SettingRow @click.native="handleInitPose">
          <template slot="title">{{ $t('initPose') }}</template>
          <template slot="tips">{{ $t('initPoseTips') }}</template>
          <span class="fs4">
            >
          </span>
        </SettingRow> -->
        <SettingRow>
          <template slot="title">{{ $t('selectReturnPoint') }}</template>
          <template slot="tips">{{ $t('selectReturnPointTips') }}</template>
          <div v-if="returnPoint" class="fs4 start-point" @click="toggleMap">
            <div class="start-point-info">{{ returnPoint.floor }} - {{ returnPoint.name }}</div>
            <div class="mt-10 small-text">{{ $t('clickEdit') }}</div>
          </div>
          <iButton v-else @click="toggleMap">{{ $t('select') }}</iButton>
        </SettingRow>
        <SettingRow>
          <template slot="title">{{ $t('resetLocation') }}</template>
          <template slot="tips">{{ $t('resetLocationTips') }}</template>
          <iButton @click="handleResetLocation">{{ $t('setting') }}</iButton>
        </SettingRow>
        <SettingRow>
          <template slot="title">{{ $t('killTask') }}</template>
          <template slot="tips">{{ $t('killTaskTips') }}</template>
          <iButton v-if="showKillTaskBtn" @click="handleKillTask">{{ $t('terminate') }}</iButton>
          <span class="fs4" v-else>
            {{ $t('noCurrentTask') }}
          </span>
        </SettingRow>
        <SettingRow>
          <template slot="title">{{ $t('versionInfo') }}</template>
          <template slot="tips">{{ $t('versionInfoTips') }}</template>
          <div class="version-info">
            <div class="fs4">
              CRSS: {{ crssVersion}}
            </div>
            <div class="mt-10">{{ releaseInfo }}</div>
          </div>
        </SettingRow>
        <SettingRow>
          <template slot="title">{{ $t('lang') }}</template>
          <!-- <template slot="tips">{{ $t('langTips') }}</template> -->
          <span class="fs4">{{ $i18n.locale }}</span>
        </SettingRow>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Bar from '@/components/Bar.vue'
import Map from '@/components/Map'
import iSwitch from '@/components/Switch.vue'
import iButton from '@/components/Button.vue'
import SettingRow from './SettingRow.vue'
import pong from '@/ue4/pong'
import pongTypes from '@/ue4/pong/types'
import ping from '@/ue4/ping'
import pingTypes from '@/ue4/ping/types'
import CONST from '@/const'
import backHandle from '@/mixins/backHandle'

export default {
  name: 'Other',
  components: {
    Bar,
    Map,
    iSwitch,
    SettingRow,
    iButton
  },
  mixins: [backHandle],
  data () {
    return {
      currentTask: null,
      showMap: false,
      localData: {},
      crssVersion: '',
      releaseInfo: '',
      softEmergencyStop: 0,
      doorLoading: false,
      pointType: CONST.INTEREST
    }
  },
  computed: {
    mapSorter () {
      // 定位校准时按type 1, 0, 2排序
      if (this.pointType !== CONST.INTEREST) {
        return (a, b) => {
          // a在前面
          if (a.type == 1 || (a.type == 0 && b.type == 2)) {
            return -1
          }
          // b在前面
          if (b.type == 1 || (b.type == 0 && a.type == 2)) {
            return 1
          }
          return 0
        }
      }
      return undefined
    },
    showKillTaskBtn () {
      return this.currentTask && this.currentTask.status === CONST.RUNNING
    },
    returnPoint () {
      return this.localData.returnPoint
    },
    ...mapGetters('app', ['hasCab'])
  },
  methods: {
    handleBack () {
      if (this.softEmergencyStop) {
        return this.$message(this.$t('disabledSoftEmergencyStopBeforeLeave'))
      }
      this.back()
    },
    // 取消任务后会下发一个taskNotify的消息，在那里刷新
    handleKillTask () {
      ping(pingTypes.CANCEL_TASK, { taskInstanceId: this.currentTask.taskInstanceId })
    },
    handleChangeEmergencyStopStatus (status) {
      this.softEmergencyStop = status
      ping(pingTypes.TOGGLE_EMERGENCY_STOP, { status })
    },
    handleChangeDoorStatus (status) {
      this.doorLoading = true
      ping(pingTypes.DOOR_SWITCH, { doors: ['door_1', 'door_2'], status: [status, status] })
    },
    handleDoorSwitchResp () {
      this.doorLoading = false
    },
    handleQueryTaskResp (data) {
      this.currentTask = data
    },
    handleCancelTaskResp () {
      ping(pingTypes.QUERY_TASK)
    },
    handleSelect (location) {
      // 校准定位
      if (this.pointType !== CONST.INTEREST) {
        ping(pingTypes.RESET_LOCATION, location)
        // console.log('reset location: ', location)
      } else {
        // 设置返回点
        this.$set(this.localData, 'returnPoint', location)
        ping(pingTypes.WRITE_LOCAL, this.localData)
      }
      this.toggleMap()
    },
    toggleMap () {
      if (this.showMap) this.pointType = CONST.INTEREST
      this.showMap = !this.showMap
    },
    handleReadLocalResp (data) {
      if (data.code === 0) {
        if (data.data) {
          this.localData = data.data
        }
      } else {
        this.$message(this.$t('readLocalError'))
      }
    },
    handleWriteLocalResp (data) {
      if (data.code !== 0) {
        this.$message(this.$t('writeLocalError'))
      }
    },
    httpGet (url) {
      return new Promise((resolve, reject) => {
        const xhr = new XMLHttpRequest()
        xhr.onreadystatechange = () => {
          if (xhr.readyState == 4) {
            if (xhr.status == 200) {
              resolve(xhr.responseText)
            } else {
              reject(new Error())
            }
          }
        }
        xhr.open('GET', `${process.env.NODE_ENV === 'development' ? '/231' : ''}${url}`)
        xhr.send()
      })
    },
    getVersionInfo () {
      this.httpGet('/common/version.json').then((data) => {
        this.crssVersion = JSON.parse(data).crss_version
      }).catch(() => {
        console.log('get verion info error')
      })
      this.httpGet('/rcu-gingerlite/release.txt').then((data) => {
        this.releaseInfo = data
      }).catch(() => {
        console.log('get release.txt error')
      })
    },
    handleInitPose () {
      this.$router.push({ name: 'InitPose' })
    },
    handleResetLocation () {
      this.pointType = [CONST.INTEREST, CONST.CHARGER, CONST.LIFT]
      this.toggleMap()
    }
  },
  mounted () {
    pong.$on(pongTypes.DOOR_SWITCH_RESP, this.handleDoorSwitchResp)
    pong.$on(pongTypes.QUERY_TASK_RESP, this.handleQueryTaskResp)
    pong.$on(pongTypes.CANCEL_TASK_RESP, this.handleCancelTaskResp)
    pong.$on(pongTypes.READ_LOCAL_RESP, this.handleReadLocalResp)
    pong.$on(pongTypes.WRITE_LOCAL_RESP, this.handleWriteLocalResp)
    ping(pingTypes.QUERY_TASK)
    ping(pingTypes.READ_LOCAL)
    this.getVersionInfo()
  },
  beforeDestroy () {
    pong.$off(pongTypes.DOOR_SWITCH_RESP, this.handleDoorSwitchResp)
    pong.$off(pongTypes.QUERY_TASK_RESP, this.handleQueryTaskResp)
    pong.$off(pongTypes.CANCEL_TASK_RESP, this.handleCancelTaskResp)
    pong.$off(pongTypes.READ_LOCAL_RESP, this.handleReadLocalResp)
    pong.$off(pongTypes.WRITE_LOCAL_RESP, this.handleWriteLocalResp)
  }
}
</script>

<style lang="scss" scoped>
.page-other {
  .page {
    display: flex;
    flex-direction: column;
  }
  .settings {
    flex: 1;
    overflow: auto;
    margin-top: 5vh;
    padding: 0 4.5vw;
    .start-point {
      text-align: center;
      overflow: hidden;
      max-width: 40vw;
      .start-point-info {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    .small-text {
      font-size: 3rem;
    }
    .version-info {
      text-align: right;
      .mt-10 {
        font-size: 2rem;
      }
    }
  }
}
</style>

