<template>
  <transition>
    <div id="qa" v-show="show">
      <div class="resource-loading" v-if="loading">
        <IconLoading></IconLoading>
        <div class="fs4">{{ $t('loadingResource') }}</div>
        <iButton @click="handleClose"
                size="small"
                type="outline">
          {{ $t('close') }}
        </iButton>
      </div>
      <template v-else>
        <iButton @click="handleClose"
                :emit-seconds="20"
                size="small"
                type="outline"
                class="close-button">
          {{ $t('close') }}
        </iButton>
        <img v-if="payload" class="display-resource" :src="`${baseUrl}${payload.img_url}`">
        <span v-else class="fs4">{{ $t('emptyResource') }}</span>
      </template>
    </div>
  </transition>
</template>

