export default class ClickDetect {
  // x, y用数组表示范围
  constructor (x, y, onFulfill, clickTimes = 7) {
    this.x = x.map(e => e / window.devicePixelRatio)
    this.y = y.map(e => e / window.devicePixelRatio)
    this.lastClick = null
    this.clickCount = 0
    this.clickTimes = clickTimes
    this.onFulfill = onFulfill
  }

  detect (event) {
    const timeStamp = event.timeStamp
    const pageX = event.touches[0].pageX
    const pageY = event.touches[0].pageY
    if (this.lastClick === null) {
      this.lastClick = timeStamp
      return
    }
    // 区域内连续快速点击n次以上
    if (timeStamp - this.lastClick < 300 &&
      pageX > this.x[0] && pageX < this.x[1] &&
      pageY > this.y[0] && pageY < this.y[1]) {
      this.clickCount++
      this.lastClick = timeStamp
      if (this.clickCount === this.clickTimes) {
        this.onFulfill()
        this.clickCount = 0
        this.lastClick = null
      }
      return
    }
    this.clickCount = 0
    this.lastClick = null
  }
}
