<template>
  <div class="page page-dispatch-running">
    <Interact class="interact" v-if="isRunning" key="dispatching">
      <img :src="appConfig.sceneBackgrounds ? (appConfig.sceneBackgrounds.key2 || faceWorking) : faceWorking">
      <template slot="tips">
        {{ $t('dispatching') }}
      </template>
      <template slot="title">
        {{ task.taskDesc }}
      </template>
    </Interact>
    <Interact class="interact" v-else>
      <img :src="appConfig.sceneBackgrounds ? (appConfig.sceneBackgrounds.key3 || faceWink) : faceWink">
      <template slot="tips">
        {{ $t('arrived') }}{{ task.nextTaskDesc ? `${$t('next')}${task.nextTaskDesc}` : '' }}
      </template>
      <template slot="title">
        {{ task.tfaceWinkaskDesc }}
      </template>
      <iButton type="outline"
              slot="footer"
              :loading="loading"
              :emit-seconds="180"
              @click="handleClick">
        {{ $t('finished') }}
      </iButton>
    </Interact>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import Interact from '@/components/Interact.vue'
import iButton from '@/components/Button.vue'
import ping from '@/ue4/ping'
import pingTypes from '@/ue4/ping/types'
import faceWorking from '@/assets/images/face-working.png'
import faceWink from '@/assets/images/face-wink.png'

export default {
  props: {
    task: {
      type: Object
    }
  },
  components: {
    ...mapState('app', ['appConfig']),
    Interact,
    iButton
  },
  data () {
    return {
      loading: false,
      faceWorking,
      faceWink
    }
  },
  computed: {
    isRunning () {
      return this.task && this.task.status === 1
    }
  },
  watch: {
    isRunning () {
      this.loading = false
    }
  },
  methods: {
    handleClick () {
      this.loading = true
      ping(pingTypes.PICKING_CONFIRM)
    }
  }
}
</script>

<style lang="scss" scoped>
.page-dispatch-running {
  overflow: hidden;
  background-color: #000;
  .interact {
    margin-top: -5vh;
  }
}
</style>

<i18n>
{
  "zh-CN": {
    "dispatching": "正在前往",
    "arrived": "已到达",
    "next": "，下一站",
    "finished": "完成"
  },
  "en": {
    "dispatching": "Going",
    "arrived": "Arrived",
    "next": ", Next stop",
    "finished": "Finished"
  },
  "th": {
    "dispatching": "กำลังเดินทาง",
    "arrived": "ถึงที่หมาย",
    "next": "，กำลังเดินทางไปที่หมายถัดไป",
    "finished": "การทำงานเสร็จสมบูรณ์"
  }
}
</i18n>
