<template>
  <div class="page guide-pending">
    <Map v-if="showMap" @on-select="handleSelect" :btn-text="$t('takeMeThere')" :type="pointType"></Map>
    <Interact v-else>
      <template slot="tips">
        {{ $t('ready') }}
      </template>
      <template slot="title">
        {{ getTarget }}
      </template>
      <template slot="footer">
        <IconLoading v-if="loading"></IconLoading>
        <template v-else>
          <iButton class="start-btn" @click="handleStart">
            {{ $t('start') }}
          </iButton>
          <iButton type="ghost" key="cancel" @click="handleCancel">
            {{ $t('cancel') }}
          </iButton>
        </template>
      </template>
    </Interact>
  </div>
</template>

<script>
import Map from '@/components/Map'
import Interact from '@/components/Interact.vue'
import iButton from '@/components/Button.vue'
import IconLoading from '@/components/IconLoading'
import startTask from '@/mixins/startTask'
import POINT_TYPE from '@/const'

export default {
  name: 'GuidePending',
  mixins: [startTask],
  components: {
    Map,
    Interact,
    IconLoading,
    iButton
  },
  data () {
    return {
      taskType: 'guide',
      showMap: true,
      pointType: POINT_TYPE.INTEREST
    }
  },
  methods: {
    handleSelect (location) {
      this.tasks.push({
        index: this.tasks.length + 1,
        point: {
          x: location.x,
          y: location.y,
          yaw: location.yaw
        },
        poi: location.name,
        mapId: location.mapId,
        floor: location.floor
      })
      this.showMap = false
    }
  }
}
</script>

<style lang="scss" scoped>
.guide-pending {
  .start-btn {
    margin-right: 6.7vw;
  }
}
</style>
