<template>
  <div class="page-password page" :class="{ 'page-password--blocked': blocked }">
    <div class="tips">{{ getTips }}</div>
    <div class="input-mask" :class="{ 'wrong-input-animation': aniamtionClass }" @animationend="toggleAnimation">
      <div class="input-mask-item"
          v-for="i in length"
          :key="i"
          :class="{ fill: i <= inputLength }">
      </div>
    </div>
    <NumberKeyboard class="keyboard"
                    @on-click="handleClick"
                    :optional-key="optionalKey.key"
                    :disabled="disabled">
    </NumberKeyboard>
    <slot v-if="!blocked"></slot>
  </div>
</template>

<script>
import NumberKeyboard from './NumberKeyboard.vue'

export default {
  name: 'Password',
  props: {
    tips: {
      type: String,
      default () {
        return this.$t('tips')
      }
    },
    blockedTips: {
      type: String,
      default () {
        return this.$t('blocked')
      }
    },
    failTips: {
      type: String,
      default () {
        return this.$t('failTips')
      }
    },
    // 可以是数字，也可以是异步请求
    validator: {
      type: [String, Function]
    },
    disabled: {
      type: Boolean,
      default: false
    },
    // 0表示无限制
    maxRetry: {
      type: Number,
      default: 5
    },
    length: {
      type: Number,
      default: 4
    },
    // 连续错误时锁定5分钟
    blockedSeconds: {
      type: Number,
      default: 5 * 60
    },
    optionalKey: {
      type: Object,
      default: () => ({
        key: 'delete',
        action: (value) => value.pop()
      })
    }
  },
  components: {
    NumberKeyboard
  },
  data () {
    return {
      value: [],
      aniamtionClass: false,
      retry: 0,
      timer: null,
      showFailTips: false
    }
  },
  computed: {
    inputLength () {
      return this.value.length
    },
    // 超过重试次数
    blocked () {
      return this.retry === 0
    },
    getTips () {
      if (this.blocked) {
        return this.blockedTips
      }
      if (this.showFailTips) {
        return this.failTips
      }
      return this.tips
    }
  },
  methods: {
    handleClick (key) {
      if (this.blocked || this.disabled) {
        return
      }
      if (key === this.optionalKey.key) {
        this.optionalKey.action(this.value)
      } else if (this.inputLength < this.length) {
        this.value.push(key)
        // 输入长度满足后开始验证
        if (this.inputLength === this.length) {
          const value = this.value.join('')
          this.$emit('on-complete-input', value)
          this.showFailTips = false
          this.validate(value)
        }
      }
    },
    validate (pwd) {
      // 手动验证，不管
      if (this.validator === undefined) {
        return
      }
      // promise
      if (typeof this.validator === 'function') {
        this.validator(pwd).then(this.pass).catch(this.fail)
        return
      }
      if (pwd === this.validator) {
        this.pass()
        return
      }
      this.fail()
    },
    pass () {
      this.$emit('on-validate-passed')
    },
    fail () {
      this.retry -= 1
      this.showFailTips = true
      if (this.blocked) {
        this.$emit('on-max-retry')
        this.timer && clearTimeout(this.timer)
        this.timer = setTimeout(this.resetRetry, this.blockedSeconds * 1000)
      } else {
        this.toggleAnimation()
      }
      this.clear()
    },
    clear () {
      this.value = []
    },
    resetRetry () {
      this.showFailTips = false
      this.retry = this.maxRetry || Infinity
    },
    toggleAnimation () {
      this.aniamtionClass = !this.aniamtionClass
    }
  },
  mounted () {
    this.resetRetry()
  },
  beforeDestroy () {
    this.timer && clearTimeout(this.timer)
  }
}
</script>

<style lang="scss" scoped>
.page-password {
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
  &.page-password--blocked {
    .input-mask,
    .keyboard {
      transform: translate3d(0, 100vh, 0);
      transition: .5s ease-out;
    }
    .tips {
      transform: translate3d(0, 20vh, 0);
      transition: .2s ease-out;
    }
  }
  .tips {
    margin-top: 23.5vh;
    font-size: 4rem;
  }
  @keyframes shake-it {
    10%, 90% {
      transform: translate3d(-1rem, 0, 0);
    }
    20%, 80% {
      transform: translate3d(2rem, 0, 0);
    }
    30%, 50%, 70% {
      transform: translate3d(-4rem, 0, 0);
    }
    40%, 60% {
      transform: translate3d(4rem, 0, 0);
    }
  }
  .input-mask {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2.1vh auto 5.2vh;
    &.wrong-input-animation {
      animation: shake-it 0.72s cubic-bezier(.36, .07, .19, .97) both;
      transform: translate3d(0, 0, 0);
      backface-visibility: hidden;
    }
    .input-mask-item {
      $v: 40/$r;
      $d: #{$v}rem;
      $b: #{4/$r}rem;
      height: $d;
      width: $d;
      border: $b solid #fff;
      border-radius: 50%;
      margin-right: 4.2vw;
      &:last-child {
        margin-right: 0;
      }
      &.fill {
        background-color: #fff;
      }
    }
  }
  .keyboard {
    width: 78%;
    height: 52%;
  }
}
</style>

<i18n>
{
  "zh-CN": {
    "tips": "请输入密码",
    "blocked": "请5分钟后再尝试",
    "failTips": "密码错误"
  },
  "en": {
    "tips": "Enter password",
    "blocked": "Please try again in five minutes",
    "failTips": "Wrong password"
  },
  "th": {
    "tips": "ใส่รหัสผ่าน",
    "blocked": "ปรดลองอีกครั้งในอีกห้านาที",
    "failTips": "รหัสผ่านผิด"
  }
}
</i18n>
