<template>
  <div class="page page-send-express">
    <Bar>
      {{ $t('sendExpress') }}
    </Bar>
    <PhoneNumber @on-validate-pass="handleValidatePass" v-if="showPhoneNumber"></PhoneNumber>
    <ExpressForm :data="data" v-else></ExpressForm>
  </div>
</template>

<script>
import Bar from '@/components/Bar.vue'
import PhoneNumber from './PhoneNumber.vue'
import ExpressForm from './Form.vue'

export default {
  name: 'SendExpress',
  components: {
    Bar,
    PhoneNumber,
    ExpressForm
  },
  data () {
    return {
      showPhoneNumber: true,
      data: null
    }
  },
  methods: {
    handleValidatePass (data) {
      this.data = data
      this.showPhoneNumber = false
    }
  }
}
</script>

<style lang="scss" scoped>
.page-send-express {
}
</style>
