<template>
  <div class="page page-map">
    <SearchBar class="search-bar" @on-change="handleQueryChange" v-bind="$attrs"></SearchBar>
    <template v-if="queryMapLoading">
      <div v-if="showRetryBtn" class="retry-query-map">
        <div class="fs4">{{ $t('retryTips') }}</div>
        <iButton @click="handleRetry">{{ $t('retry') }}</iButton>
      </div>
      <CuteLoading v-else class="cute-loading">
        {{ $t('loadingMapInfo') }}
      </CuteLoading>
    </template>
    <div v-else class="map">
      <div class="left" v-show="!query">
        <div class="view-switch" @click="handleSwitchView">
          <img src="@/assets/images/icon-apps.png" alt="">
          切换
        </div>
        <FloorSlider :floors="floors"
                    @on-switch="handleSwitchFloor"></FloorSlider>
      </div>
      <LocationList v-show="!query"
                    class="location-list"
                    :floor-info="currentFloorInfo"
                    v-bind="$attrs"
                    :type="type"
                    :view="view"
                    :sorter="sorter"></LocationList>
      <SearchResult v-show="query" class="search-result" :data="searchLocations"></SearchResult>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import iButton from '@/components/Button'
import CuteLoading from '@/components/CuteLoading'
import SearchBar from '../SearchBar'
import FloorSlider from './FloorSlider'
import LocationList from './LocationList'
import SearchResult from './SearchResult'

export default {
  name: 'Map',
  props: {
    type: {
      type: [Number, String, Array]
    },
    sorter: {
      type: Function,
      default: (a, b) => {
        if (a.name > b.name) {
          return 1
        }
        if (a.name < b.name) {
          return -1
        }
        return 0
      }
    }
  },
  components: {
    iButton,
    CuteLoading,
    SearchBar,
    FloorSlider,
    LocationList,
    SearchResult
  },
  data () {
    return {
      query: '',
      currentFloorIndex: 0,
      loading: false,
      queryMapTimeout: null,
      showRetryBtn: false,
      view: 'grid'
    }
  },
  computed: {
    ...mapState('map', ['mapInfo', 'queryMapLoading']),
    floors () {
      if (this.mapInfo) {
        return this.mapInfo.map(e => {
          return {
            floor: e.floor,
            mapId: e.mapId
          }
        })
      }
      return []
    },
    currentFloorInfo () {
      if (this.mapInfo && this.mapInfo.length) {
        return this.mapInfo[this.currentFloorIndex]
      }
      return {}
    },
    searchLocations () {
      if (this.query && this.mapInfo) {
        const matched = []
        for (let i = 0; i < this.mapInfo.length; i++) {
          for (let j = 0; j < this.mapInfo[i].locations.length; j++) {
            const item = this.mapInfo[i].locations[j]
            if (this.type === undefined || item.type == this.type) {
              const startIndex = item.name.indexOf(this.query)
              if (startIndex !== -1) {
                matched.push({
                  html: item.name.split(this.query).join(`<span class="primary-color-text">${this.query}</span>`),
                  code: `${this.mapInfo[i].floor}-${(j + 1).toString().padStart(3, 0)}`,
                  item
                })
              }
            }
          }
        }
        return matched
      }
      return []
    },
    queryMapNoResp () {
      return !this.mapInfo && this.queryMapLoading
    }
  },
  watch: {
    queryMapNoResp (nv) {
      // 正在查询，超过10秒没查到就显示个按钮可以让用户点击重试加载
      if (!nv) {
        this.showRetryBtn = false
        this.queryMapTimeout && clearTimeout(this.queryMapTimeout)
      }
    }
  },
  methods: {
    ...mapActions('map', ['queryMapInfo']),
    handleSwitchView () {
      this.view = this.view === 'list' ? 'grid' : 'list'
    },
    handleSwitchFloor (index) {
      this.currentFloorIndex = index
    },
    handleQueryChange (query) {
      this.query = query.trim()
    },
    handleSelect (item) {
      this.$emit('on-select', item)
      console.log('select', item)
    },
    startQueryMapTimeout () {
      this.queryMapTimeout && clearTimeout(this.queryMapTimeout)
      this.queryMapTimeout = setTimeout(() => {
        this.showRetryBtn = true
      }, 10000)
    },
    handleRetry () {
      this.showRetryBtn = false
      this.startQueryMapTimeout()
      // 再发一次请求
      this.queryMapInfo()
    }
  },
  mounted () {
    this.queryMapInfo()
    if (this.queryMapNoResp) {
      this.startQueryMapTimeout()
    }
  },
  beforeDestroy () {
    this.queryMapTimeout && clearTimeout(this.queryMapTimeout)
  }
}
</script>

<i18n>
{
  "zh-CN": {
    "loadingMapInfo": "加载地图信息",
    "retryTips": "查询地图信息超时"
  },
  "en": {
    "loadingMapInfo": "Loading map information",
    "retryTips": "Querying time out"
  },
  "th": {
    "loadingMapInfo": "กำลังโหลดแผนที่",
    "retryTips": "ดึงข้อมูลไม่สำเร็จ"
  }
}
</i18n>

<style lang="scss" scoped>
.page-map {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  .retry-query-map {
    text-align: center;
    margin-top: 30vh;
    align-self: center;
    .fs4 {
      margin-bottom: 4rem;
    }
  }
  .cute-loading {
    align-self: center;
    margin-top: 30vh;
  }
  .search-bar {
    height: 9.9vh;
    flex: 0 0 9.9vh;
  }
  .map {
    flex: 0 0 90.1vh;
    height: 90.1vh;
    padding: 0 4.2vw;
    display: flex;
    align-items: flex-start;
    .view-switch {
      width: 12vw;
      height: 6.1vh;
      display: flex;
      background-color: $primary-color;
      border-radius: 2rem;
      $fs: 30 / 12;
      font-size: #{$fs}rem;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin-bottom: 1vh;
      transition: $base-transition;
      &:focus,
      &:active {
        background-color: lighten($primary-color, 5)
      }
      img {
        width: 3.3vw;
        height: 3.3vw;
        margin-bottom: .5rem;
      }
    }
    .location-list {
      margin-left: 2.5vw;
      height: 100%;
      flex: 0 0 1;
      overflow: auto;
    }
    .search-result {
      width: 100%;
    }
  }
}
</style>
