<template>
  <div class="simple-map">
    <Bar v-bind="$attrs">{{ $t('back') }} <div class="center">{{ $t('SelectRoom') }}</div></Bar>
    <div class="map-loading" v-if="mapLoading">
      <div v-if="showRetry" class="retry-query-map">
        <div class="fs4">{{ $t('retryTips') }}</div>
        <iButton @click="handleRetry">{{ $t('retry') }}</iButton>
      </div>
      <CuteLoading v-else class="cute-loading">
        {{ $t('loadingMapInfo') }}
      </CuteLoading>
    </div>
    <div v-else class="map-content" >
      <div class="map-floor">
        <div class="floor-item"
            v-for="(item, i) in floors"
            @click="handleSwitchFloor(i)"
            :class="{
              active: i === currentFloorIndex,
              'active-pre': (i + 1) == currentFloorIndex
            }"
            :key="i">
          <div>{{ item.floor | floorDisplay }}</div>
        </div>
      </div>
      <div class="room-list">
        <div class="room-list-wrapper">
          <div class="room-item"
              v-for="(room, i) in locations"
              :key="i"
              @click="handleSelect(room)">
            {{ room.name }}
            <span class="select-count"
                v-if="selection[room.id]">
              {{ selection[room.id].count }}
            </span>
          </div>
          <div v-if="locations.length === 0" class="empty-list-tips">
            <img src="@/assets/images/icon-not-found.png">
            {{ $t('emptyTips') }}
          </div>
        </div>
        <div class="handle-btns">
          <div class="current-count">{{ $t('selected') }} {{ currentCount }}</div>
          <iButton :disabled="!Object.keys(selection).length"
                  class="confirm-btn"
                  size="large"
                  @click="handleConfirm">
            {{ $t('confirmSelect') }}
          </iButton>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import map from '@/mixins/map'
import Bar from '@/components/Bar'
// import POINT_TYPE from '@/const'
import iButton from '@/components/Button'
import CuteLoading from '@/components/CuteLoading'

export default {
  mixins: [map],
  components: {
    CuteLoading,
    iButton,
    Bar
  },
  data () {
    return {
      behind: null,
      mapData: null,
      mapLoading: false,
      showRetry: false,
      selection: {}
    }
  },
  filters: {
    floorDisplay (floor) {
      if (floor >= 0) {
        return `${floor}F`
      }
      return `B${Math.abs(floor)}`
    }
  },
  computed: {
    floors () {
      if (this.mapData) {
        return this.mapData.map(e => {
          return {
            floor: e.floor,
            mapId: e.mapCode
          }
        })
      }
      return []
    },
    floorData () {
      if (this.mapData && this.mapData[this.currentFloorIndex]) {
        return this.mapData[this.currentFloorIndex]
      }
      return null
    },
    locations () {
      if (this.floorData) {
        let points = []
        Object.keys(this.floorData.bizPoints).forEach((category) => {
          points = points.concat(this.floorData.bizPoints[category])
        })
        // 先按sort排序，如果sort相等按名字排序
        return points.sort((a, b) => {
          if (a.sort !== b.sort) {
            return a.sort - b.sort
          }
          if (a.name > b.name) {
            return 1
          }
          if (a.name < b.name) {
            return -1
          }
          return 0
        })
      }
      return []
    },
    currentCount () {
      return Object.keys(this.selection).reduce((pre, current) => {
        return this.selection[current].count + pre
      }, 0)
    },
    isMax () {
      return this.currentCount > 20
    }
  },
  methods: {
    handleSelect (room) {
      const target = this.selection[room.id]
      if (target) {
        // 最多三个，超过清零
        if (target.count === 3) {
          delete this.selection[room.id]
          this.selection = { ...this.selection }
          return
        }
        this.$set(target, 'count', target.count + 1)
        return
      }
      this.$set(this.selection, room.id, {
        ...room,
        count: 1
      })
    },
    // handleCancelSelect (room) {
    //   const target = this.selection[room.id]
    //   if (target) {
    //     if (target.count > 1) {
    //       this.$set(target, 'count', target.count - 1)
    //     } else {
    //       delete this.selection[room.id]
    //       this.selection = { ...this.selection }
    //     }
    //   }
    // },
    clearSelection () {
      this.selection = {}
    },
    handleConfirm () {
      if (this.isMax) return this.$message(this.$t('maxTips'))
      this.$emit('on-confirm', JSON.parse(JSON.stringify(this.selection)))
    },
    getBizMap () {
      this.mapLoading = true
      this.cross_sdk.map.getBizPointList().then(({ data }) => {
        if (data) {
          this.mapData = data
        }
        this.showRetry = false
      }).catch(() => {
        this.showRetry = true
      }).finally(() => {
        this.mapLoading = false
      })
    },
    handleRetry () {
      this.getBizMap()
    }
  },
  mounted () {
    this.getBizMap()
  }
}
</script>

<style lang="scss">
.simple-map {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  background-color: #F1F1F1;
  .bar {
    color: #000;
    padding: 2.5rem 2.1rem;
    .back-btn {
      filter: invert(100%);
      img {
        width: 2vw;
      }
    }
    .bar-right {
      position: relative;
      .center {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%)
      }
    }
  }
  .map-loading {
    text-align: center;
    margin-top: 30vh;
    color: #000;
    .retry-query-map .fs4 {
      margin-bottom: 4rem;
    }
    .cute-loading {
      filter: invert(.2);
    }
  }
  .map-content {
    flex: 1;
    display: flex;
    overflow: hidden;
    background-color: #fff;
    .map-floor {
      flex: 0 0 16.3333vw;
      overflow: auto;
      font-size: 4rem;
      color: #000;
      background-color: #f1f1f1;
      .floor-item {
        div {
          width: 100%;
          height: 9.27vh;
          line-height: 9.27vh;
          text-align: center;
        }
        &.active {
          background-color: #fff;
          border-radius: 3rem 0 0 3rem;
        }
        &.active-pre,
        &.active + .floor-item {
          div {
            border-radius: 0 0 3rem 0;
            background-color: #f1f1f1;
          }
          background-color: #fff;
        }
        &.active + .floor-item div {
          border-radius: 0 3rem 0 0;
        }
      }
    }
    .room-list {
      flex: 1;
      display: flex;
      flex-direction: column;
      overflow: hidden;
      height: 100%;
      .room-list-wrapper {
        flex: 1;
        overflow: auto;
        padding: 4rem 4vw 1.5rem 2.6666vw;
        box-sizing: border-box;
        .empty-list-tips {
          font-size: 4rem;
          color: #000;
          text-align: center;
          img {
            width: 30vw;
            filter: invert(10%);
            display: block;
            margin: 20vh auto 2vh auto;
          }
        }
        .room-item {
          position: relative;
          width: 22.75%;
          height: 7vh;
          background: #f1f1f1;
          color: #000;
          font-size: 4rem;
          border-radius: 1.3333rem;
          margin-right: 3%;
          display: inline-block;
          line-height: 7vh;
          text-align: center;
          &:nth-child(4n) {
            margin-right: 0;
            margin-bottom: 2.5rem;
          }
          .select-count {
            background-color: red;
            display: inline-block;
            padding: 0 1.5rem;
            border-radius: 3rem;
            color: #fff;
            font-size: 2rem;
            position: absolute;
            height: 4rem;
            line-height: 4rem;
            top: 0;
            right: 0;
            transform: translate(50%, -50%);
          }
        }
      }
      .handle-btns {
        flex: 0 0 15rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 4rem;
        width: 100%;
        .current-count {
          flex: 0 0 30%;
          font-size: 4rem;
          color: #000;
        }
        .confirm-btn {
          padding-left: 15rem;
          padding-right: 15rem;
          background: linear-gradient(180deg, #6c83e7, #576cdb);
          border-radius: 1.33333rem;
        }
      }
    }
    .box-position {
      flex: 0 0 12vw;
      font-size: 4rem;
      color: #000;
      border-radius: 1.3333rem 0 0 1.3333rem;
      margin-bottom: auto;
      margin-top: 4vh;
      overflow: hidden;
      .box-item {
        height: 7vh;
        line-height: 7vh;
        width: 100%;
        text-align: center;
        background: #f1f1f1;
        &.active {
          background: linear-gradient(180deg, #6c83e7, #576cdb);
          color: #fff;
        }
      }
    }
  }
}
</style>

<i18n>
{
  "zh-CN": {
    "SelectRoom": "请选择房间",
    "loadingMapInfo": "加载地图信息",
    "retryTips": "查询地图信息超时",
    "maxTips": "最多选择20个点位",
    "selected": "已选：",
    "confirmSelect": "开始放餐"
  },
  "en": {
    "SelectRoom": "Please select room",
    "loadingMapInfo": "Loading map information",
    "retryTips": "Querying time out",
    "maxTips": "Up to 20 rooms",
    "selected": "Selected: ",
    "confirmSelect": "Place Meal"
  },
  "th": {
    "SelectRoom": "Please select room",
    "loadingMapInfo": "กำลังโหลดแผนที่",
    "retryTips": "ดึงข้อมูลไม่สำเร็จ",
    "maxTips": "Up to 20 rooms",
    "selected": "Selected: ",
    "confirmSelect": "Place Meal"
  }
}
</i18n>
