<template>
  <button :class="[
            'button',
            `button--${type}`,
            `button--${size}`,
            { 'button--loading': loading }
          ]"
          :disabled="disabled || loading"
          @click="handleClick"
          v-on="$listeners"
          v-bind="$attrs">
    <div class="flex-default-center">
      <IconLoading v-show="loading" class="icon-loading"></IconLoading>
      <slot></slot><template v-if="showCountdown">({{ countdown }}S)</template>
    </div>
  </button>
</template>

<script>
import IconLoading from '@/components/IconLoading'
import countdown from '@/mixins/countdown'

export default {
  name: 'Button',
  mixins: [countdown],
  props: {
    type: {
      type: String,
      default: 'default',
      validator (value) {
        return ['default', 'outline', 'outline-light', 'ghost'].indexOf(value) !== -1
      }
    },
    size: {
      type: String,
      default: 'large'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    },
    // 0秒表示不启用
    emitSeconds: {
      type: Number,
      default: 0
    }
  },
  components: {
    IconLoading
  },
  data () {
    return {
      showCountdown: false
    }
  },
  methods: {
    handleEmit () {
      this.countdownTimer && clearTimeout(this.countdownTimer)
      const e = document.createEvent('MouseEvents')
      e.initEvent('click', true, true)
      this.$el.dispatchEvent(e)
      this.showCountdown = false
    },
    handleClick (ev) {
      this.countdownTimer && clearTimeout(this.countdownTimer)
    }
  },
  mounted () {
    if (this.emitSeconds) {
      this.showCountdown = true
      this.COUNTDOWN = this.emitSeconds
      this.startCountdown(this.handleEmit)
    }
  }
}
</script>

<style lang="scss" scoped>
.button {
  background-color: $primary-color;
  color: #fff;
  border: none;
  padding: 1.44rem 4rem;
  border-radius: 10rem;
  cursor: pointer;
  transition: $base-transition;
  overflow: hidden;
  vertical-align: top;
  outline: none;
  font-size: 4rem;
  position: relative;
  &:before {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
  }
  .icon-loading {
    width: 4rem;
    height: 4rem;
    margin-right: 2rem;
  }
  &.button--ghost {
    background-color: transparent;
    color: #C7C7C7;
  }
  &.button--outline-light,
  &.button--outline {
    border: 1px solid #979797;
    box-sizing: border-box;
    background-color: transparent;
  }
  &.button--outline-light {
    color: #000;
    &:active:before {
      background: rgba(0, 0, 0, .05);
    }
  }
  &:active:before {
    background-color: rgba(255, 255, 255, .1);
  }
  &[disabled] {
    cursor: not-allowed;
    &:before {
      background-color: rgba(255, 255, 255, .2);
    }
  }
  &.button--small {
    font-size: 3rem;
    padding: 1.2rem 3rem;
  }
}
</style>
