import Vue from 'vue'
import Block from './Block'

const BlockConstructor = Vue.extend(Block)

const block = new BlockConstructor({
  el: document.createElement('div'),
  data () {
    return {
      content: 'block',
      icon: null,
      showBlock: false,
      closeTimeout: null
    }
  },
  methods: {
    show (content, opt = {}) {
      this.content = content
      if (opt.duration) {
        this.closeTimeout && clearTimeout(this.closeTimeout)
        this.closeTimeout = setTimeout(this.hide, opt.duration)
      }
      this.icon = opt.icon
      this.showBlock = true
    },
    hide () {
      this.closeTimeout && clearTimeout(this.closeTimeout)
      this.showBlock = false
    }
  },
  mounted () {
    document.body.append(this.$el)
  }
})

export default {
  show: block.show.bind(block),
  hide: block.hide
}
