import Vue from 'vue'
import Message from './Message'

const MessageConstructor = Vue.extend(Message)

const queue = []
const offsetTop = 4.5

function resetHeight (fromIndex) {
  queue.splice(fromIndex, 1)
  const { length } = queue
  // 删除后没有元素，或者删除最后一个，跳过
  if (!length || fromIndex === length) return
  for (let i = 0; i < queue.length; i++) {
    const item = queue[i]
    let top = 0
    if (i !== 0) {
      const preItem = queue[i - 1]
      top = preItem.top + preItem.height
    }
    item.setIndex(i)
    item.setTop(top)
  }
}

export default function message (content, opt = {}) {
  const item = new MessageConstructor({
    el: document.createElement('div'),
    data () {
      return {
        content,
        top: 0,
        height: 0,
        show: true,
        index: queue.length,
        offsetTop
      }
    },
    methods: {
      setTop (value) {
        this.top = value
      },
      setIndex (index) {
        this.index = index
      }
    },
    mounted () {
      this.$nextTick(() => {
        const duration = opt.duration || 3000
        this.height = parseInt(getComputedStyle(this.$el, null).height)
        setTimeout(() => {
          this.show = false
          resetHeight(this.index)
        }, duration)
      })
    }
  })
  document.body.append(item.$el)
  queue.push(item)
  if (queue.length > 1) {
    const preItem = queue[queue.length - 2]
    item.setTop(preItem.top + preItem.height)
  }
}
