<template>
  <div class="home-header flex-between-center">
    <div class="logo">
      <img :src="logo">
    </div>
    <div class="info flex-default-center">
      <div class="hours">
        {{ time.getHours().toString().padStart(2, 0) }}:{{ time.getMinutes().toString().padStart(2, 0) }}
      </div>
      <div class="days secondary-text-color">
        <div>{{ $d(time, 'date') }}</div>
        <div>{{ daytime }} {{ $d(time, 'weekday') }}</div>
      </div>
      <div class="device-status" style="margin-left: .5rem">
        <!-- <SignalBar></SignalBar> -->
        <!-- <div style="font-size: 1.8rem;">99%</div> -->
        <!-- <BatteryStatus style="mt-10"></BatteryStatus> -->
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
// import SignalBar from './SignalBar'
// import BatteryStatus from './BatteryStatus'

const logoDefault = require('@/assets/images/logo.png')

export default {
  name: 'HomeHeader',
  components: {
    // SignalBar,
    // BatteryStatus
  },
  data () {
    return {
      time: new Date(),
      timer: null
    }
  },
  computed: {
    ...mapState('app', ['appConfig']),
    daytime (date) {
      const hours = this.time.getHours()
      if (hours < 12) {
        return this.$t('morning')
      }
      return this.$t('afternoon')
    },
    logo () {
      if (this.appConfig) {
        return this.appConfig.logo
      }
      if (process.env.NODE_ENV !== 'production') {
        return logoDefault
      }
      return ''
    }
  },
  methods: {
    getTime () {
      this.time = new Date()
      this.timer && clearTimeout(this.timer)
      this.timer = setTimeout(this.getTime, 1000)
    }
  },
  created () {
    this.getTime()
  },
  beforeDestroy () {
    this.timer && clearTimeout(this.timer)
  }
}
</script>

<style lang="scss" scoped>
.home-header {
  overflow: hidden;
  .logo {
    flex: 0 0 29.85vw;
    height: 5.6vh;
    overflow: hidden;
    img {
      display: block;
      max-height: 5.6vh;
      max-width: 29.85vw;
    }
  }
  .info {
    .hours {
      font-size: 6rem;
    }
    .days {
      font-size: 2rem;
      margin: 1rem;
    }
  }
}
</style>
