<template>
  <div class="grid-pending">
    <SimpleMap v-show="showMap"
              :on-back="handleMapBackBtnClick"
              @on-confirm="handleConfirm"
              ref="map">
    </SimpleMap>
    <div v-show="!showMap" class="grid-box-view">
      <Bar :on-back="handleBack">{{ $t('backSelect') }}</Bar>
      <div class="title">
        请按照下列位置放餐
      </div>
      <div class="box-view">
        <div class="box-column-row">
          <div class="box-column left">
            {{ $t('leftCol') }}
          </div>
          <div class="box-column right">
            {{ $t('rightCol') }}
          </div>
        </div>
        <div class="box-layer"
            v-for="(layer, layerIndex) in 5"
            :key="layer">
          <div class="layer-text">
            {{ $t('track') }}<br>
            {{ (2 * layer - 1).toString().padStart(2, '0') }}
          </div>
          <div v-for="(col, i) in ['left', 'right']"
              :class="`box-column ${col}`"
              :key="col">
            <div class="empty-tips">
              <div class="select-tips">
                <!-- {{ $t('selectTips') }} -->{{ $t('empty') }}
              </div>
              <!-- <div class="limit-tips">
                {{ $t('limitTips') }}
              </div> -->
            </div>
            <template v-for="(pos, j) in ['behind', 'front']">
              <div :class="`box-item ${pos}`"
                  :key="pos"
                  v-if="boxlist[layerIndex * 4 + i * 2 + (1 - j)]">
                {{ boxlist[layerIndex * 4 + i * 2 + (1 - j)].name }}
              </div>
            </template>
            <template v-if="boxlist[layerIndex * 4 + i * 2]">
              <div class="position-text behind">{{ $t('inner') }}</div>
              <div class="position-text front">{{ $t('outter') }}</div>
            </template>
          </div>
          <div class="layer-text">
            {{ $t('track') }}<br>
            {{ (2 * layer).toString().padStart(2, '0') }}
          </div>
        </div>
        <div class="handle-btns">
          <iButton class="start-task-btn" @click="handleStartTask">{{ $t('startTask') }}</iButton>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SimpleMap from '@/components/SimpleMap'
import Bar from '@/components/Bar'
import startTask from '@/mixins/startTask'
import iButton from '@/components/Button'

export default {
  mixins: [startTask],
  components: {
    SimpleMap,
    Bar,
    iButton
  },
  data () {
    return {
      taskType: 'delivery',
      showMap: true,
      boxlist: [],
      // 轨道序号，从左到右，从上到下
      trackIndex: null
    }
  },
  methods: {
    handleMapBackBtnClick () {
      this.$router.back()
    },
    handleShowMap (trackIndex) {
      this.trackIndex = trackIndex
      this.showMap = true
      this.$nextTick(() => {
        this.$refs.map.clearSelection()
        if (this.boxlist[this.trackIndex] || this.boxlist[this.trackIndex + 1]) {
          this.$refs.map.set(this.boxlist[this.trackIndex], this.boxlist[this.trackIndex + 1])
        }
      })
    },
    handleConfirm (rooms) {
      console.log(rooms)
      this.boxlist = []
      Object.keys(rooms).map(e => {
        return rooms[e]
      }).sort((a, b) => {
        if (a.sort !== b.sort) {
          return a.sort - b.sort
        }
        if (a.name > b.name) {
          return 1
        }
        if (a.name < b.name) {
          return -1
        }
        return 0
      }).forEach((e) => {
        for (let n = 0; n < e.count; n++) {
          this.boxlist.push({ ...e })
        }
      })
      console.log(this.boxlist)
      this.showMap = false
      // this.$set(this.boxlist, this.trackIndex, room.front)
      // this.$set(this.boxlist, this.trackIndex + 1, room.behind)
      // console.log(this.boxlist)
    },
    handleRemove (index) {
      this.$set(this.boxlist, index, null)
    },
    handleStartTask () {
      const selectedPoints = this.boxlist.map((e, index) => {
        if (e) {
          const i = index + 1
          return {
            ...e,
            drawer: `${Math.ceil(i / 2)}-${(i % 2) || 2}`
          }
        }
        return e
      }).filter(e => e)
      if (!selectedPoints.length) return
      const mergedPoints = []
      let merge = null
      selectedPoints.reduce((pre, current) => {
        if (pre && current.mapId === pre.mapId && current.name === pre.name) {
          merge.drawer += (',' + current.drawer)
        } else {
          merge = JSON.parse(JSON.stringify(current))
          mergedPoints.push(merge)
        }
        return current
      }, null)
      const data = {
        targetPoi: mergedPoints.map(e => {
          return {
            bizPointId: e.id,
            bizPointName: e.name,
            drawer: e.drawer
          }
        })
      }
      console.log(data)
      // 已经发起过
      if (this.tasks.length) {
        this.tasks[1] = data
      } else {
        this.tasks = [data]
      }
      this.handleStart()
    },
    handleBack () {
      this.showMap = true
    }
  }
}
</script>

<style lang="scss">
.grid-pending {
  width: 100%;
  height: 100%;
  background-color: #202439;
  .grid-box-view {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    .title {
      font-size: 4rem;
      text-align: center;
      margin-bottom: 4rem;
    }
    .box-view {
      font-size: 4rem;
      .box-column-row, .box-layer {
        display: flex;
        align-items: center;
        .layer-text {
          flex: 0 0 12vw;
          font-size: 3rem;
          color: rgba(255, 255, 255, .4);
          text-align: center;
        }
        .box-column {
          flex: 0 0 36.5vw;
          position: relative;
          text-align: center;
          display: flex;
          flex-direction: column;
          &.left {
            margin-right: 3vw;
          }
          .empty-tips {
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            color: rgba(255, 255, 255, .4);
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            .limit-tips {
              font-size: 2rem;
              margin-top: .8rem;
            }
          }
          .box-item {
            flex: 1 1 50%;
            background: #6C83E7;
            z-index: 1;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            box-sizing: border-box;
            &.behind {
              background: #4460D7;
            }
          }
        }
      }
      .box-column-row {
        justify-content: center;
        font-size: 3rem;
        color: rgba(255, 255, 255, .4);
        margin: 2.5rem 0 1.3333rem 0;
      }
      .box-layer {
        margin-bottom: 2.5rem;
        .box-column {
          height: 12vh;
          background-color: #424969;
          border-radius: 1.66667rem;
          overflow: hidden;
          .position-text {
            position: absolute;
            right: 1.666667rem;
            font-size: 2.6rem;
            color: #fff;
            background-color: rgba(0, 0, 0, .2);
            width: 6rem;
            height: 6rem;
            line-height: 6rem;
            text-align: center;
            border-radius: .5rem;
            z-index: 2;
            &.behind {
              top: 1.66667rem;
            }
            &.front {
              bottom: 1.66667rem;
            }
          }
        }
      }
    }
    .handle-btns {
      padding: 0 12vw;
      margin-top: 4vh;
      .start-task-btn {
        width: 100%;
        padding: 0;
        border-radius: 1.33333rem;
        background: linear-gradient(to bottom, #08AFCF, #0F966B);
        >div {
          width: 100%;
          justify-content: center;
          height: 10.83333rem;
          box-sizing: border-box;
        }
      }
    }
  }
}
</style>

<i18n>
{
  "zh-CN": {
    "leftCol": "左侧餐道",
    "rightCol": "右侧餐道",
    "selectTips": "添加房间",
    "limitTips": "最多可添加2个房间",
    "startTask": "开始配送",
    "track": "货道",
    "backSelect": "重新选择",
    "empty": "空",
    "inner": "内",
    "outter": "外"
  }
}
</i18n>
