<template>
  <div class="number-keyboard page" :class="{ 'number-keyboard--disabled': disabled }">
    <div class="key-item"
        v-for="(item, index) in keys"
        :class="{ 'optional-key': item.optional }"
        :key="index">
      <a @click.prevent="handleClick(item)">
        {{ item.optional ? $t(item.key) : item }}
      </a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'NumberKeyboard',
  props: {
    optionalKey: {
      type: String,
      default: 'delete'
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    keys () {
      return [1, 2, 3, 4, 5, 6, 7, 8, 9, 0, { optional: true, key: this.optionalKey }]
    }
  },
  methods: {
    handleClick (item) {
      if (this.disabled) {
        return
      }
      let value = item
      if (item && item.optional) {
        value = item.key
      }
      this.$emit('on-click', value)
    }
  }
}
</script>

<style lang="scss" scoped>
.number-keyboard {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  font-size: #{82/$r}rem;
  &:not(.number-keyboard--disabled) {
    a:active {
      background-color: rgba(255, 255, 255, .2);
    }
  }
  .key-item {
    flex: 0 0 33.3333%;
    height: 25%;
    text-align: center;
    overflow: hidden;
    a {
      $v: 140/12;
      $d: #{$v}rem;
      display: inline-block;
      height: $d;
      width: $d;
      border-radius: 50%;
      line-height: $d;
      text-align: center;
      transition: $base-transition;
    }
    &:before {
      content: '';
      display: inline-block;
      height: 100%;
      width: 0;
      vertical-align: middle;
    }
  }
  .optional-key {
    font-size: #{64/$r}rem;
    a {
      border-radius: 0;
      padding: 0 2rem;
      border-radius: 2rem;
      &:active {
        background-color: transparent;
        opacity: .5;
      }
    }
  }
}
</style>
