import axios from 'axios'
import md5 from 'blueimp-md5'
import store from '../store'
import { logger } from '../ue4/ping'

const urlPrefix = (url, proxy) => {
  return `${process.env.NODE_ENV === 'development' ? proxy : ''}${url}`
}

const requestHandler = (request) => {
  return new Promise((resolve, reject) => {
    request
      .then((res) => {
        const { status, data } = res
        if (status === 200 && data && data.code === 0) {
          resolve(data)
        } else {
          reject(res)
        }
      })
      .catch(reject)
  })
}

function getAuth () {
  const obj = {
    appId: '61095f6966146393710420b62808637b',
    timestamp: Date.now(),
    nonceStr: Math.random().toString(36).substring(6)
  }
  obj.sign = md5(`appId${obj.appId}timestamp${obj.timestamp}nonceStr${obj.nonceStr}2ea5aa97d33a46369bcc45a761efea0c`)
  return obj
}

axios.interceptors.request.use((config) => {
  // 请求添加鉴权
  config.headers = Object.assign(getAuth(), config.headers)
  const robotInfo = store.state.app.robotInfo
  const key = config.method === 'post' ? 'data' : 'params'
  if (robotInfo) {
    config[key] = Object.assign({ robotCode: robotInfo.robotId, type: 0 }, config[key])
  }
  const log = {
    url: config.url,
    [key]: config[key],
    headers: config.headers
  }
  logger(`[HTTP ${config.method}] `, log)
  return config
}, (error) => {
  // 请求错误
  logger('[HTTP ERROR] ', error)
  return Promise.reject(error)
})

axios.interceptors.response.use((response) => {
  const key = response.config.method === 'post' ? 'data' : 'params'
  const log = {
    status: response.status,
    data: response.data,
    request: {
      url: response.config.url,
      method: response.config.method,
      [key]: response.config[key]
    }
  }
  logger('[HTTP RESPONSE] ', log)
  return response
}, (error) => {
  // 响应错误
  logger('[HTTP REPONSE ERROR] ', error)
  return Promise.reject(error)
})

export default {
  get: (url, config = {}) => {
    return requestHandler(axios.get(urlPrefix(url, config.proxy || '/86'), config)).catch((err) => Promise.reject(err))
  },
  post: (url, data, config = {}) => {
    return requestHandler(axios.post(urlPrefix(url, config.proxy || '/86'), data, config)).catch((err) => Promise.reject(err))
  }
}
