import { mapState, mapGetters, mapActions } from 'vuex'
import request from '@/utils/request'
import backHandle from './backHandle'

export default {
  mixins: [backHandle],
  data () {
    return {
      validated: null,
      tasks: [],
      loading: false
    }
  },
  computed: {
    ...mapState('map', ['returnPoint', 'currentFloor', 'currentLocation', 'currentMapId', 'mapInfo']),
    ...mapGetters('map', ['isValidReturnPoint']),
    ...mapGetters('app', ['isPatrol']),
    getTarget () {
      const { length } = this.tasks
      if (length === 0) return 'unknow'
      if (length < 3) {
        return this.tasks[0].poi
      }
      return this.tasks[1].poi
    }
  },
  methods: {
    ...mapActions('map', ['queryReturnPoint']),
    getStartPoint () {
      return {
        type: 1,
        point: {
          x: 0,
          y: 0,
          yaw: 0
        },
        poi: this.$t('currentPoint'),
        mapId: this.currentMapId,
        floor: this.currentFloor
      }
    },
    getReturnPoint () {
      return {
        type: 3,
        point: {
          x: this.returnPoint.x,
          y: this.returnPoint.y,
          yaw: this.returnPoint.yaw
        },
        poi: this.returnPoint.name,
        mapId: this.returnPoint.mapId,
        floor: this.returnPoint.floor
      }
    },
    getCurrentLocation () {
      return {
        type: 3,
        point: {
          x: this.currentLocation.x,
          y: this.currentLocation.y,
          yaw: 0
        },
        poi: this.$t('currentPoint'),
        mapId: this.currentMapId,
        floor: this.currentFloor
      }
    },
    startTaskTips (msg) {
      this.$notice(msg, {
        actions: [{
          onClick: 'close',
          emitSeconds: 10,
          label: this.$t('gotIt')
        }]
      })
    },
    handleStart () {
      localStorage.removeItem('openDoorEvent')
      localStorage.removeItem('closeDoorEvent')
      // validated标识tasks已经处理过，用户多次点击不要重复处理
      if (!this.validated) {
        // 有当前地图才可以进行任务
        if (this.currentMapId) {
          this.tasks.unshift(this.getStartPoint())
          // 有设置返回点且返回点在地图列表中
          if (this.returnPoint && this.isValidReturnPoint) {
            this.tasks.push(this.getReturnPoint())
          } else if (this.currentLocation) {
            // 把当前点当返回点
            this.tasks.push(this.getCurrentLocation())
            this.startTaskTips(this.$t('missingReturnPoint'))
          } else {
            return this.startTaskTips(this.$t('invalidReturnPointAndCurrentLocation'))
          }
          // 在这里才验证通过
          this.validated = true
        } else {
          return this.startTaskTips(this.$t('missingCurrentMap'))
        }
      }
      this.loading = true
      const param = {
        taskType: this.taskType,
        opId: `startTask-${Date.now()}`,
        tasks: this.tasks
      }
      request
        .post(
          `/crss-robot/task/robotapi/v1/receptionTask/${this.isPatrol ? 'startTaskByBizPoint' : 'startTask'}`,
          { param: this.isPatrol ? param : JSON.stringify({ param }) }
        )
        .then((res) => {
          console.log('发起成功: ', res)
        })
        .catch((error) => {
          this.loading = false
          console.log('发起失败: ', error)
          console.log('notice: ', this.$notice)
          if (error.status == 200) {
            const err = error.data
            let code = err.code
            let message = err.message
            let data
            if (err.code == 122304) {
              if (this.isPatrol) {
                code = err.code
                message = err.message
              } else {
                try {
                  data = JSON.parse(err.data)
                } catch (runtimeErr) {
                  console.error('parse err.data error: ', runtimeErr)
                  data = err.data
                }
                const hasErrCode = data.errCode !== undefined && data.errCode !== ''
                code = hasErrCode ? data.errCode : err.code
                message = hasErrCode ? this.$t(`errors.${code}`) : data.errDetail
              }
            }
            this.$notice(`${this.$t('startTaskFailed')}<br>code: ${code || ''}<br> message: ${message}`, {
              actions: [{
                onClick: 'close',
                emitSeconds: 10,
                label: this.$t('gotIt')
              }]
            })
          } else {
            this.$message('network error')
          }
        })
    },
    // 取消
    handleCancel () {
      this.back()
    }
  },
  mounted () {
    this.queryReturnPoint()
  }
}
