import ping from '@/ue4/ping'
import pingTypes from '@/ue4/ping/types'
import message from '@/components/Message'

export default {
  namespaced: true,
  state: {
    queryMapLoading: false,
    mapInfo: null,
    returnPoint: null,
    // 机器人当前地图所在楼层
    currentFloor: null,
    currentLocation: null,
    currentMapId: null
  },
  getters: {
    isValidReturnPoint ({ returnPoint, mapInfo }) {
      return returnPoint && mapInfo && mapInfo.find(e => e.mapId === returnPoint.mapId)
    }
  },
  mutations: {
    setQueryMapLoading (state, loading) {
      state.queryMapLoading = loading
    },
    setCurrentFloor (state, floor) {
      state.currentFloor = floor
      console.log('setCurrentFloor: ', state.currentFloor)
    },
    setCurrentMapId (state, mapId) {
      state.currentMapId = mapId
      console.log('setCurrentMapId: ', state.currentMapId)
    },
    setCurrentLocation (state, location) {
      state.currentLocation = location
      console.log('setCurrentLocation: ', state.currentLocation)
    },
    setMapInfo (state, data) {
      if (!data || !data.mapInfos) {
        return
      }
      state.mapInfo = data.mapInfos.filter(e => e && e.mapId).map(e => {
        const floor = e.info.map_floor
        return {
          floor,
          name: e.info.map_name,
          mapId: e.mapId,
          locations: Object.keys(e.pois).map(k => {
            return {
              name: k,
              floor,
              mapId: e.mapId,
              ...e.pois[k]
            }
          })
        }
      }).sort((a, b) => b.floor - a.floor)
    },
    setReturnPoint (state, data) {
      state.returnPoint = data
    }
  },
  actions: {
    queryMapInfo ({ commit }) {
      commit('setQueryMapLoading', true)
      ping(pingTypes.QUERY_MAP_INFO)
    },
    queryMapInfoResp ({ commit }, data) {
      commit('setQueryMapLoading', false)
      commit('setMapInfo', data)
      if (data && data.mapInfos) {
        const exist = data.mapInfos.find(e => e.mapId === data.active)
        if (exist) {
          commit('setCurrentMapId', data.active)
          commit('setCurrentFloor', exist.info.map_floor)
        } else {
          console.warn(`未在地图列表中找到激活的地图${data.active}`)
        }
      }
      if (data.x !== undefined) {
        commit('setCurrentLocation', { x: data.x, y: data.y })
      }
    },
    queryReturnPoint () {
      ping(pingTypes.READ_LOCAL)
    },
    queryReturnPointResp ({ commit }, localData) {
      const { data, code } = localData
      if (code === 0) {
        if (data && data.returnPoint) {
          commit('setReturnPoint', data.returnPoint)
        }
        return
      }
      message('readLocal error')
    }
  }
}
