<template>
  <div class="page page-phone-number">
    <div class="tips">
      {{ $t('enterUserPhoneNumber') }}
    </div>
    <div class="phone-number">
      {{ phoneString }}
    </div>
    <NumberKeyboard class="keyboard"
                    @on-click="handleClick"
                    :disabled="loading">
    </NumberKeyboard>
    <iButton type="outline"
            class="send-btn"
            :loading="loading"
            :disabled="!lengthFullfill"
            @click="handleValidate">
      {{ $t('sendExpress') }}
    </iButton>
  </div>
</template>

<script>
import NumberKeyboard from '@/components/NumberKeyboard.vue'
import iButton from '@/components/Button.vue'
import request from '@/utils/request'

export default {
  components: {
    NumberKeyboard,
    iButton
  },
  data () {
    return {
      phoneNumber: [],
      // 11位手机号
      length: 11,
      loading: false,
      notice: null
    }
  },
  computed: {
    inputLength () {
      return this.phoneNumber.length
    },
    phoneString () {
      return this.phoneNumber.reduce((pre, cur, index) => {
        if (index === 2 || index === 6) {
          cur += ' '
        }
        pre += cur
        return pre
      }, '')
    },
    lengthFullfill () {
      return this.inputLength === this.length
    }
  },
  methods: {
    handleClick (key) {
      if (this.notice) {
        this.notice.hide()
      }
      if (key === 'delete') {
        this.phoneNumber.pop()
      } else if (!this.lengthFullfill) {
        this.phoneNumber.push(key)
      }
    },
    handleValidate () {
      this.loading = true
      request
        .get(`/crss-mobile/uces/v1/person/getByPhone?phone=${this.phoneNumber.join('')}`)
        .then((res) => {
          this.$emit('on-validate-pass', res.data)
        })
        .catch(() => {
          this.notice = this.$notice2(this.$t('phoneNumberValidateFail'), { duration: 5000 })
        })
        .finally(() => {
          this.loading = false
        })
    }
  },
  // 离开前把请求取消了
  beforeDestroy () {
    // cancel request
  }
}
</script>

<style lang="scss" scoped>
.page-phone-number {
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 0;
  .tips {
    font-size: 4rem;
    margin: 12vh 0 3.4vh 0;
  }
  .phone-number {
    font-size: #{82 / $r}rem;
    background-color: #000;
    border: 1px solid #979797;
    border-radius: #{10 / $r}rem;
    height: 6.3vh;
    width: 58.3vw;
    text-align: center;
    line-height: 6.3vh;
    letter-spacing: #{5 / $r}rem;
  }
  .keyboard {
    width: 78%;
    height: 52%;
  }
  .send-btn {
    margin-top: 3vh;
    padding-left: 8vw;
    padding-right: 8vw;
  }
}
</style>

<i18n>
{
  "zh-CN": {
    "enterUserPhoneNumber": "输入寄件人手机号码",
    "phoneNumberValidateFail": "验证失败，请核对后重新输入。手机号码为入职时提供的号码"
  },
  "en": {
    "enterUserPhoneNumber": "Enter sender's phone number",
    "phoneNumberValidateFail": "Validation failed, please check and retry. The phone number is provided onboarding"
  },
  "th": {
    "enterUserPhoneNumber": "ระบุเบอร์โทรศัพท์ผู้ส่ง",
    "phoneNumberValidateFail": "การตรวจสอบล้มเหลวโปรดตรวจสอบและลองอีกครั้ง หมายเลขโทรศัพท์ไม่ถูกต้อง"
  }
}
</i18n>
