<template>
  <div class="page">
    <Map v-if="showMap" @on-select="handleSelect" :on-back="handleMapBackBtnClick" :btn-text="$t('chooseMe')" :type="pointType"></Map>
    <div class="page new-disinfect-task">
      <Bar>
        {{ $t('new') }}
      </Bar>
      <div class="task-form">
        <SettingRow @click.native="handleSelectLocation">
          <template slot="title">{{ $t('selectLocation') }}</template>
          <div class="flex-between-center" :class="{ placeholder: !selectedLocation }">
            {{ selectedLocation ? selectedLocation.name : $t('selectLocationTips') }}
            <img class="select-arrow" src="@/assets/images/icon-arrow-left.png" alt="">
          </div>
        </SettingRow>
        <SettingRow>
          <template slot="title">{{ $t('duration') }}</template>
          <div class="duration-row flex-between-center" :class="{ placeholder: true }">
            <input v-model="minute" class="fs4 duration-input" type="number" :min="1" :max="120" />
            <span>{{ $t('minute') }}</span>
          </div>
        </SettingRow>
      </div>
      <div class="task-handler">
        <div class="tips fs4">{{ $t('tips') }}</div>
        <iButton class="new-task-btn" @click="handleStart" :loading="loading">{{ $t('start') }}</iButton>
      </div>
    </div>
  </div>
</template>

<script>
import Bar from '@/components/Bar.vue'
import iButton from '@/components/Button'
import Map from '@/components/Map'
import SettingRow from '../Publicize/SettingRow.vue'
import CONST from '@/const'
import ping from '@/ue4/ping'
import pingTypes from '@/ue4/ping/types'

export default {
  components: {
    Bar,
    iButton,
    Map,
    SettingRow
  },
  data () {
    return {
      showMap: false,
      pointType: CONST.INTEREST,
      selectedLocation: null,
      minute: 60,
      loading: false
    }
  },
  computed: {
    disabled () {
      return !this.selectedLocation || !Number.isInteger(this.minute) || this.minute <= 0 || this.minute > 120
    }
  },
  methods: {
    handleSelectLocation () {
      this.showMap = true
    },
    handleMapBackBtnClick () {
      this.showMap = false
    },
    handleSelect (location) {
      this.selectedLocation = location
      this.showMap = false
    },
    handleStart () {
      if (!this.selectedLocation) {
        return this.$message(this.$t('selectLocation'))
      }
      const minute = parseInt(this.minute)
      if (!Number.isInteger(minute) || minute <= 0 || minute > 120) {
        return this.$message(this.$t('invalidDuration'))
      }
      ping(pingTypes.START_UE4_TASK, {
        taskType: CONST.DISINFECT,
        tasks: [{
          type: 1,
          mapId: this.selectedLocation.mapId,
          poi: this.selectedLocation.name,
          point: {
            x: this.selectedLocation.x,
            y: this.selectedLocation.y,
            yaw: this.selectedLocation.yaw
          },
          duration: minute
        }]
      })
      this.loading = true
    }
  }
}
</script>

<i18n>
{
  "zh-CN": {
    "new": "新建任务",
    "start": "开始消杀",
    "selectLocation": "选择兴趣点",
    "tips": "对我说\"开始消杀\"或",
    "selectLocationTips": "选择兴趣点",
    "duration": "消杀时长",
    "chooseMe": "选我",
    "selectLocation": "请选择消杀地点",
    "invalidDuration": "消杀时长为1-120以内的整数"
  }
}
</i18n>

<style lang="scss" scoped>
.new-disinfect-task {
  display: flex;
  flex-direction: column;
  .task-form {
    padding: 4vh 4vw;
    flex: 1;
    .placeholder {
      color: #9D9D9D;
    }
    .select-arrow {
      transform: rotate(-90deg);
      height: 4.25vw;
      opacity: .5;
    }
    .duration-row {
      height: 100%;
    }
    .duration-input {
      background: transparent;
      background-color: transparent;
      border: none;
      outline: none;
      color: #fff;
      height: 100%;
      width: 70%;
      &:focus {
        outline: none;
      }
    }
  }
  .task-handler {
    text-align: center;
    margin-bottom: 8vh;
    .tips {
      color: #999;
      margin-bottom: 2vh;
    }
  }
}
</style>
