<template>
  <div class="bar flex-default-center">
    <a @click.prevent="handleClick" class="back-btn">
      <img src="@/assets/images/icon-arrow-left.png">
    </a>
    <div class="bar-right">
      <slot></slot><template v-if="showCountdown">({{ countdown }}S)</template>
    </div>
  </div>
</template>

<script>
import countdown from '@/mixins/countdown'
import backHandle from '@/mixins/backHandle'

export default {
  name: 'Bar',
  mixins: [countdown, backHandle],
  props: {
    onBack: {
      type: Function
    },
    // 自动触发退回按钮
    emitSeconds: {
      type: Number
    }
  },
  data () {
    return {
      showCountdown: false
    }
  },
  methods: {
    handleClick () {
      this.countdownTimer && clearTimeout(this.countdownTimer)
      this.showCountdown = false
      if (this.onBack) this.onBack()
      else this.back()
    }
  },
  mounted () {
    if (this.emitSeconds) {
      this.showCountdown = true
      this.COUNTDOWN = this.emitSeconds
      this.startCountdown(this.handleClick)
    }
  }
}
</script>

<style lang="scss" scoped>
.bar {
  padding: 2.4vh 4.2vw 2.2vh 4.5vw;
  .back-btn {
    text-align: center;
    position: relative;
    // 稍微扩大可点击的范围
    &:before {
      content: '';
      position: absolute;
      display: block;
      padding: 4vw;
      background-color: transparent;
      transition: $base-transition;
      left: 50%;
      top: 50%;
      transform: translate3d(-50%, -50%, 0);
      border-radius: 50%;
      z-index: 0;
    }
    &:active:before {
      background-color: rgba(255, 255, 255, .1);
    }
    img {
      position: relative;
      width: 2.8vw;
      z-index: 1;
    }
  }
  .bar-right {
    margin-left: 4.4vw;
    flex: 1;
    font-size: 4rem;
  }
}
</style>
