<template>
  <transition name="modal-mask-fade">
    <div v-if="value" class="page modal-wrapper flex-default-center">
      <div class="modal">
        <div class="title" v-if="title">
          {{ title }}
        </div>
        <slot></slot>
        <IconLoading v-if="loading" class="icon-loading"></IconLoading>
        <div v-else class="footer flex-default-center">
          <slot name="footer">
            <iButton type="outline-light" @click="onCancel">{{ cancelText }}</iButton>
            <iButton @click="onConfirm">{{ confirmText }}</iButton>
          </slot>
        </div>
        <img v-if="closeBtn"
            src="@/assets/images/icon-close-round.png"
            class="icon-close"
            @click="handleClose">
      </div>
    </div>
  </transition>
</template>

<script>
import iButton from '@/components/Button'
import IconLoading from '@/components/IconLoading'

export default {
  name: 'Modal',
  props: {
    title: {
      type: String
    },
    show: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    },
    confirmText: {
      type: String,
      default () {
        return this.$t('confirm')
      }
    },
    cancelText: {
      type: String,
      default () {
        return this.$t('cancel')
      }
    },
    onConfirm: {
      type: Function,
      default: () => {}
    },
    onCancel: {
      type: Function,
      default: () => {}
    },
    closeBtn: {
      type: Boolean,
      default: false
    }
  },
  components: {
    iButton,
    IconLoading
  },
  data () {
    return {
      value: false
    }
  },
  watch: {
    show: {
      immediate: true,
      handler (nv) {
        if (nv !== this.value) this.value = nv
      }
    }
  },
  methods: {
    handleClose () {
      this.value = false
      this.$emit('update:show', false)
    }
  }
}
</script>

<style lang="scss" scoped>
.modal-mask-fade-enter,
.model-mask-fade-leave-to {
  opacity: 0;
  transform: translate3d(0, -2vh, 0);
}

.modal-wrapper {
  position: absolute;
  z-index: $modal-z-index;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, .5);
  transition: $base-transition;
  .modal {
    background-color: #fff;
    border-radius: #{30 / $r}rem;
    padding: 2.6vh 5.3vw;
    width: 75vw;
    margin: 0 auto;
    font-size: 4rem;
    color: #000;
    text-align: center;
    position: relative;
    .title {
      font-size: 4.5rem;
      margin-bottom: 2.8vh;
    }
    .icon-loading {
      display: block;
      margin: 0 auto;
      margin-top: 3.6vh;
    }
    .footer {
      justify-content: center;
      margin-top: 3.6vh;
      button {
        margin-right: 6.6rem;
        &:last-child {
          margin-right: 0;
        }
      }
    }
    .icon-close {
      position: absolute;
      width: 3.3vw;
      height: 2.1vh;
      display: block;
      top: 3.6vh;
      right: 3.8vw;
    }
  }
}
</style>
