export default {
  data () {
    return {
      COUNTDOWN: 70,
      countdown: 0,
      countdownTimer: null
    }
  },
  methods: {
    runCountdown (cb) {
      this.countdownTimer && clearTimeout(this.countdownTimer)
      this.countdown -= 1
      if (this.countdown === 0) {
        typeof cb === 'function' && cb.call(this)
        this.countdown = this.COUNTDOWN
        return
      }
      this.countdownTimer = setTimeout(() => {
        this.runCountdown(cb)
      }, 1000)
    },
    startCountdown (cb) {
      this.countdown = this.COUNTDOWN
      this.countdownTimer && clearTimeout(this.countdownTimer)
      this.countdownTimer = setTimeout(() => {
        this.runCountdown(cb)
      }, 1000)
    }
  },
  beforeDestroy () {
    this.countdownTimer && clearTimeout(this.countdownTimer)
  }
}
