<template>
  <div class="floor-slider">
    <a @click.prevent="handleScroll(true)" class="slide-btn">
      <img src="@/assets/images/icon-arrow-up.png">
    </a>
    <div class="floor-list"
        ref="floorList"
        :style="{ flex: `0 0 ${floors.length < 12 ? floors.length * 5.8 : 69.6}vh` }">
      <div class="floor-list-item"
          :class="{
            'active-floor': item.mapId === active,
            'current-floor': currentMapId === item.mapId
          }"
          @click="handleClick(item.mapId, i)"
          v-for="(item, i) in floors"
          :key="item.mapId">
        {{ item.floor | floorDisplay }}
      </div>
    </div>
    <a @click.prevent="handleScroll(false)" class="slide-btn">
      <img src="@/assets/images/icon-arrow-up.png">
    </a>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'FloorSlider',
  props: {
    floors: {
      type: Array,
      require: true
    }
  },
  data () {
    return {
      active: null
    }
  },
  filters: {
    floorDisplay (floor) {
      if (floor >= 0) {
        return `${floor}F`
      }
      return `B${Math.abs(floor)}`
    }
  },
  computed: {
    ...mapState('map', ['currentFloor', 'currentMapId'])
  },
  watch: {
    currentMapId: {
      handler (nv) {
        if (nv) {
          const index = this.floors.findIndex(e => e.mapId === nv)
          if (index !== -1) {
            this.active = this.floors[index].mapId
            this.$emit('on-switch', index)
          }
        }
      },
      immediate: true
    }
  },
  methods: {
    handleClick (mapId, index) {
      this.active = mapId
      this.$emit('on-switch', index)
    },
    handleScroll (upside) {
      const now = this.$refs.floorList.scrollTop
      this.$refs.floorList.scrollTop = now + (upside ? -120 : 120)
    }
  }
}
</script>

<style lang="scss" scoped>
.floor-slider {
  $bd: 32 / 12;
  border-radius: #{$bd}rem;
  background-color: rgba(255, 255, 255, .7);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 12vw;
  .slide-btn {
    flex: 0 0 5.8vh;
    height: 5.8vh;
    text-align: center;
    width: 100%;
    display: block;
    transition: .1s ease-out;
    &:active {
      background-color: rgba(0, 0, 0, .1);
    }
    img {
      width: 2.3vw;
    }
    &:after {
      content: '';
      height: 100%;
      display: inline-block;
      width: 0;
      visibility: hidden;
      vertical-align: middle;
    }
    &:last-child {
      img {
        transform: rotate(180deg);
      }
    }
  }
  .floor-list {
    overflow: auto;
    color: #000;
    .floor-list-item {
      height: 5.8vh;
      line-height: 5.8vh;
      width: 100%;
      text-align: center;
      font-size: 3rem;
      position: relative;
      transition: $base-transition;
      &.active-floor {
        background-color: $primary-color;
        font-size: 3.5rem;
        color: #fff;
      }
      &.current-floor:after {
        content: '';
        position: absolute;
        height: 1.5rem;
        width: 1.5rem;
        background-color: red;
        border-radius: 50%;
        right: 1rem;
        top: 1rem;
      }
    }
  }
}
</style>
