import Vue from 'vue'
import Notice from './Notice'

const NoticeConstructor = Vue.extend(Notice)

const queue = []
const offsetTop = 8.5

function resetHeight (fromIndex) {
  queue.splice(fromIndex, 1)
  const { length } = queue
  // 删除后没有元素，或者删除最后一个，跳过
  if (!length || fromIndex === length) return
  for (let i = 0; i < queue.length; i++) {
    const item = queue[i]
    let top = 0
    if (i !== 0) {
      const preItem = queue[i - 1]
      top = preItem.top + preItem.height
    }
    item.setIndex(i)
    item.setTop(top)
  }
}

export default function notice (content, opt = {}) {
  const item = new NoticeConstructor({
    el: document.createElement('div'),
    data () {
      return {
        content,
        top: 0,
        height: 0,
        show: true,
        index: queue.length,
        offsetTop,
        actions: opt.actions,
        closeTimeout: null
      }
    },
    methods: {
      setTop (value) {
        this.top = value
      },
      setIndex (index) {
        this.index = index
      },
      hide () {
        this.closeTimeout && clearTimeout(this.closeTimeout)
        this.show = false
        resetHeight(this.index)
      }
    },
    mounted () {
      this.$nextTick(() => {
        this.height = parseInt(getComputedStyle(this.$el, null).height)
        if (opt.duration) {
          this.closeTimeout && clearTimeout(this.closeTimeout)
          this.closeTimeout = setTimeout(this.hide, opt.duration)
        }
      })
    }
  })
  document.body.append(item.$el)
  queue.push(item)
  if (queue.length > 1) {
    const preItem = queue[queue.length - 2]
    item.setTop(preItem.top + preItem.height)
  }
  return item
}
