<template>
  <div class="page-password-auth page">
    <Password :tips="$t('enterAuthPassword')"
              :optional-key="{ key: 'back', action: back }"
              :validator="validator"
              @on-validate-passed="handlePassed">
    </Password>
  </div>
</template>

<script>
import Password from '@/components/Password.vue'
import backHandle from '@/mixins/backHandle'

export default {
  name: 'PasswordAuth',
  components: {
    Password
  },
  mixins: [backHandle],
  data () {
    return {
    }
  },
  computed: {
    validator () {
      return this.$store.state.app.appConfig ? this.$store.state.app.appConfig.adminPwd : '9999'
    }
  },
  methods: {
    handlePassed () {
      this.$router.replace({ name: this.$route.query.to, query: { authed: true } })
    }
  }
}
</script>

<style lang="scss" scoped>
.page-password-auth {
}
</style>

<i18n>
{
  "zh-CN": {
    "enterAuthPassword": "输入管理员密码"
  },
  "en": {
    "enterAuthPassword": "Enter administrator password"
  },
  "th": {
    "enterAuthPassword": "ระบุรหัสผ่านแอดมิน"
  }
}
</i18n>
