<template>
  <div class="page-express-form">
    <SettingRow v-for="item in headItems"
                :key="item">
      <template slot="title">{{ $t(item) }}</template>
      <template>{{ form[item] || '-' }}</template>
    </SettingRow>
    <SettingRow>
      <template slot="title">{{ $t('expressType') }}</template>
      <template>
        <div class="express-type-item"
            :class="{ active: type.value === form.type }"
            v-for="(type, index) in expressTypes"
            :key="index"
            @click="handleChangeType(type.value)">
          {{ $t(type.name) }}
        </div>
      </template>
    </SettingRow>
    <SettingRow v-for="(item, index) in lastItems"
                :key="index">
      <template slot="title">{{ $t(item.key) }}</template>
      <div v-if="form[item.key]" @click="item.action">
        {{ form[item.key] }}
      </div>
      <div v-else class="placeholder" @click="item.action">
        {{ $t(item.placeholder || 'pleaseSelect') }} >
      </div>
    </SettingRow>
    <SettingRow>
      <template slot="title">{{ $t('expressNumber') }}</template>
      <input type="text"
            :disabled="loading"
            class="express-number"
            :placeholder="$t('pleaseEnterExpressNumber')"
            v-model.trim="form.expressNumber">
    </SettingRow>
    <iButton slot="footer"
            class="comfirm-form-btn"
            :loading="loading"
            :disabled="comfirmFormDisabled"
            @click="handleComfirmSend">
      {{ $t('confirmSend') }}
    </iButton>
    <Modal :show.sync="showCategoryModal"
          :title="$t('selectGoodsCategory')"
          close-btn>
      <div class="good-category flex-between-center">
        <div class="category-item"
            :class="{ active: cate === category }"
            @click="handleChangeCategory(cate)"
            v-for="(cate, index) in categories"
            :key="index">
          {{ cate }}
        </div>
        <input type="text"
              class="other"
              :placeholder="$t('inputCategory')"
              v-model.trim="otherCategory"
              @input="handleOtherCategoryChange">
      </div>
      <iButton slot="footer" :disabled="categoryBtnDisabled" @click="handleConfirmCategory">
        {{ $t('confirm') }}
      </iButton>
    </Modal>
    <Modal :show.sync="showExpressCompanyModal"
          :title="$t('selectExpressCompany')"
          close-btn>
      <div class="express-company flex-default-center">
        <div class="express-company-item"
            :class="{ active: company.name === expressCompany }"
            v-for="(company, index) in expressCompanies"
            @click="handleChangeExpressCompany(company.name)"
            :key="index">
          <img :src="company.icon">
          {{ $t(company.name) }}
        </div>
        <input type="text"
              class="other"
              :placeholder="$t('inputExpressCompany')"
              v-model.trim="otherExpressCompany"
              @input="handleOtherExpressCompanyChange">
      </div>
      <iButton slot="footer" :disabled="expressCompanyBtnDisabled" @click="handleConfirmExpressCompany">
        {{ $t('confirm') }}
      </iButton>
    </Modal>
    <Modal :show.sync="showMessage" class="send-success-modal">
      <div class="send-success">
        <img src="@/assets/images/checkmark.png" alt="">
        <div class="send-success-title">{{ $t('success') }}</div>
        <div class="send-success-message">
          {{ $t('sendSuccess') }}
        </div>
      </div>
      <iButton slot="footer" :emit-seconds="5" @click="handleBack">
        {{ $t('gotIt') }}
      </iButton>
    </Modal>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import SettingRow from '../../Other/SettingRow.vue'
import Modal from '@/components/Modal'
import iButton from '@/components/Button'
import request from '@/utils/request'
import backHandle from '@/mixins/backHandle'

export default {
  props: {
    data: {
      type: Object
    }
  },
  components: {
    SettingRow,
    Modal,
    iButton
  },
  mixins: [backHandle],
  data () {
    return {
      expressTypes: [
        {
          name: 'company',
          value: 0
        },
        {
          name: 'private',
          value: 1
        }
      ],
      headItems: ['sender', 'contactPhone', 'department'],
      lastItems: [
        {
          key: 'goods',
          action: () => {
            if (!this.loading) this.showCategoryModal = true
          }
        },
        {
          key: 'expressCompany',
          action: () => {
            if (!this.loading) this.showExpressCompanyModal = true
          }
        }
      ],
      form: {},
      showCategoryModal: false,
      category: '文件',
      categories: ['文件', '电子类产品', '生活用品', '服饰', '生鲜', '食品', '易碎品', '化妆品', '药品'],
      otherCategory: '',
      showExpressCompanyModal: false,
      expressCompany: 'SF',
      expressCompanies: [
        'SF',
        'JDL',
        'Yunda',
        'STO',
        'YTO',
        'EMS',
        'BEST',
        'TTKD'
      ].map(e => {
        return {
          name: e,
          icon: require(`@/assets/images/express/${e}.jpg`)
        }
      }),
      loading: false,
      otherExpressCompany: '',
      showMessage: false
    }
  },
  watch: {
    data: {
      deep: true,
      immediate: true,
      handler (staff) {
        this.form = {
          sender: staff.name,
          contactPhone: staff.phone.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2'),
          department: staff.branchName,
          type: 0,
          goods: null,
          expressCompany: null,
          expressNumber: ''
        }
      }
    }
  },
  computed: {
    ...mapState('app', ['robotInfo']),
    categoryBtnDisabled () {
      return !this.otherCategory && !this.category
    },
    comfirmFormDisabled () {
      return !this.form.goods || !this.form.expressCompany || !this.form.expressNumber
    },
    expressCompanyBtnDisabled () {
      return !this.otherExpressCompany && !this.expressCompany
    }
  },
  methods: {
    handleChangeType (value) {
      if (this.loading) return
      this.form.type = value
    },
    handleChangeCategory (value) {
      this.category = value
      this.otherCategory = ''
    },
    handleOtherCategoryChange () {
      this.category = null
    },
    handleConfirmCategory () {
      this.form.goods = this.category || this.otherCategory
      this.showCategoryModal = false
    },
    handleChangeExpressCompany (name) {
      this.expressCompany = name
    },
    handleConfirmExpressCompany () {
      this.form.expressCompany = this.otherExpressCompany || this.$t(this.expressCompany)
      this.showExpressCompanyModal = false
    },
    handleOtherExpressCompanyChange () {
      this.expressCompany = null
    },
    handleComfirmSend () {
      if (this.loading) return
      this.loading = true
      request
        .post('/crss/reception/v1/express/send', {
          personId: this.data.id,
          type: this.form.type,
          goods: this.form.goods,
          expressCompany: this.form.expressCompany,
          sn: this.form.expressNumber,
          taskInstanceId: null,
          robotCode: this.robotInfo.robotId,
          opId: `h5-${Date.now()}`
        })
        .then(() => {
          this.showMessage = true
          this.form.goods = ''
          this.form.expressCompany = ''
          this.form.expressNumber = ''
        })
        .catch(({ data: { code, message } }) => {
          this.$notice(`${this.$t('fail')}<br>code: ${code}<br> message: ${message}`, {
            actions: [{
              onClick: 'close',
              emitSeconds: 5,
              label: this.$t('gotIt')
            }]
          })
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleBack () {
      this.back()
    }
  }
}
</script>

<style lang="scss">
.page-express-form {
  margin-top: 3vh;
  padding: 0 4.5vw;
  .setting-row {
    padding: 2vh 0;
  }
  .setting-input {
    font-size: 5rem;
  }
  .express-type-item {
    display: inline-block;
    height: #{100 / $r}rem;
    line-height: #{100 / $r}rem;
    padding: 0 4vw;
    background-color: #A1A1A1;
    font-size: 5rem;
    color: #e5e5e5;
    border-radius: #{50 / $r}rem;
    margin-left: 2.5vw;
    &:first-child {
      margin-left: 0;
    }
    &.active {
      background-color: #fff;
      color: $primary-color;
    }
  }
  .placeholder {
    color: #999;
  }
  .good-category {
    flex-wrap: wrap;
  }
  .category-item, .other {
    flex: 0 0 32%;
    border: 1px solid #000;
    height: 5.2vh;
    border-radius: 2.6vh;
    line-height: 5.2vh;
    margin-bottom: 1.6vh;
    &.active {
      border-color: $primary-color;
      color: $primary-color;
    }
  }
  .express-company {
    flex-wrap: wrap;
    .express-company-item {
      flex: 0 0 49%;
      margin-bottom: 2vh;
      &:nth-child(2n) {
        margin-left: 2%;
      }
      img {
        height: #{140 / $r}rem;
        width: #{140 / $r}rem;
        border-radius: 50%;
        vertical-align: middle;
        margin-right: 3.3vw;
        border: 2px solid transparent;
      }
      &.active {
        color: $primary-color;
        img {
          border-color: $primary-color;
        }
      }
    }
    .other {
      flex: 0 0 95% !important;
      justify-self: center !important;
      margin-left: auto !important;
    }
  }
  .modal {
    width: 91.7vw !important;
    button {
      padding-left: 13.9vw;
      padding-right: 13.9vw;
    }
    .other {
      flex: 0 0 100%;
      outline: none;
      padding: 0 5vw;
      font-size: 4rem;
      box-sizing: border-box;
      text-align: center;
      margin: 0;
      &::placeholder {
        color: #C3C3C3;
      }
    }
  }
  .express-number {
    font-size: 5rem;
    text-align: right;
    background-color: transparent;
    outline: none;
    border: 0;
    caret-color: #fff;
    color: #fff;
    &::placeholder {
      color: #999;
    }
  }
  .comfirm-form-btn {
    margin-top: 5vh;
    width: 24vw;
    margin: 5vh auto 0 auto;
    display: block;
    text-align: center;
    .flex-default-center {
      justify-content: center !important;
    }
  }
  .send-success-modal .modal {
    width: 75vw !important;
  }
  .send-success {
    text-align: center;
    img {
      width: 14.6vw;
      height: 9.1vh;
      margin: 2vh 0;
    }
    .send-success-title {
      font-size: 5rem;
    }
    .send-success-message {
      color: #999;
      margin: 2vh auto 0 auto;
      text-align: center;
      max-width: 50rem;
    }
  }
}
</style>

