<template>
  <div class="location-list">
    <template v-if="view === 'list'">
      <div class="location-item flex-between-center"
          v-for="(item, i) in locations"
          :key="i">
        <img class="location-image" :src="item.img" alt="">
        <div class="location-info">
          <div class="location-name">{{ item.name }}</div>
        </div>
        <iButton class="btn" @click="handleClick(item)">{{ btnText }}</iButton>
      </div>
    </template>
    <div class="location-grid-view" v-else>
      <div class="location-grid-item fs4"
          v-for="(item, i) in locations"
          @click="handleClick(item)"
          :key="i">
        <div>{{ item.name }}</div>
      </div>
    </div>
    <div v-if="locations.length === 0" class="empty-list-tips">
      <img src="@/assets/images/icon-not-found.png">
      {{ $t('emptyTips') }}
    </div>
  </div>
</template>

<script>
import iButton from '@/components/Button.vue'

export default {
  name: 'LocationList',
  props: {
    floorInfo: {
      type: Object,
      default: () => {}
    },
    btnText: {
      type: String,
      default () {
        return this.$t('deliveryHere')
      }
    },
    type: {
      type: [Number, String, Array]
    },
    sorter: {
      type: Function
    },
    view: {
      type: String,
      validator (v) {
        return v === 'list' || v === 'grid'
      },
      default: 'list'
    }
  },
  components: {
    iButton
  },
  filters: {
    fillzero (i) {
      return i.toString().padStart(3, 0)
    }
  },
  data () {
    return {
      imgs: [
        require('@/assets/images/location-default-1.png'),
        require('@/assets/images/location-default-2.png'),
        require('@/assets/images/location-default-3.png'),
        require('@/assets/images/location-default-4.png'),
        require('@/assets/images/location-default-5.png')
      ]
    }
  },
  computed: {
    filterType () {
      if (Array.isArray(this.type)) {
        return this.type
      }
      return [this.type]
    },
    locations () {
      if (this.floorInfo && this.floorInfo.locations) {
        const data = this.type !== undefined ? this.floorInfo.locations.filter(e => this.filterType.indexOf(e.type) !== -1) : this.floorInfo.locations
        return data.map(e => {
          const img = this.imgs[Math.floor(Math.random() * this.imgs.length)]
          const res = {
            ...e,
            img
          }
          return res
        }).sort(this.sorter)
      }
      return []
    }
  },
  methods: {
    handleClick (item) {
      this.$parent.handleSelect(item)
    }
  }
}
</script>

<style lang="scss" scoped>
.location-list {
  width: 100%;
  .location-item {
    width: 100%;
    height: 9.344vh;
    background-color: rgba(0, 0, 0, .43);
    border-radius: #{10 / $r}rem;
    // margin-bottom: 1.5vh;
    margin-bottom: 1vh;
    padding: 2.1vw;
    .location-image {
      flex: 0 0 16vw;
      width: 16vw;
      height: 7.68vh;
    }
    .location-info {
      flex: 1;
      margin-left: 5.9vw;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
      // height: 100%;
      overflow: hidden;
      .location-name {
        width: 100%;
        font-size: 4rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        line-height: 1;
      }
      .location-code {
        font-size: #{30 / $r}rem;
      }
      .btn {
        background-color: #000;
        border: #{1 / $r}rem solid $primary-color;
      }
    }
  }
  .location-grid-view {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    overflow: hidden;
    width: 100%;
    .location-grid-item {
      flex: 0 0 17.4vw;
      width: 17.4vw;
      height: 6.8vh;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: rgba(255, 255, 255, .4);
      border-radius: 2rem;
      overflow: hidden;
      $m: 30 / 12;
      margin-bottom: #{$m}rem;
      margin-right: 2.5vw;
      transition: $base-transition;
      div {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      &:nth-child(4n) {
        margin-right: 0;
      }
      &:focus,
      &:active {
        background-color: $primary-color;
      }
    }
  }
  .empty-list-tips {
    font-size: 4rem;
    text-align: center;
    img {
      display: block;
      margin: 14.7vh auto 3.5vh auto;
      width: 21.9vw;
      height: 10.4vh;
    }
  }
}
</style>

<i18n>
{
  "zh-CN": {
    "location": "位置"
  },
  "en": {
    "location": "Location"
  },
  "th": {
    "location": "สถานที่"
  }
}
</i18n>
