<template>
  <div class="page page-express">
    <router-view />
    <!-- <SmartVoice class="smart-voice"></SmartVoice> -->
  </div>
</template>

<script>
// import SmartVoice from '@/components/SmartVoice.vue'
import pong from '@/ue4/pong'
import pongTypes from '@/ue4/pong/types'
import { logger } from '@/ue4/ping'

export default {
  name: 'Express',
  components: {
    // SmartVoice
  },
  methods: {
    handleTaskRunning (data) {
      if (this.$route.name !== 'DispatchExpress') {
        logger('express主页收到updateTaskStaus或updateSingleTaskStatus', data)
        localStorage.setItem('runningDispatchTask', JSON.stringify(data))
        this.$router.replace({ name: 'DispatchExpress' })
      }
    }
  },
  mounted () {
    pong.$on(pongTypes.UPDATE_SINGLE_TASK_STATUS, this.handleTaskRunning)
    pong.$on(pongTypes.UPDATE_TASK_STATUS, this.handleTaskRunning)
  },
  beforeDestroy () {
    pong.$off(pongTypes.UPDATE_SINGLE_TASK_STATUS, this.handleTaskRunning)
    pong.$off(pongTypes.UPDATE_TASK_STATUS, this.handleTaskRunning)
  }
}
</script>

<style lang="scss" scoped>
.page-express {
  .smart-voice {
    position: fixed;
    bottom: 0;
    left: 0;
  }
}
</style>
