<template>
  <div class="page page-publicize">
    <Pending v-if="status === PENDING"></Pending>
    <Interact v-else-if="status === WORKING">
      <template slot="tips">
        {{ $t('running') }}
      </template>
      <template slot="title">
        {{ $t('publicizing') }}
      </template>
    </Interact>
  </div>
</template>

<script>
import Interact from '@/components/Interact.vue'
import Pending from './Pending'
import pong from '@/ue4/pong'
import pongTypes from '@/ue4/pong/types'
import backHandle from '@/mixins/backHandle'

const PENDING = 1
const WORKING = 2

export default {
  name: 'Publicize',
  components: {
    Pending,
    Interact
  },
  mixins: [backHandle],
  data () {
    return {
      PENDING,
      WORKING,
      status: PENDING
    }
  },
  methods: {
    handleStartPublicizeResp (data) {
      if (data.code === 0) {
        this.status = WORKING
      } else {
        this.$message(this.$t('publicizeFailed'))
      }
    },
    handleStopPublicize () {
      this.$message(this.$t('stopPublicize'))
      this.back()
    }
  },
  created () {
    pong.$on(pongTypes.START_PUBLICIZE_RESP, this.handleStartPublicizeResp)
    pong.$on(pongTypes.STOP_PUBLICIZE, this.handleStopPublicize)
  },
  beforeDestroy () {
    pong.$off(pongTypes.START_PUBLICIZE_RESP, this.handleStartPublicizeResp)
    pong.$off(pongTypes.STOP_PUBLICIZE, this.handleStopPublicize)
  }
}
</script>

<i18n>
{
  "zh-CN": {
    "running": "巡逻中",
    "publicizing": "正在宣讲",
    "stopPublicize": "宣讲结束"
  },
  "en": {
    "running": "Patrolling",
    "publicizing": "Publicizing",
    "stopPublicize": "Publicizing ending"
  },
  "th": {
    "running": "กำลังเดินทาง",
    "publicizing": "กำลังเผยแพร่",
    "stopPublicize": "การเผยแพร่เสร็จสิ้น"
  }
}
</i18n>

<style lang="scss" scoped>
.page-publicize {}
</style>
