<template>
  <div class="page disinfect-task-list">
    <Bar>
      {{ $t('Disinfect') }}
    </Bar>
    <CuteLoading class="loading" v-if="loading">{{ $t('loading') }}</CuteLoading>
    <template v-else>
      <div class="empty-task-list" v-if="!list.length">
        <img src="@/assets/images/icon-not-found.png" alt="">
        <div class="fs4">{{ $t('emptyList') }}</div>
        <div class="empty-list-tips">{{ $t('emptyListTips') }}</div>
        <iButton type="outline" @click="getTaskList">{{ $t('fetch') }}</iButton>
      </div>
      <div class="disinfect-tasks" v-else>
        <TaskItem v-for="(item, index) in list"
                  :key="index"
                  :open="openIndex === index"
                  :class="{ active: index === active }"
                  :data="item"
                  class="task-item"
                  @click.native="handleSelectTask(index)"
                  @toggle="handleToggleTask(index)">
        </TaskItem>
      </div>
      <div class="disinfect-task-handler">
        <iButton v-if="list.length" type="outline" :disabled="restoreDisabled" @click="handleRestoreTask">{{ $t('restore') }}</iButton>
        <iButton class="new-task-btn" @click="$router.push({ name: 'NewDisinfectTask' })">{{ $t('new') }}</iButton>
      </div>
    </template>
  </div>
</template>

<script>
import Bar from '@/components/Bar.vue'
import iButton from '@/components/Button'
import CuteLoading from '@/components/CuteLoading'
import TaskItem from './TaskItem'
import request from '@/utils/request'
import ping from '@/ue4/ping'
import pingTypes from '@/ue4/ping/types'
import CONST from '@/const'
import { mapState } from 'vuex'

export default {
  components: {
    Bar,
    CuteLoading,
    iButton,
    TaskItem
  },
  data () {
    return {
      active: null,
      openIndex: null,
      loading: false,
      restoreLoading: false,
      list: []
    }
  },
  computed: {
    ...mapState('app', ['robotInfo']),
    restoreDisabled () {
      return this.active === null
    },
    selectedTask () {
      if (this.active !== null) {
        return this.list[this.active]
      }
      return null
    }
  },
  methods: {
    handleSelectTask (index) {
      this.active = index
    },
    handleToggleTask (index) {
      if (this.openIndex === index) {
        this.openIndex = null
      } else {
        this.openIndex = index
      }
    },
    getTaskList () {
      this.loading = true
      request
        .get(`/crss/task/v1/task/list?size=999&robotCode=${this.robotInfo.robotId}&bizTaskType=12`, { proxy: '/231' })
        .then(({ code, data }) => {
          if (code === 0) {
            this.list = data.records
          }
        })
        .catch(() => {
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleRestoreTask () {
      ping(pingTypes.START_UE4_TASK, {
        taskType: CONST.DISINFECT,
        type: 2
      })
    }
  },
  mounted () {
    this.getTaskList()
  }
}
</script>

<i18n>
{
  "zh-CN": {
    "restore": "恢复任务",
    "new": "新建任务",
    "loading": "加载任务列表",
    "emptyList": "暂无获取到任务",
    "emptyListTips": "你可以尝试手动获取或者新建任务哦~",
    "fetch": "手动获取"
  }
}
</i18n>

<style lang="scss" scoped>
.disinfect-task-list {
  display: flex;
  flex-direction: column;
  .task-item {
    border-radius: #{8 / $r}rem;
    background-color: #fff;
    margin-bottom: 2.6vh;
    border: .5vw solid transparent;
    &:last-child {
      margin-bottom: 0;
    }
    &.active {
      border-color: $primary-color;
    }
  }
  .loading {
    margin-top: 30vh;
  }
  .empty-task-list {
    text-align: center;
    margin-top: 18vh;
    flex: 1;
    .fs4 {
      margin: 4vh 0 1.25vh 0;
    }
    .empty-list-tips {
      font-size: 3rem;
      color: #999;
      margin-bottom: 4vh;
    }
  }
  .disinfect-tasks {
    flex: 1;
    overflow: auto;
    padding: 0 3.25vw;
    color: #000;
  }
  .disinfect-task-handler {
    height: 17vh;
    padding-top: 3.4vh;
    text-align: center;
    .new-task-btn {
      margin-left: 4.25vw;
    }
  }
}
</style>
