<template>
  <div class="page page-dispatch-express">
    <Pending v-if="status === PENDING"></Pending>
    <Running v-else-if="status === RUNNING" :task="task"></Running>
  </div>
</template>

<script>
import Pending from './Pending.vue'
import Running from './Running.vue'
import pong from '@/ue4/pong'
import pongTypes from '@/ue4/pong/types'
import backHandle from '@/mixins/backHandle'

const PENDING = 1
const RUNNING = 2
const FINISHED = 3

export default {
  name: 'DispatchExpress',
  components: {
    Pending,
    Running
  },
  mixins: [backHandle],
  data () {
    return {
      PENDING,
      RUNNING,
      FINISHED,
      status: PENDING,
      task: null
    }
  },
  methods: {
    handleUpdateSingleTask (data) {
      this.status = RUNNING
      this.task = data
    },
    handleUpdateTask () {
      this.back()
    }
  },
  mounted () {
    const runningDispatchTask = JSON.parse(localStorage.getItem('runningDispatchTask'))
    if (runningDispatchTask) {
      this.task = runningDispatchTask
      this.status = RUNNING
      localStorage.removeItem('runningDispatchTask')
    }
    pong.$on(pongTypes.UPDATE_SINGLE_TASK_STATUS, this.handleUpdateSingleTask)
    pong.$on(pongTypes.UPDATE_TASK_STATUS, this.handleUpdateTask)
  },
  beforeDestroy () {
    pong.$off(pongTypes.UPDATE_SINGLE_TASK_STATUS, this.handleUpdateSingleTask)
    pong.$off(pongTypes.UPDATE_TASK_STATUS, this.handleUpdateTask)
    // pong.$off(pongTypes.TASK_SUMMARY, this.handleTaskSummary)
  }
}
</script>
