<template>
  <div class="page page-video-chat">
    <div class="caller-image"></div>
    <div class="caller-name">{{ $route.query.caller }}</div>
    <div class="chat-tips">
      {{ $t('chatTips') }}
    </div>
    <div class="chat-handlers flex-between-center">
      <img src="@/assets/images/icon-close-chat.png" @click="handleChatResp(false)" alt="">
      <img src="@/assets/images/icon-video-chat.png" @click="handleChatResp" alt="">
    </div>
  </div>
</template>

<script>
import ping from '@/ue4/ping'
import pingTypes from '@/ue4/ping/types'
import pong from '@/ue4/pong'
import pongTypes from '@/ue4/pong/types'
import backHandle from '@/mixins/backHandle'

export default {
  mixins: [backHandle],
  methods: {
    handleChatResp (receive) {
      ping(pingTypes[receive ? 'ANSWER_CHAT' : 'REJECT_CHAT'])
      this.back()
    },
    // status == 2为超时
    handleUpdatePage (data) {
      if (data.pageType === 'VideoCallPage' && data.status === 2) {
        this.back()
      }
    }
  },
  created () {
    pong.$on(pongTypes.UPDATE_PAGE, this.handleUpdatePage)
  },
  beforeDestroy () {
    pong.$off(pongTypes.UPDATE_PAGE, this.handleUpdatePage)
  }
}
</script>

<i18n>
{
  "zh-CN": {
    "chatTips": "邀请你视频通话"
  }
}
</i18n>

<style lang="scss" scoped>
.page-video-chat {
  text-align: center;
  .caller-image {
    height: 25vw;
    width: 25vw;
    margin: 28vh auto 0 auto;
    border-radius: 2rem;
    background-color: #999;
  }
  .caller-name {
    font-size: 5rem;
    margin: 2vh 0;
  }
  .chat-tips {
    font-size: 3rem;
    color: #999;
  }
  .chat-handlers {
    margin: 18vh 20vw 0 20vw;
    img {
      width: 14.5vw;
      height: 14.5vw;
    }
  }
}
</style>
