<template>
  <Modal class="running-exception"
        :show="show"
        :loading="loading">
    <template>
      <div>{{ getTitle }}</div>
      <div class="task-detail" v-if="task">
        <template v-if="isRunning">
          {{ getTaskDetail }}
        </template>
        <Error v-else-if="hasErrorCode" :code="task.errorCode"></Error>
      </div>
    </template>
    <template slot="footer">
      <iButton type="outline-light" @click="handleCancel">{{ $t('cancelTask') }}</iButton>
      <!-- 如果用v-show控制显示modal，页面在一进来必定渲染这个组件
          所以这里的自动触发button一！定！要！加个v-if阻止自运行 -->
      <iButton class="continue-task-btn"
              @click="handleConfirm">
        {{ $t('continueTask') }}
      </iButton>
    </template>
  </Modal>
</template>

<script>
// 以下情况显示
// 1、设备任务过程中重启/断网
// 2、任务过程中被挂起

import { mapState, mapMutations } from 'vuex'
import Modal from '@/components/Modal'
import iButton from '@/components/Button'
import Error from '@/components/Error'
import pingTypes from '@/ue4/ping/types'
import pong from '@/ue4/pong'
import pongTypes from '@/ue4/pong/types'
import taskException from '@/mixins/taskException'
import TASK_STATUS from '@/const'

export default {
  name: 'RunningException',
  props: {
    type: {
      type: Number,
      required: true
    }
  },
  components: {
    Modal,
    iButton,
    Error
  },
  mixins: [taskException],
  computed: {
    ...mapState('task', ['currentTask']),
    // 从notifyTask接收到的pending
    // 这一状态只可能从notifyTask处接收到
    isNotifyPaused () {
      return this.task && this.task.status == 'pending'
    },
    // 从queryTask查询到的paused
    isQueryPaused () {
      return this.task && this.task.status == TASK_STATUS.PAUSED
    },
    // 从queryTask查询到的running
    isRunning () {
      return this.task && this.task.status == TASK_STATUS.RUNNING
    },
    getTitle () {
      if (this.isNotifyPaused || this.isQueryPaused) return this.$t('taskPaused')
      return this.$t('taskRunning')
    },
    getTaskDetail () {
      if (this.isNotifyPaused) {
        return this.task.detail
      }
      if (this.task) {
        return this.task.taskName || this.task.detail
      }
      return ''
    },
    // 只有从queryTask过来，且状态是running的任务可以自动重试
    // autoRetry () {
    //   if (this.isRunning) {
    //     return 10
    //   }
    //   return 0
    // },
    hasErrorCode () {
      return this.task && this.task.errorCode !== undefined
    }
  },
  watch: {
    currentTask: {
      handler (nv) {
        // 从cross主动查询到task才需要执行一下逻辑
        if (nv && nv.taskType === this.type && nv.cross) {
          this.task = this.currentTask
          if ((this.isNotifyPaused || this.isQueryPaused) && this.task.errorCode) {
            const code = this.task.errorCode.split('&').pop()
            if (code && code == '84903') {
              this.$block.show(this.$t('HAPause'), { icon: 'loading' })
              return
            }
          }
          this.show = true
          // 显示对话框就自动重试一遍，不再显示倒计时
          if (this.isRunning) this.handleConfirm()
          this.$block.hide()
        }
      },
      immediate: true
    }
  },
  methods: {
    ...mapMutations('task', ['clearCurrentTask']),
    handleTaskNotify (data) {
      // notifyTask把paused状态称为pending
      if (data.status === 'pending') {
        this.task = data
        this.loading = false
        if (data.errorCode == '84903') {
          this.show = false
          this.$block.show(this.$t('HAPause'), { icon: 'loading' })
        } else {
          this.show = true
          this.$block.hide()
        }
      } else {
        this.show = false
        this.$block.hide()
      }
    },
    handleConfirm () {
      this.changeTaskStatus(pingTypes.RETRY_TASK)
        .then(() => {
          // 重试成功
          // this.isQueryPaused && this.clearCurrentTask()
          this.show = false
        })
        .catch(() => this.$message(this.$t('retryFailed')))
    },
    handleCancel () {
      this.changeTaskStatus(pingTypes.CANCEL_TASK)
        .then(() => {
          this.show = false
          this.$message(this.$t('cancelSuccess'))
        })
        .catch(() => {
          this.$message(this.$t('cancelFailed'))
        })
    }
  },
  mounted () {
    pong.$on(pongTypes.TASK_NOTIFY, this.handleTaskNotify)
    // if (this.currentTask) {
    //   this.task = this.currentTask
    //   this.show = true
    // }
  },
  beforeDestroy () {
    pong.$off(pongTypes.TASK_NOTIFY, this.handleTaskNotify)
  }
}
</script>

<style lang="scss" scoped>
.running-exception {
  .task-detail {
    margin-top: 3rem;
  }
  .continue-task-btn {
    margin-left: 4rem;
  }
}
</style>

<i18n>
{
  "zh-CN": {
    "HAPause": "思考中请稍候..."
  },
  "en": {
    "HAPause": "I'm thinking, Please Wait"
  },
  "th": {
    "HAPause": "โปรดรอสักครู่"
  }
}
</i18n>
