<template>
  <div class="page theme-list">
    <SearchBar @on-change="handleQueryChange"
              :placeholder="placeholder"
              :on-back="onBack">
    </SearchBar>
    <ListPanel class="theme-list-content">
      <ListItem v-for="(item, index) in displayList"
                :key="index"
                @click.native="handleSelect(index)"
                :checked="active === index">
        {{ item.name }}
      </ListItem>
      <div class="empty-tips" v-if="!displayList.length || !list.length">
        <img src="@/assets/images/no-themes.png" alt="">
        <div class="fs4">{{ !list.length ? emptyTips : searchEmptyTips }}</div>
      </div>
      <iButton slot="footer" @click="handleConfirm">{{ confirmText }}</iButton>
    </ListPanel>
  </div>
</template>

<script>
import SearchBar from '@/components/SearchBar'
import ListItem from '@/components/ListItem'
import iButton from '@/components/Button'
import ListPanel from './ListPanel'

export default {
  name: 'ThemeList',
  props: {
    list: {
      type: Array
    },
    placeholder: {
      type: String,
      default () {
        return this.$t('searchTips')
      }
    },
    emptyTips: {
      type: String,
      default () {
        return this.$t('noData')
      }
    },
    searchEmptyTips: {
      type: String,
      default () {
        return this.$t('noData')
      }
    },
    confirmText: {
      type: String,
      default () {
        return this.$t('confirm')
      }
    },
    onBack: {
      type: Function,
      default: () => {}
    },
    selectTips: {
      type: String,
      default () {
        return this.$t('pleaseSelect')
      }
    }
  },
  components: {
    SearchBar,
    ListItem,
    iButton,
    ListPanel
  },
  data () {
    return {
      active: null,
      query: ''
    }
  },
  computed: {
    displayList () {
      if (this.query) {
        return this.list.filter(e => e.name.indexOf(this.query) !== -1)
      }
      return this.list
    }
  },
  methods: {
    handleSelect (index) {
      this.active = index
    },
    handleQueryChange (query) {
      this.query = query.trim()
    },
    handleConfirm () {
      if (this.active !== null) {
        this.$emit('on-confirm', this.list[this.active])
      } else {
        this.$message(this.selectTips)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.theme-list {
  display: flex;
  flex-direction: column;
  .theme-list-content {
    flex: 1;
    .empty-tips {
      text-align: center;
      margin-top: 20vh;
      img {
        width: 15.2vw;
        margin-bottom: 2.1vh;
      }
      .fs4 {
        width: 44vw;
        margin: 0 auto;
      }
    }
  }
}
</style>
