<template>
  <div class="error-detail">
    {{ $t(`errors.${code}`) }}
    <span class="show-more" @click="() => showErrorCode = !showErrorCode">{{ $t('detail') }}</span>
    <small v-show="showErrorCode">{{ $t('errorCode') }} {{ code }}</small>
  </div>
</template>

<script>
export default {
  props: {
    code: {
      type: [Number, String]
    }
  },
  data () {
    return {
      showErrorCode: false
    }
  }
}
</script>

<style lang="scss" scoped>
.error-detail {
  .show-more {
    margin-left: 1rem;
    color: $primary-color;
    font-size: 3rem;
  }
  small {
    margin-top: 1rem;
    display: block;
    text-align: center;
    color: #9d9d9d;
  }
}
</style>
